import { useState, useEffect } from 'react'

interface ResponsiveLayoutProps {
  children: React.ReactNode
  sidebar?: React.ReactNode
  rightPanel?: React.ReactNode
  className?: string
}

export default function ResponsiveLayout({ 
  children, 
  sidebar, 
  rightPanel, 
  className = '' 
}: ResponsiveLayoutProps) {
  const [isMobile, setIsMobile] = useState(false)
  const [isIPad, setIsIPad] = useState(false)
  const [isIPadPro, setIsIPadPro] = useState(false)
  const [sidebarOpen, setSidebarOpen] = useState(false)

  useEffect(() => {
    const checkDevice = () => {
      const width = window.innerWidth
      const userAgent = navigator.userAgent.toLowerCase()
      const isIOS = /ipad|iphone|ipod/.test(userAgent)
      
      setIsMobile(width < 768)
      setIsIPad(width >= 768 && width <= 1024 && isIOS)
      setIsIPadPro(width > 1024 && width <= 1366 && isIOS)
    }

    checkDevice()
    window.addEventListener('resize', checkDevice)
    
    return () => window.removeEventListener('resize', checkDevice)
  }, [])

  // Mobile layout
  if (isMobile) {
    return (
      <div className={`flex flex-col h-full ${className}`}>
        {/* Mobile header with menu button */}
        <div className="bg-white border-b border-gray-200 px-4 py-3 flex items-center justify-between">
          {sidebar && (
            <button
              onClick={() => setSidebarOpen(true)}
              className="p-2 text-gray-500 hover:text-gray-700"
            >
              <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
              </svg>
            </button>
          )}
        </div>

        {/* Mobile sidebar overlay */}
        {sidebar && sidebarOpen && (
          <div className="fixed inset-0 z-50 flex">
            <div className="fixed inset-0 bg-black bg-opacity-50" onClick={() => setSidebarOpen(false)} />
            <div className="relative w-80 bg-white shadow-xl">
              <div className="flex items-center justify-between p-4 border-b">
                <h2 className="text-lg font-semibold">Menu</h2>
                <button
                  onClick={() => setSidebarOpen(false)}
                  className="p-1 text-gray-500 hover:text-gray-700"
                >
                  <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                  </svg>
                </button>
              </div>
              {sidebar}
            </div>
          </div>
        )}

        {/* Mobile main content */}
        <div className="flex-1 overflow-auto">
          {children}
        </div>
      </div>
    )
  }

  // iPad layout
  if (isIPad) {
    return (
      <div className={`flex h-full ${className}`}>
        {sidebar && (
          <div className="sidebar-ipad bg-white border-r border-gray-200 flex-shrink-0 overflow-y-auto">
            {sidebar}
          </div>
        )}
        <div className="split-view-content overflow-auto">
          {children}
        </div>
        {rightPanel && (
          <div className="w-80 bg-white border-l border-gray-200 flex-shrink-0 overflow-y-auto">
            {rightPanel}
          </div>
        )}
      </div>
    )
  }

  // iPad Pro layout (three columns)
  if (isIPadPro && sidebar && rightPanel) {
    return (
      <div className={`three-column-ipad-pro ${className}`}>
        <div className="bg-white border-r border-gray-200 overflow-y-auto">
          {sidebar}
        </div>
        <div className="overflow-auto">
          {children}
        </div>
        <div className="bg-white border-l border-gray-200 overflow-y-auto">
          {rightPanel}
        </div>
      </div>
    )
  }

  // Desktop layout
  return (
    <div className={`flex h-full ${className}`}>
      {sidebar && (
        <div className="w-64 bg-white border-r border-gray-200 flex-shrink-0 overflow-y-auto">
          {sidebar}
        </div>
      )}
      <div className="flex-1 overflow-auto">
        {children}
      </div>
      {rightPanel && (
        <div className="w-80 bg-white border-l border-gray-200 flex-shrink-0 overflow-y-auto">
          {rightPanel}
        </div>
      )}
    </div>
  )
}