import { detectElementTypeWithContext } from './screenplayFormatter'

export interface BreakdownElement {
  id: string
  type: 'cast' | 'props' | 'wardrobe' | 'location' | 'sfx' | 'music' | 'makeup' | 'other'
  name: string
  category: string
  scenes: string[]
  firstAppearance: number
  totalAppearances: number
  confidence: number // 0-1 scale for auto-detection confidence
  isAutoDetected: boolean
  tags: string[]
  notes?: string
}

export interface SceneBreakdown {
  sceneNumber: string
  sceneHeading: string
  pageNumber: number
  location: {
    interior: boolean
    exterior: boolean
    name: string
  }
  timeOfDay: string
  characters: string[]
  elements: {
    props: string[]
    wardrobe: string[]
    sfx: string[]
    music: string[]
    other: string[]
  }
  description: string
  estimatedDuration: number // in minutes
}

// Common industry patterns for element detection
const ELEMENT_PATTERNS = {
  cast: [
    // Character names in ALL CAPS (screenplay standard)
    /^[A-Z][A-Z\s\-'\.]+$/,
    // Character names with (V.O.) or (O.S.)
    /^[A-Z][A-Z\s\-'\.]+\s*\([VO\.S]+\)$/,
    // Character names in action lines
    /\b[A-Z][A-Z\s\-'\.]{2,}\b/
  ],
  props: [
    /\b(gun|weapon|knife|sword|pistol|rifle)\b/i,
    /\b(phone|cell phone|telephone|mobile)\b/i,
    /\b(car|vehicle|truck|motorcycle|bike)\b/i,
    /\b(computer|laptop|tablet|ipad)\b/i,
    /\b(key|keys|keychain)\b/i,
    /\b(wallet|purse|bag|briefcase|suitcase)\b/i,
    /\b(document|paper|letter|file|folder)\b/i,
    /\b(camera|video camera|camcorder)\b/i,
    /\b(watch|clock|timer)\b/i,
    /\b(bottle|glass|cup|mug)\b/i
  ],
  wardrobe: [
    /\b(uniform|costume|dress|suit|jacket|coat)\b/i,
    /\b(hat|helmet|cap|beanie)\b/i,
    /\b(glasses|sunglasses|goggles)\b/i,
    /\b(shoes|boots|sneakers|heels)\b/i,
    /\b(gloves|jewelry|watch|necklace|ring)\b/i,
    /\b(shirt|blouse|t-shirt|sweater)\b/i,
    /\b(pants|jeans|skirt|shorts)\b/i
  ],
  sfx: [
    /\b(explosion|gunshot|crash|thunder|rain)\b/i,
    /\b(music|sound|noise|audio|soundtrack)\b/i,
    /\b(ring|beep|buzz|alarm|siren)\b/i,
    /\b(footsteps|door slam|glass breaking)\b/i,
    /\b(wind|fire crackling|water dripping)\b/i
  ],
  music: [
    /\b(music|song|soundtrack|score|theme)\b/i,
    /\b(plays|playing|begins|starts|fades)\b/i,
    /\b(piano|guitar|violin|drums|orchestra)\b/i,
    /\b(radio|stereo|speakers|headphones)\b/i
  ],
  location: [
    // These are handled separately via scene headings
  ]
}

const LOCATION_PATTERNS = {
  interior: /^INT\./i,
  exterior: /^EXT\./i,
  timeOfDay: /\b(DAWN|MORNING|DAY|AFTERNOON|EVENING|DUSK|NIGHT|LATER|CONTINUOUS|SAME TIME)\b/i
}

export class AutoBreakdownEngine {
  private lines: string[]
  
  constructor(script: string) {
    this.lines = script.split('\n')
  }

  /**
   * Perform full automated script breakdown
   */
  public performFullBreakdown(): {
    elements: BreakdownElement[]
    scenes: SceneBreakdown[]
    statistics: {
      totalScenes: number
      totalElements: number
      characterCount: number
      locationCount: number
      estimatedDuration: number
    }
  } {
    const scenes = this.extractScenes()
    const elements = this.extractElements(scenes)
    
    const statistics = {
      totalScenes: scenes.length,
      totalElements: elements.length,
      characterCount: elements.filter(e => e.type === 'cast').length,
      locationCount: new Set(scenes.map(s => s.location.name)).size,
      estimatedDuration: scenes.reduce((total, scene) => total + scene.estimatedDuration, 0)
    }

    return { elements, scenes, statistics }
  }

  /**
   * Extract all scenes from the script
   */
  private extractScenes(): SceneBreakdown[] {
    const scenes: SceneBreakdown[] = []
    let currentScene: Partial<SceneBreakdown> | null = null
    let currentPageNumber = 1
    let currentSceneNumber = 1

    for (let i = 0; i < this.lines.length; i++) {
      const line = this.lines[i].trim()
      const elementType = detectElementTypeWithContext(this.lines, i)

      // Detect scene headings
      if (elementType === 'scene_heading') {
        // Save previous scene if exists
        if (currentScene) {
          scenes.push(this.completeScene(currentScene, i))
        }

        // Start new scene
        currentScene = {
          sceneNumber: currentSceneNumber.toString(),
          sceneHeading: line,
          pageNumber: currentPageNumber,
          location: this.parseLocation(line),
          timeOfDay: this.extractTimeOfDay(line),
          characters: [],
          elements: {
            props: [],
            wardrobe: [],
            sfx: [],
            music: [],
            other: []
          },
          description: '',
          estimatedDuration: 0
        }
        currentSceneNumber++
      }

      // Add content to current scene
      if (currentScene) {
        if (elementType === 'character') {
          const character = line.replace(/\s*\([VO\.S]+\)$/, '').trim()
          if (!currentScene.characters?.includes(character)) {
            currentScene.characters?.push(character)
          }
        }

        if (elementType === 'action') {
          currentScene.description += line + '\n'
          
          // Extract elements from action lines
          const sceneElements = this.extractElementsFromLine(line, currentScene.sceneNumber!)
          this.addElementsToScene(currentScene, sceneElements)
        }
      }

      // Estimate page breaks (roughly 1 page = 55 lines)
      if (i % 55 === 0) {
        currentPageNumber++
      }
    }

    // Complete final scene
    if (currentScene) {
      scenes.push(this.completeScene(currentScene, this.lines.length))
    }

    return scenes
  }

  /**
   * Extract location information from scene heading
   */
  private parseLocation(sceneHeading: string): SceneBreakdown['location'] {
    const isInterior = LOCATION_PATTERNS.interior.test(sceneHeading)
    const isExterior = LOCATION_PATTERNS.exterior.test(sceneHeading)
    
    // Extract location name (between INT./EXT. and time of day)
    let locationName = sceneHeading
      .replace(/^(INT\.|EXT\.)\s*/i, '')
      .replace(/\s*-\s*(DAWN|MORNING|DAY|AFTERNOON|EVENING|DUSK|NIGHT|LATER|CONTINUOUS|SAME TIME).*$/i, '')
      .trim()

    return {
      interior: isInterior,
      exterior: isExterior,
      name: locationName || 'UNKNOWN LOCATION'
    }
  }

  /**
   * Extract time of day from scene heading
   */
  private extractTimeOfDay(sceneHeading: string): string {
    const match = sceneHeading.match(LOCATION_PATTERNS.timeOfDay)
    return match ? match[1] : 'DAY'
  }

  /**
   * Complete scene breakdown with estimates
   */
  private completeScene(scene: Partial<SceneBreakdown>, _endLine: number): SceneBreakdown {
    const completed = scene as SceneBreakdown
    
    // Estimate scene duration based on content
    const actionLines = completed.description.split('\n').filter(line => line.trim()).length
    const dialogueCount = completed.characters?.length || 0
    
    // Rough estimation: 1 minute per 10 action lines + 30 seconds per character dialogue
    completed.estimatedDuration = Math.max(1, Math.round((actionLines / 10) + (dialogueCount * 0.5)))
    
    return completed
  }

  /**
   * Extract all elements from scenes
   */
  private extractElements(scenes: SceneBreakdown[]): BreakdownElement[] {
    const elementMap = new Map<string, BreakdownElement>()
    let elementId = 1

    // Extract characters first
    const allCharacters = new Set<string>()
    scenes.forEach(scene => {
      scene.characters.forEach(char => allCharacters.add(char))
    })

    allCharacters.forEach(character => {
      const appearances = scenes.filter(scene => scene.characters.includes(character))
      elementMap.set(`cast_${character}`, {
        id: `element_${elementId++}`,
        type: 'cast',
        name: character,
        category: this.categorizeCharacter(character, appearances.length),
        scenes: appearances.map(scene => scene.sceneNumber),
        firstAppearance: Math.min(...appearances.map(scene => parseInt(scene.sceneNumber))),
        totalAppearances: appearances.length,
        confidence: 1.0,
        isAutoDetected: true,
        tags: []
      })
    })

    // Extract other elements from scene descriptions
    scenes.forEach(scene => {
      const elements = this.extractElementsFromText(scene.description, scene.sceneNumber)
      elements.forEach(element => {
        const key = `${element.type}_${element.name}`
        if (elementMap.has(key)) {
          const existing = elementMap.get(key)!
          existing.scenes.push(scene.sceneNumber)
          existing.totalAppearances++
        } else {
          elementMap.set(key, {
            type: element.type!,
            name: element.name!,
            category: element.category!,
            confidence: element.confidence!,
            isAutoDetected: element.isAutoDetected!,
            tags: element.tags!,
            id: `element_${elementId++}`,
            scenes: [scene.sceneNumber],
            firstAppearance: parseInt(scene.sceneNumber),
            totalAppearances: 1,
            notes: element.notes
          })
        }
      })
    })

    return Array.from(elementMap.values()).sort((a, b) => a.firstAppearance - b.firstAppearance)
  }

  /**
   * Extract elements from a single line of text
   */
  private extractElementsFromLine(line: string, _sceneNumber: string): Partial<BreakdownElement>[] {
    return this.extractElementsFromText(line, _sceneNumber)
  }

  /**
   * Extract elements from text using pattern matching
   */
  private extractElementsFromText(text: string, _sceneNumber: string): Partial<BreakdownElement>[] {
    const elements: Partial<BreakdownElement>[] = []
    
    Object.entries(ELEMENT_PATTERNS).forEach(([type, patterns]) => {
      patterns.forEach(pattern => {
        const matches = text.match(new RegExp(pattern.source, 'gi'))
        if (matches) {
          matches.forEach(match => {
            const cleanMatch = match.trim().toLowerCase()
            if (cleanMatch.length > 2) {
              elements.push({
                type: type as BreakdownElement['type'],
                name: this.formatElementName(cleanMatch),
                category: this.categorizeElement(type as BreakdownElement['type'], cleanMatch),
                confidence: this.calculateConfidence(pattern, cleanMatch),
                isAutoDetected: true,
                tags: []
              })
            }
          })
        }
      })
    })

    return elements
  }

  /**
   * Add elements to scene breakdown
   */
  private addElementsToScene(scene: Partial<SceneBreakdown>, elements: Partial<BreakdownElement>[]): void {
    elements.forEach(element => {
      if (!element.type || !element.name) return
      
      const list = scene.elements?.[element.type as keyof SceneBreakdown['elements']] as string[]
      if (list && !list.includes(element.name)) {
        list.push(element.name)
      }
    })
  }

  /**
   * Categorize character based on appearances
   */
  private categorizeCharacter(_character: string, appearances: number): string {
    if (appearances >= 10) return 'Lead Character'
    if (appearances >= 5) return 'Supporting Character'
    if (appearances >= 2) return 'Recurring Character'
    return 'Minor Character'
  }

  /**
   * Categorize element based on type and content
   */
  private categorizeElement(type: BreakdownElement['type'], name: string): string {
    switch (type) {
      case 'props':
        if (name.includes('weapon') || name.includes('gun')) return 'Weapons'
        if (name.includes('phone') || name.includes('computer')) return 'Technology'
        if (name.includes('car') || name.includes('vehicle')) return 'Vehicles'
        return 'General Props'
        
      case 'wardrobe':
        if (name.includes('uniform') || name.includes('costume')) return 'Costumes'
        if (name.includes('formal') || name.includes('suit')) return 'Formal Wear'
        return 'General Wardrobe'
        
      case 'sfx':
        if (name.includes('explosion') || name.includes('gunshot')) return 'Action SFX'
        if (name.includes('weather') || name.includes('nature')) return 'Environmental SFX'
        return 'General SFX'
        
      default:
        return 'General'
    }
  }

  /**
   * Calculate confidence score for auto-detection
   */
  private calculateConfidence(pattern: RegExp, match: string): number {
    // Simple confidence calculation - can be enhanced
    const baseConfidence = 0.7
    
    // Boost confidence for exact matches
    if (pattern.test(match)) {
      return Math.min(1.0, baseConfidence + 0.2)
    }
    
    return baseConfidence
  }

  /**
   * Format element name for consistency
   */
  private formatElementName(name: string): string {
    return name
      .split(' ')
      .map(word => word.charAt(0).toUpperCase() + word.slice(1).toLowerCase())
      .join(' ')
  }
}

/**
 * Quick function to get elements from script content
 */
export function analyzeScriptForElements(scriptContent: string): BreakdownElement[] {
  const engine = new AutoBreakdownEngine(scriptContent)
  return engine.performFullBreakdown().elements
}

/**
 * Quick function to get scene breakdown from script content
 */
export function analyzeScriptForScenes(scriptContent: string): SceneBreakdown[] {
  const engine = new AutoBreakdownEngine(scriptContent)
  return engine.performFullBreakdown().scenes
}