// Screenplay auto-formatting utilities

export interface ScreenplayElement {
  type: 'scene_heading' | 'action' | 'character' | 'dialogue' | 'parenthetical' | 'transition' | 'shot'
  content: string
  position: number
}

export const ELEMENT_PATTERNS = {
  // Scene headings: INT./EXT. LOCATION - TIME
  SCENE_HEADING: /^(INT|EXT|INT\/EXT|INTERIOR|EXTERIOR)[\.\s]+(.+?)[\s\-]+(DAY|NIGHT|DAWN|DUSK|MORNING|AFTERNOON|EVENING|LATER|CONTINUOUS|SAME TIME)/i,
  
  // Character names: ALL CAPS at start of line, possibly with (CONT'D) or (O.S.) etc
  CHARACTER: /^[A-Z][A-Z\s]+(\([^)]*\))?$/,
  
  // Transitions: FADE IN:, CUT TO:, etc.
  TRANSITION: /^(FADE IN|FADE OUT|FADE TO BLACK|CUT TO|DISSOLVE TO|MATCH CUT|SMASH CUT|JUMP CUT)[\:\s]/i,
  
  // Parentheticals: (something in parentheses)
  PARENTHETICAL: /^\([^)]+\)$/,
  
  // Shots: CLOSE UP, WIDE SHOT, etc.
  SHOT: /^(CLOSE UP|CLOSE-UP|WIDE SHOT|MEDIUM SHOT|TWO SHOT|INSERT|POV|ANGLE ON)/i
}

export const COMMON_SCENE_PREFIXES = ['INT.', 'EXT.', 'INT/EXT.', 'INTERIOR', 'EXTERIOR']
export const COMMON_TIMES = ['DAY', 'NIGHT', 'DAWN', 'DUSK', 'MORNING', 'AFTERNOON', 'EVENING', 'LATER', 'CONTINUOUS', 'SAME TIME']
export const COMMON_TRANSITIONS = ['FADE IN:', 'FADE OUT.', 'FADE TO BLACK.', 'CUT TO:', 'DISSOLVE TO:', 'MATCH CUT TO:', 'SMASH CUT TO:']

export function detectElementType(line: string, previousElementType?: ScreenplayElement['type']): ScreenplayElement['type'] {
  const trimmedLine = line.trim()
  
  if (!trimmedLine) return 'action'
  
  // Scene headings
  if (ELEMENT_PATTERNS.SCENE_HEADING.test(trimmedLine)) {
    return 'scene_heading'
  }
  
  // Transitions
  if (ELEMENT_PATTERNS.TRANSITION.test(trimmedLine)) {
    return 'transition'
  }
  
  // Shots
  if (ELEMENT_PATTERNS.SHOT.test(trimmedLine)) {
    return 'shot'
  }
  
  // Parentheticals
  if (ELEMENT_PATTERNS.PARENTHETICAL.test(trimmedLine)) {
    return 'parenthetical'
  }
  
  // Character names (all caps, possibly with extensions)
  if (ELEMENT_PATTERNS.CHARACTER.test(trimmedLine)) {
    return 'character'
  }
  
  // Dialogue detection: if previous line was character name or parenthetical, and current line isn't empty, it's dialogue
  if (previousElementType === 'character' || previousElementType === 'parenthetical') {
    // Make sure it's not another structural element
    if (!ELEMENT_PATTERNS.SCENE_HEADING.test(trimmedLine) && 
        !ELEMENT_PATTERNS.TRANSITION.test(trimmedLine) &&
        !ELEMENT_PATTERNS.SHOT.test(trimmedLine) &&
        !ELEMENT_PATTERNS.CHARACTER.test(trimmedLine)) {
      return 'dialogue'
    }
  }
  
  // Everything else is action
  return 'action'
}

// Context-aware version for script processing
export function detectElementTypeWithContext(lines: string[], currentIndex: number): ScreenplayElement['type'] {
  const currentLine = lines[currentIndex]
  const previousLine = currentIndex > 0 ? lines[currentIndex - 1] : ''
  const previousElementType = currentIndex > 0 ? detectElementType(previousLine) : undefined
  
  return detectElementType(currentLine, previousElementType)
}

export function autoFormatLine(line: string, elementType: ScreenplayElement['type']): string {
  const trimmedLine = line.trim()
  
  switch (elementType) {
    case 'scene_heading':
      return trimmedLine.toUpperCase()
      
    case 'character':
      return trimmedLine.toUpperCase()
      
    case 'transition':
      return trimmedLine.toUpperCase()
      
    case 'shot':
      return trimmedLine.toUpperCase()
      
    case 'parenthetical':
      return `(${trimmedLine.replace(/^\(|\)$/g, '').toLowerCase()})`
      
    case 'dialogue':
    case 'action':
    default:
      return trimmedLine
  }
}

export function getAutoCompletesuggestions(partial: string, elementType: ScreenplayElement['type']): string[] {
  const upperPartial = partial.toUpperCase()
  
  switch (elementType) {
    case 'scene_heading':
      const suggestions: string[] = []
      
      // Scene prefixes
      COMMON_SCENE_PREFIXES.forEach(prefix => {
        if (prefix.startsWith(upperPartial)) {
          suggestions.push(prefix + ' ')
        }
      })
      
      // If already has INT./EXT., suggest times
      if (partial.includes(' - ') || partial.endsWith(' -')) {
        COMMON_TIMES.forEach(time => {
          if (time.startsWith(upperPartial.split(' - ').pop() || '')) {
            suggestions.push(time)
          }
        })
      }
      
      return suggestions
      
    case 'transition':
      return COMMON_TRANSITIONS.filter(transition => 
        transition.startsWith(upperPartial)
      )
      
    default:
      return []
  }
}

export function insertIndentation(elementType: ScreenplayElement['type']): string {
  switch (elementType) {
    case 'scene_heading':
    case 'action':
    case 'shot':
      return '' // No indentation
      
    case 'character':
      return '                    ' // 20 spaces (centered)
      
    case 'dialogue':
      return '          ' // 10 spaces (dialogue indent)
      
    case 'parenthetical':
      return '               ' // 15 spaces (centered parenthetical)
      
    case 'transition':
      return '                                        ' // Right-aligned
      
    default:
      return ''
  }
}

export function shouldAutoCapitalize(elementType: ScreenplayElement['type']): boolean {
  return ['scene_heading', 'character', 'transition', 'shot'].includes(elementType)
}

export function formatEntireScript(script: string): string {
  const lines = script.split('\n')
  
  const formattedLines = lines.map((line, index) => {
    const elementType = detectElementTypeWithContext(lines, index)
    
    // Don't auto-format dialogue content to preserve natural speech
    if (elementType === 'dialogue') {
      return line
    }
    
    return autoFormatLine(line, elementType)
  })
  
  return formattedLines.join('\n')
}

export function extractScriptElements(script: string): ScreenplayElement[] {
  const lines = script.split('\n')
  const elements: ScreenplayElement[] = []
  let position = 0
  
  lines.forEach((line, index) => {
    if (line.trim()) {
      const elementType = detectElementTypeWithContext(lines, index)
      
      elements.push({
        type: elementType,
        content: line.trim(),
        position
      })
    }
    position += line.length + 1 // +1 for newline
  })
  
  return elements
}