import { useState, useRef } from 'react'
import { 
  detectElementType, 
  detectElementTypeWithContext,
  autoFormatLine, 
  getAutoCompletesuggestions,
  insertIndentation,
  shouldAutoCapitalize
} from '@/utils/screenplayFormatter'

interface EnhancedScriptEditorProps {
  value: string
  onChange: (value: string) => void
  placeholder?: string
  className?: string
}

interface AutoComplete {
  suggestions: string[]
  position: number
  selectedIndex: number
  isVisible: boolean
}

export default function EnhancedScriptEditor({ 
  value, 
  onChange, 
  placeholder, 
  className = '' 
}: EnhancedScriptEditorProps) {
  const textareaRef = useRef<HTMLTextAreaElement>(null)
  const [autoComplete, setAutoComplete] = useState<AutoComplete>({
    suggestions: [],
    position: 0,
    selectedIndex: 0,
    isVisible: false
  })
  const [tooltip, setTooltip] = useState<{
    text: string
    isVisible: boolean
    elementType?: string
  }>({
    text: '',
    isVisible: false
  })

  const getCurrentLine = (): string => {
    if (!textareaRef.current) return ''
    
    const textarea = textareaRef.current
    const cursorPos = textarea.selectionStart
    const textBeforeCursor = value.substring(0, cursorPos)
    const lines = textBeforeCursor.split('\n')
    
    return lines[lines.length - 1]
  }

  const getCurrentLineStart = (): number => {
    if (!textareaRef.current) return 0
    
    const textarea = textareaRef.current
    const cursorPos = textarea.selectionStart
    const lastNewlineIndex = value.substring(0, cursorPos).lastIndexOf('\n')
    
    return lastNewlineIndex + 1
  }

  const replaceCurrentLine = (newLine: string) => {
    if (!textareaRef.current) return
    
    const textarea = textareaRef.current
    const cursorPos = textarea.selectionStart
    const lineStart = getCurrentLineStart()
    const textAfterCursor = value.substring(cursorPos)
    const currentLineEnd = textAfterCursor.indexOf('\n')
    const lineEnd = currentLineEnd === -1 ? value.length : cursorPos + currentLineEnd
    
    const newValue = value.substring(0, lineStart) + newLine + value.substring(lineEnd)
    onChange(newValue)
    
    // Restore cursor position
    setTimeout(() => {
      const newCursorPos = lineStart + newLine.length
      textarea.setSelectionRange(newCursorPos, newCursorPos)
      textarea.focus()
    }, 0)
  }

  const handleKeyDown = (e: React.KeyboardEvent<HTMLTextAreaElement>) => {
    const textarea = textareaRef.current
    if (!textarea) return

    // Handle Tab for auto-completion
    if (e.key === 'Tab' && autoComplete.isVisible) {
      e.preventDefault()
      const selectedSuggestion = autoComplete.suggestions[autoComplete.selectedIndex]
      if (selectedSuggestion) {
        replaceCurrentLine(selectedSuggestion)
        setAutoComplete(prev => ({ ...prev, isVisible: false }))
      }
      return
    }

    // Handle arrow keys for auto-completion navigation
    if (autoComplete.isVisible) {
      if (e.key === 'ArrowDown') {
        e.preventDefault()
        setAutoComplete(prev => ({
          ...prev,
          selectedIndex: (prev.selectedIndex + 1) % prev.suggestions.length
        }))
        return
      }
      if (e.key === 'ArrowUp') {
        e.preventDefault()
        setAutoComplete(prev => ({
          ...prev,
          selectedIndex: prev.selectedIndex === 0 ? prev.suggestions.length - 1 : prev.selectedIndex - 1
        }))
        return
      }
      if (e.key === 'Escape') {
        setAutoComplete(prev => ({ ...prev, isVisible: false }))
        return
      }
    }

    // Handle Enter key for auto-formatting and smart indentation
    if (e.key === 'Enter') {
      const currentLine = getCurrentLine()
      const elementType = detectElementType(currentLine)
      
      // Auto-format current line before creating new line
      if (currentLine.trim()) {
        const formattedLine = autoFormatLine(currentLine, elementType)
        if (formattedLine !== currentLine) {
          replaceCurrentLine(formattedLine)
        }
      }

      // Determine next line type and indentation
      let nextIndentation = ''
      if (elementType === 'character') {
        // After character name, next line should be dialogue
        nextIndentation = insertIndentation('dialogue')
      } else if (elementType === 'scene_heading') {
        // After scene heading, action follows
        nextIndentation = insertIndentation('action')
      }

      // Insert new line with proper indentation
      if (nextIndentation) {
        e.preventDefault()
        const cursorPos = textarea.selectionStart
        const newValue = value.substring(0, cursorPos) + '\n' + nextIndentation + value.substring(cursorPos)
        onChange(newValue)
        
        setTimeout(() => {
          const newCursorPos = cursorPos + 1 + nextIndentation.length
          textarea.setSelectionRange(newCursorPos, newCursorPos)
          textarea.focus()
        }, 0)
      }
    }

    // Handle colon for transitions
    if (e.key === ':') {
      const currentLine = getCurrentLine()
      if (currentLine.trim().match(/^(FADE IN|CUT TO|DISSOLVE TO)$/i)) {
        setTimeout(() => {
          const newLine = getCurrentLine()
          const formattedLine = autoFormatLine(newLine, 'transition')
          if (formattedLine !== newLine) {
            replaceCurrentLine(formattedLine)
          }
        }, 0)
      }
    }
  }

  const handleInput = (e: React.FormEvent<HTMLTextAreaElement>) => {
    const newValue = (e.target as HTMLTextAreaElement).value
    onChange(newValue)

    // Get current line and context for auto-formatting and suggestions
    const currentLine = getCurrentLine()
    if (!currentLine.trim()) {
      setAutoComplete(prev => ({ ...prev, isVisible: false }))
      setTooltip(prev => ({ ...prev, isVisible: false }))
      return
    }

    // Get context-aware element type detection
    const lines = newValue.split('\n')
    const cursorPos = textareaRef.current?.selectionStart || 0
    const textBeforeCursor = newValue.substring(0, cursorPos)
    const currentLineIndex = textBeforeCursor.split('\n').length - 1
    
    const elementType = detectElementTypeWithContext(lines, currentLineIndex)
    
    // Auto-capitalization for certain elements
    if (shouldAutoCapitalize(elementType)) {
      const upperLine = currentLine.toUpperCase()
      if (upperLine !== currentLine) {
        setTimeout(() => {
          replaceCurrentLine(upperLine)
        }, 0)
      }
    }

    // Show contextual tooltip
    updateTooltip(elementType)

    // Show auto-complete suggestions
    const suggestions = getAutoCompletesuggestions(currentLine, elementType)
    if (suggestions.length > 0) {
      setAutoComplete({
        suggestions,
        position: getCurrentLineStart(),
        selectedIndex: 0,
        isVisible: true
      })
    } else {
      setAutoComplete(prev => ({ ...prev, isVisible: false }))
    }
  }

  const updateTooltip = (elementType: string) => {
    let tooltipText = ''
    
    switch (elementType) {
      case 'scene_heading':
        tooltipText = '🎬 Scene Heading: Use INT./EXT. LOCATION - TIME format'
        break
      case 'character':
        tooltipText = '👤 Character Name: Type in ALL CAPS, press Enter for dialogue'
        break
      case 'dialogue':
        tooltipText = '💬 Dialogue: Character speech - will be indented in final format'
        break
      case 'parenthetical':
        tooltipText = '📝 Parenthetical: Actor direction in (parentheses)'
        break
      case 'action':
        tooltipText = '🎯 Action: Scene description and action lines'
        break
      case 'transition':
        tooltipText = '🎞️ Transition: Scene transitions like CUT TO:, FADE OUT'
        break
      default:
        tooltipText = ''
    }
    
    if (tooltipText) {
      setTooltip({
        text: tooltipText,
        isVisible: true,
        elementType
      })
      
      // Auto-hide tooltip after 3 seconds
      setTimeout(() => {
        setTooltip(prev => ({ ...prev, isVisible: false }))
      }, 3000)
    }
  }

  // Calculate autocomplete position
  const getAutoCompleteStyle = () => {
    if (!textareaRef.current || !autoComplete.isVisible) return { display: 'none' }
    
    const textarea = textareaRef.current
    const rect = textarea.getBoundingClientRect()
    const lineHeight = 24 // Approximate line height
    const textBeforeCursor = value.substring(0, textareaRef.current.selectionStart)
    const lineNumber = textBeforeCursor.split('\n').length - 1
    
    return {
      position: 'absolute' as const,
      left: rect.left + 20, // Approximate character width
      top: rect.top + (lineNumber + 1) * lineHeight,
      zIndex: 1000,
      backgroundColor: 'white',
      border: '1px solid #e2e8f0',
      borderRadius: '6px',
      boxShadow: '0 4px 6px -1px rgba(0, 0, 0, 0.1)',
      minWidth: '200px'
    }
  }

  return (
    <div className="relative">
      <textarea
        ref={textareaRef}
        value={value}
        onChange={handleInput}
        onKeyDown={handleKeyDown}
        placeholder={placeholder}
        className={`${className} script-font`}
        style={{
          fontFamily: 'Courier New, monospace',
          fontSize: '12pt',
          lineHeight: '24px',
          whiteSpace: 'pre-wrap',
          backgroundColor: '#ffffff',
          color: '#1f2937',
          border: 'none',
          outline: 'none',
          padding: '24px',
          width: '100%',
          height: '100%',
          resize: 'none'
        }}
      />
      
      {/* Auto-complete dropdown */}
      {autoComplete.isVisible && (
        <div style={getAutoCompleteStyle()}>
          <div className="py-1 max-h-60 overflow-y-auto">
            {autoComplete.suggestions.map((suggestion, index) => (
              <div
                key={index}
                className={`px-3 py-2 cursor-pointer text-sm ${
                  index === autoComplete.selectedIndex
                    ? 'bg-primary-100 text-primary-900'
                    : 'text-gray-900 hover:bg-gray-100'
                }`}
                onClick={() => {
                  replaceCurrentLine(suggestion)
                  setAutoComplete(prev => ({ ...prev, isVisible: false }))
                }}
              >
                {suggestion}
              </div>
            ))}
          </div>
          <div className="px-3 py-1 bg-gray-50 border-t border-gray-200 text-xs text-gray-500">
            Press Tab to accept, ↑↓ to navigate, Esc to close
          </div>
        </div>
      )}

      {/* Contextual Tooltip */}
      {tooltip.isVisible && (
        <div className="absolute top-4 right-4 bg-gray-900 text-white px-3 py-2 rounded-lg shadow-lg z-20 max-w-sm">
          <div className="flex items-center space-x-2">
            <div className="text-sm font-medium">{tooltip.text}</div>
            <button 
              onClick={() => setTooltip(prev => ({ ...prev, isVisible: false }))}
              className="text-gray-300 hover:text-white"
            >
              <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
                <path fillRule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clipRule="evenodd" />
              </svg>
            </button>
          </div>
          <div className="absolute top-full left-1/2 transform -translate-x-1/2 w-0 h-0 border-4 border-transparent border-t-gray-900"></div>
        </div>
      )}
    </div>
  )
}