import React, { useState } from 'react'
import { SCRIPT_TEMPLATES, ScriptTemplate } from '@/templates/scriptTemplates'
import { 
  FilmIcon,
  TvIcon,
  MegaphoneIcon,
  DocumentTextIcon,
  SparklesIcon,
  MagnifyingGlassIcon,
  CheckIcon
} from '@heroicons/react/24/outline'

interface ScriptTemplateSelectorProps {
  onSelectTemplate: (template: ScriptTemplate | null) => void
  selectedTemplateId?: string
}

const categoryIcons = {
  film: FilmIcon,
  tv: TvIcon,
  commercial: MegaphoneIcon,
  documentary: DocumentTextIcon,
  other: SparklesIcon
}

const categoryLabels = {
  film: 'Film & Cinema',
  tv: 'Television & Web',
  commercial: 'Commercial & Advertising',
  documentary: 'Documentary & Non-Fiction',
  other: 'Other Formats'
}

const categoryColors = {
  film: 'bg-red-500',
  tv: 'bg-blue-500',
  commercial: 'bg-green-500',
  documentary: 'bg-purple-500',
  other: 'bg-orange-500'
}

export default function ScriptTemplateSelector({ onSelectTemplate }: ScriptTemplateSelectorProps) {
  const [selectedCategory, setSelectedCategory] = useState<ScriptTemplate['category'] | 'all'>('all')
  const [searchQuery, setSearchQuery] = useState('')
  const [selectedTemplate, setSelectedTemplate] = useState<ScriptTemplate | null>(null)
  const [showPreview, setShowPreview] = useState(false)

  const filteredTemplates = SCRIPT_TEMPLATES.filter(template => {
    const matchesCategory = selectedCategory === 'all' || template.category === selectedCategory
    const matchesSearch = searchQuery === '' || 
      template.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
      template.description.toLowerCase().includes(searchQuery.toLowerCase()) ||
      template.tags.some(tag => tag.toLowerCase().includes(searchQuery.toLowerCase()))
    
    return matchesCategory && matchesSearch
  })

  const handleSelectTemplate = (template: ScriptTemplate) => {
    setSelectedTemplate(template)
    onSelectTemplate(template)
  }

  const handleStartBlank = () => {
    setSelectedTemplate(null)
    onSelectTemplate(null)
  }

  const categories = Object.keys(categoryLabels) as ScriptTemplate['category'][]

  return (
    <div className="max-w-6xl mx-auto">
      {/* Header */}
      <div className="text-center mb-8">
        <h2 className="text-3xl font-bold text-gray-900 mb-4">Choose Your Script Template</h2>
        <p className="text-lg text-gray-600 max-w-2xl mx-auto">
          Start with a professional template or begin with a blank script. 
          Our templates follow industry standards and include helpful formatting guides.
        </p>
      </div>

      {/* Search and Filter Bar */}
      <div className="flex flex-col md:flex-row gap-4 mb-8">
        {/* Search */}
        <div className="flex-1 relative">
          <MagnifyingGlassIcon className="h-5 w-5 absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400" />
          <input
            type="text"
            placeholder="Search templates..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            className="w-full pl-10 pr-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-orange-500 focus:border-transparent"
          />
        </div>

        {/* Category Filter */}
        <div className="flex flex-wrap gap-2">
          <button
            onClick={() => setSelectedCategory('all')}
            className={`px-4 py-2 rounded-lg font-medium transition-all ${
              selectedCategory === 'all'
                ? 'bg-orange-500 text-white shadow-lg'
                : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
            }`}
          >
            All Templates
          </button>
          {categories.map(category => {
            const Icon = categoryIcons[category]
            return (
              <button
                key={category}
                onClick={() => setSelectedCategory(category)}
                className={`flex items-center px-4 py-2 rounded-lg font-medium transition-all ${
                  selectedCategory === category
                    ? 'bg-orange-500 text-white shadow-lg'
                    : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                }`}
              >
                <Icon className="h-4 w-4 mr-2" />
                {categoryLabels[category]}
              </button>
            )
          })}
        </div>
      </div>

      {/* Templates Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 mb-8">
        {/* Blank Template Option */}
        <div
          onClick={handleStartBlank}
          className={`group cursor-pointer bg-gradient-to-br from-orange-100 to-orange-200 rounded-2xl p-6 border-2 transition-all hover:shadow-xl hover:scale-105 ${
            selectedTemplate === null ? 'border-orange-500 bg-orange-50' : 'border-gray-200'
          }`}
        >
          <div className="text-center">
            <div className="w-16 h-16 bg-gradient-to-br from-orange-500 to-orange-600 rounded-xl flex items-center justify-center mx-auto mb-4">
              <SparklesIcon className="h-8 w-8 text-white" />
            </div>
            <h3 className="text-xl font-bold text-gray-900 mb-2">Blank Script</h3>
            <p className="text-gray-600 text-sm mb-4">
              Start from scratch with a clean, properly formatted screenplay document.
            </p>
            <div className="flex flex-wrap gap-1 justify-center">
              <span className="px-2 py-1 bg-orange-200 text-orange-800 rounded text-xs font-medium">
                Custom
              </span>
              <span className="px-2 py-1 bg-orange-200 text-orange-800 rounded text-xs font-medium">
                Flexible
              </span>
            </div>
          </div>
          {selectedTemplate === null && (
            <div className="absolute -top-2 -right-2 w-8 h-8 bg-orange-500 rounded-full flex items-center justify-center">
              <CheckIcon className="h-5 w-5 text-white" />
            </div>
          )}
        </div>

        {/* Template Cards */}
        {filteredTemplates.map(template => {
          const Icon = categoryIcons[template.category]
          const isSelected = selectedTemplate?.id === template.id
          
          return (
            <div
              key={template.id}
              onClick={() => handleSelectTemplate(template)}
              className={`group cursor-pointer bg-white rounded-2xl p-6 border-2 transition-all hover:shadow-xl hover:scale-105 relative ${
                isSelected ? 'border-orange-500 bg-orange-50' : 'border-gray-200'
              }`}
            >
              <div className="flex items-start justify-between mb-4">
                <div className={`w-12 h-12 ${categoryColors[template.category]} rounded-lg flex items-center justify-center flex-shrink-0`}>
                  <Icon className="h-6 w-6 text-white" />
                </div>
                {isSelected && (
                  <div className="w-8 h-8 bg-orange-500 rounded-full flex items-center justify-center">
                    <CheckIcon className="h-5 w-5 text-white" />
                  </div>
                )}
              </div>
              
              <h3 className="text-lg font-bold text-gray-900 mb-2">{template.name}</h3>
              <p className="text-gray-600 text-sm mb-4 line-clamp-3">{template.description}</p>
              
              <div className="flex flex-wrap gap-1 mb-4">
                {template.tags.slice(0, 3).map(tag => (
                  <span key={tag} className="px-2 py-1 bg-gray-100 text-gray-600 rounded text-xs font-medium">
                    {tag}
                  </span>
                ))}
                {template.tags.length > 3 && (
                  <span className="px-2 py-1 bg-gray-100 text-gray-600 rounded text-xs font-medium">
                    +{template.tags.length - 3}
                  </span>
                )}
              </div>
              
              <button
                onClick={(e) => {
                  e.stopPropagation()
                  setSelectedTemplate(template)
                  setShowPreview(true)
                }}
                className="w-full py-2 bg-gray-100 hover:bg-gray-200 text-gray-700 rounded-lg transition-colors text-sm font-medium"
              >
                Preview Template
              </button>
            </div>
          )
        })}
      </div>

      {/* Empty State */}
      {filteredTemplates.length === 0 && (
        <div className="text-center py-12">
          <DocumentTextIcon className="h-16 w-16 text-gray-400 mx-auto mb-4" />
          <h3 className="text-lg font-medium text-gray-900 mb-2">No templates found</h3>
          <p className="text-gray-600">Try adjusting your search or category filter.</p>
        </div>
      )}

      {/* Template Preview Modal */}
      {showPreview && selectedTemplate && (
        <div className="fixed inset-0 z-50 overflow-y-auto bg-black bg-opacity-50 flex items-center justify-center p-4">
          <div className="bg-white rounded-2xl max-w-4xl w-full max-h-[80vh] overflow-hidden">
            <div className="p-6 border-b border-gray-200">
              <div className="flex items-center justify-between">
                <div className="flex items-center space-x-4">
                  <div className={`w-12 h-12 ${categoryColors[selectedTemplate.category]} rounded-lg flex items-center justify-center`}>
                    {React.createElement(categoryIcons[selectedTemplate.category], { className: "h-6 w-6 text-white" })}
                  </div>
                  <div>
                    <h3 className="text-xl font-bold text-gray-900">{selectedTemplate.name}</h3>
                    <p className="text-gray-600">{selectedTemplate.description}</p>
                  </div>
                </div>
                <button
                  onClick={() => setShowPreview(false)}
                  className="w-8 h-8 bg-gray-100 rounded-lg flex items-center justify-center hover:bg-gray-200 transition-colors"
                >
                  ×
                </button>
              </div>
            </div>
            <div className="p-6 overflow-y-auto max-h-96">
              <pre className="text-sm font-mono leading-6 whitespace-pre-wrap text-gray-800 bg-gray-50 p-4 rounded-lg">
                {selectedTemplate.content.substring(0, 1500)}
                {selectedTemplate.content.length > 1500 && '\n\n... [Content continues in full template]'}
              </pre>
            </div>
            <div className="p-6 border-t border-gray-200 flex justify-end space-x-3">
              <button
                onClick={() => setShowPreview(false)}
                className="px-6 py-2 bg-gray-100 hover:bg-gray-200 text-gray-700 rounded-lg transition-colors"
              >
                Close
              </button>
              <button
                onClick={() => {
                  handleSelectTemplate(selectedTemplate)
                  setShowPreview(false)
                }}
                className="px-6 py-2 bg-orange-500 hover:bg-orange-600 text-white rounded-lg transition-colors"
              >
                Use This Template
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}