import { create } from 'zustand'
import { persist } from 'zustand/middleware'

export interface User {
  id: string
  email: string
  name: string
  role: 'admin' | 'producer' | 'writer' | 'collaborator'
  avatar?: string
  preferences: {
    theme: 'light' | 'dark' | 'auto'
    scriptFont: string
    scriptFontSize: number
  }
  subscription: {
    plan: 'free' | 'individual' | 'company' | 'enterprise'
    status: 'active' | 'inactive' | 'trial'
    expiresAt?: string
  }
  createdAt: string
  lastLoginAt: string
}

interface AuthState {
  user: User | null
  token: string | null
  isLoading: boolean
  isAuthenticated: boolean
  
  // Actions
  login: (email: string, password: string) => Promise<boolean>
  register: (name: string, email: string, password: string) => Promise<boolean>
  logout: () => void
  updateProfile: (updates: Partial<User>) => Promise<void>
  initializeAuth: () => void
  refreshToken: () => Promise<boolean>
}

export const useAuthStore = create<AuthState>()(
  persist(
    (set, get) => ({
      user: null,
      token: null,
      isLoading: false,
      isAuthenticated: false,

      login: async (email: string, _password: string) => {
        set({ isLoading: true })
        
        try {
          // For standalone app, simulate authentication
          // Accept any valid email format
          const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
          if (!emailRegex.test(email)) {
            throw new Error('Invalid email format')
          }
          
          // Create a mock user based on email
          const mockUser: User = {
            id: 'standalone-user',
            name: email.split('@')[0].charAt(0).toUpperCase() + email.split('@')[0].slice(1),
            email: email,
            role: 'admin' as const,
            avatar: `https://ui-avatars.com/api/?name=${email.split('@')[0]}&background=2563eb&color=fff`,
            preferences: {
              theme: 'light' as const,
              scriptFont: 'Courier New',
              scriptFontSize: 12
            },
            subscription: {
              plan: 'enterprise' as const,
              status: 'active' as const
            },
            createdAt: new Date().toISOString(),
            lastLoginAt: new Date().toISOString()
          }
          
          const mockToken = `standalone-token-${Date.now()}`
          
          // Save to localStorage for persistence
          localStorage.setItem('hemantdoc_user', JSON.stringify(mockUser))
          localStorage.setItem('hemantdoc_token', mockToken)
          
          set({
            user: mockUser,
            token: mockToken,
            isAuthenticated: true,
            isLoading: false,
          })
          
          console.log('✅ Standalone login successful:', mockUser)
          return true
          
        } catch (error) {
          console.error('Login error:', error)
          set({ isLoading: false })
          return false
        }
      },

      register: async (name: string, email: string, _password: string) => {
        set({ isLoading: true })
        
        try {
          // For standalone app, simulate registration
          const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
          if (!emailRegex.test(email) || !name.trim()) {
            throw new Error('Invalid name or email format')
          }
          
          const mockUser: User = {
            id: `user-${Date.now()}`,
            name: name.trim(),
            email: email,
            role: 'admin' as const,
            avatar: `https://ui-avatars.com/api/?name=${name}&background=2563eb&color=fff`,
            preferences: {
              theme: 'light' as const,
              scriptFont: 'Courier New',
              scriptFontSize: 12
            },
            subscription: {
              plan: 'enterprise' as const,
              status: 'active' as const
            },
            createdAt: new Date().toISOString(),
            lastLoginAt: new Date().toISOString()
          }
          
          const mockToken = `standalone-token-${Date.now()}`
          
          // Save to localStorage
          localStorage.setItem('hemantdoc_user', JSON.stringify(mockUser))
          localStorage.setItem('hemantdoc_token', mockToken)
          
          set({
            user: mockUser,
            token: mockToken,
            isAuthenticated: true,
            isLoading: false,
          })
          
          console.log('✅ Standalone registration successful:', mockUser)
          return true
          
        } catch (error) {
          console.error('Registration error:', error)
          set({ isLoading: false })
          return false
        }
      },

      logout: () => {
        // Clear localStorage
        localStorage.removeItem('hemantdoc_user')
        localStorage.removeItem('hemantdoc_token')
        
        set({
          user: null,
          token: null,
          isAuthenticated: false,
        })
        
        console.log('✅ User logged out')
      },

      updateProfile: async (updates: Partial<User>) => {
        const { user } = get()
        if (!user) return

        try {
          // For standalone app, update locally
          const updatedUser = { ...user, ...updates }
          
          // Save to localStorage
          localStorage.setItem('hemantdoc_user', JSON.stringify(updatedUser))
          
          set({ user: updatedUser })
          console.log('✅ Profile updated:', updatedUser)
        } catch (error) {
          console.error('Profile update error:', error)
          throw error
        }
      },

      initializeAuth: () => {
        try {
          // Try to restore from localStorage
          const savedUser = localStorage.getItem('hemantdoc_user')
          const savedToken = localStorage.getItem('hemantdoc_token')
          
          if (savedUser && savedToken) {
            const user = JSON.parse(savedUser)
            set({
              user,
              token: savedToken,
              isAuthenticated: true,
            })
            console.log('✅ Auto-login successful:', user)
          }
        } catch (error) {
          console.error('Auto-login failed:', error)
          // Clear corrupted data
          localStorage.removeItem('hemantdoc_user')
          localStorage.removeItem('hemantdoc_token')
        }
      },

      refreshToken: async () => {
        // For standalone app, tokens don't need refreshing
        const { user, token } = get()
        
        if (!user || !token) {
          return false
        }
        
        // Token is always valid in standalone mode
        console.log('✅ Token refresh (standalone mode)')
        return true
      },
    }),
    {
      name: 'auth-store',
      partialize: (state) => ({ 
        user: state.user, 
        token: state.token,
        isAuthenticated: state.isAuthenticated,
      }),
    }
  )
)