import Modal from '../ui/Modal'
import { CommandLineIcon, KeyIcon } from '@heroicons/react/24/outline'

interface KeyboardShortcut {
  category: string
  shortcuts: {
    keys: string
    description: string
    mac?: string
    windows?: string
  }[]
}

interface KeyboardShortcutsModalProps {
  isOpen: boolean
  onClose: () => void
}

const shortcuts: KeyboardShortcut[] = [
  {
    category: 'Script Formatting',
    shortcuts: [
      { keys: 'Cmd+1', description: 'Scene Heading', windows: 'Ctrl+1' },
      { keys: 'Cmd+2', description: 'Action', windows: 'Ctrl+2' },
      { keys: 'Cmd+3', description: 'Character', windows: 'Ctrl+3' },
      { keys: 'Cmd+4', description: 'Dialogue', windows: 'Ctrl+4' },
      { keys: 'Cmd+5', description: 'Parenthetical', windows: 'Ctrl+5' },
      { keys: 'Cmd+6', description: 'Transition', windows: 'Ctrl+6' },
      { keys: 'Cmd+7', description: 'Shot', windows: 'Ctrl+7' },
      { keys: 'Cmd+Shift+F', description: 'Auto-format entire script', windows: 'Ctrl+Shift+F' },
    ]
  },
  {
    category: 'File Operations',
    shortcuts: [
      { keys: 'Cmd+N', description: 'New script', windows: 'Ctrl+N' },
      { keys: 'Cmd+O', description: 'Open/Import script', windows: 'Ctrl+O' },
      { keys: 'Cmd+S', description: 'Save script', windows: 'Ctrl+S' },
      { keys: 'Cmd+Shift+S', description: 'Save As...', windows: 'Ctrl+Shift+S' },
      { keys: 'Cmd+E', description: 'Export as PDF', windows: 'Ctrl+E' },
      { keys: 'Cmd+Shift+E', description: 'Export as Final Draft', windows: 'Ctrl+Shift+E' },
      { keys: 'Cmd+Alt+E', description: 'Export as Text', windows: 'Ctrl+Alt+E' },
    ]
  },
  {
    category: 'Navigation & View',
    shortcuts: [
      { keys: 'Cmd+P', description: 'Toggle Preview mode', windows: 'Ctrl+P' },
      { keys: 'Cmd+R', description: 'View Revisions', windows: 'Ctrl+R' },
      { keys: 'Cmd+K', description: 'Keyboard shortcuts (this dialog)', windows: 'Ctrl+K' },
      { keys: 'Cmd+F', description: 'Find in script', windows: 'Ctrl+F' },
      { keys: 'Cmd+G', description: 'Find next', windows: 'Ctrl+G' },
      { keys: 'Cmd+Shift+G', description: 'Find previous', windows: 'Ctrl+Shift+G' },
    ]
  },
  {
    category: 'Editing',
    shortcuts: [
      { keys: 'Tab', description: 'Accept autocomplete suggestion' },
      { keys: '↑↓', description: 'Navigate autocomplete suggestions' },
      { keys: 'Esc', description: 'Close autocomplete/dialogs' },
      { keys: 'Cmd+Z', description: 'Undo', windows: 'Ctrl+Z' },
      { keys: 'Cmd+Shift+Z', description: 'Redo', windows: 'Ctrl+Y' },
      { keys: 'Cmd+A', description: 'Select all', windows: 'Ctrl+A' },
      { keys: 'Cmd+C', description: 'Copy', windows: 'Ctrl+C' },
      { keys: 'Cmd+V', description: 'Paste', windows: 'Ctrl+V' },
      { keys: 'Cmd+X', description: 'Cut', windows: 'Ctrl+X' },
    ]
  },
  {
    category: 'Smart Features',
    shortcuts: [
      { keys: 'Enter', description: 'Smart line break with proper indentation' },
      { keys: 'Shift+Enter', description: 'Simple line break' },
      { keys: 'Cmd+/', description: 'Toggle comment/note', windows: 'Ctrl+/' },
      { keys: 'Cmd+D', description: 'Duplicate current line', windows: 'Ctrl+D' },
      { keys: 'Cmd+L', description: 'Go to line', windows: 'Ctrl+L' },
    ]
  },
  {
    category: 'Production Tools',
    shortcuts: [
      { keys: 'Cmd+B', description: 'Open Script Breakdown', windows: 'Ctrl+B' },
      { keys: 'Cmd+T', description: 'Create Task', windows: 'Ctrl+T' },
      { keys: 'Cmd+M', description: 'Add Note/Comment', windows: 'Ctrl+M' },
      { keys: 'Cmd+Shift+B', description: 'Auto-breakdown current scene', windows: 'Ctrl+Shift+B' },
    ]
  }
]

const isMac = typeof navigator !== 'undefined' && navigator.platform.toUpperCase().indexOf('MAC') >= 0

export default function KeyboardShortcutsModal({ isOpen, onClose }: KeyboardShortcutsModalProps) {
  const renderKey = (keyString: string) => {
    const keys = keyString.split('+')
    return (
      <div className="flex items-center space-x-1">
        {keys.map((key, index) => (
          <div key={index} className="flex items-center">
            <kbd className="px-2 py-1 text-xs font-medium bg-gray-100 border border-gray-300 rounded shadow-sm">
              {key === 'Cmd' && !isMac ? 'Ctrl' : key}
            </kbd>
            {index < keys.length - 1 && <span className="text-gray-400 mx-1">+</span>}
          </div>
        ))}
      </div>
    )
  }

  return (
    <Modal isOpen={isOpen} onClose={onClose} title="Keyboard Shortcuts" size="xl">
      <div className="max-h-[70vh] overflow-y-auto">
        <div className="mb-4 p-3 bg-blue-50 border border-blue-200 rounded-lg">
          <div className="flex items-center text-blue-800 text-sm">
            <KeyIcon className="h-4 w-4 mr-2" />
            <span className="font-medium">Pro Tip:</span>
            <span className="ml-1">All shortcuts work in both editing and preview modes</span>
          </div>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          {shortcuts.map((category) => (
            <div key={category.category} className="bg-gray-50 rounded-lg p-4">
              <h3 className="font-semibold text-gray-900 mb-3 flex items-center">
                <CommandLineIcon className="h-4 w-4 mr-2" />
                {category.category}
              </h3>
              <div className="space-y-2">
                {category.shortcuts.map((shortcut, index) => (
                  <div key={index} className="flex items-center justify-between bg-white rounded px-3 py-2">
                    <div className="flex-1">
                      <span className="text-sm text-gray-700">{shortcut.description}</span>
                    </div>
                    <div className="ml-4">
                      {renderKey(isMac ? shortcut.keys : (shortcut.windows || shortcut.keys))}
                    </div>
                  </div>
                ))}
              </div>
            </div>
          ))}
        </div>

        <div className="mt-6 p-4 bg-yellow-50 border border-yellow-200 rounded-lg">
          <h4 className="font-medium text-yellow-800 mb-2">Quick Start Tips</h4>
          <ul className="text-sm text-yellow-700 space-y-1">
            <li>• Type "INT." or "EXT." and the line will auto-format as a scene heading</li>
            <li>• Character names in ALL CAPS will be auto-detected and formatted</li>
            <li>• Press Tab to accept autocomplete suggestions</li>
            <li>• Use Enter after character names for automatic dialogue indentation</li>
            <li>• The script auto-saves every 2 seconds while typing</li>
          </ul>
        </div>

        <div className="mt-4 text-center">
          <button
            onClick={onClose}
            className="btn-primary"
          >
            Got it, thanks!
          </button>
        </div>
      </div>
    </Modal>
  )
}