import { useState, useEffect } from 'react'
import { useProjectStore } from '@/stores/projectStore'
import LoadingSpinner from '@/components/ui/LoadingSpinner'
import { analyzeScriptForScenes, SceneBreakdown } from '@/utils/autoBreakdownEngine'
import { 
  PlusIcon, 
  CalendarDaysIcon,
  MapPinIcon,
  ClockIcon,
  UserGroupIcon,
  FilmIcon,
  PencilIcon,
  TrashIcon,
  ChevronLeftIcon,
  ChevronRightIcon,
  SunIcon,
  MoonIcon
} from '@heroicons/react/24/outline'
import toast from 'react-hot-toast'

interface ShootDay {
  id: string
  date: Date
  location: string
  scenes: string[]
  estimatedHours: number
  weather: 'indoor' | 'outdoor' | 'both'
  notes: string
  status: 'planned' | 'in-progress' | 'completed'
}

interface StripboardScene {
  id: string
  sceneNumber: string
  location: string
  timeOfDay: 'day' | 'night' | 'dawn' | 'dusk'
  pageCount: number
  estimatedTime: number
  shootDay?: string
  characters: string[]
  props: string[]
  notes: string
}

export default function WorkingSchedulePage() {
  const { currentProject } = useProjectStore()
  const [, setScenes] = useState<SceneBreakdown[]>([])
  const [shootDays, setShootDays] = useState<ShootDay[]>([])
  const [stripboardScenes, setStripboardScenes] = useState<StripboardScene[]>([])
  const [isLoading, setIsLoading] = useState(true)
  const [selectedView, setSelectedView] = useState<'calendar' | 'stripboard'>('calendar')
  const [isAddDayModalOpen, setIsAddDayModalOpen] = useState(false)
  const [isStripboardEditOpen, setIsStripboardEditOpen] = useState(false)
  const [currentMonth, setCurrentMonth] = useState(new Date())
  
  const [newShootDay, setNewShootDay] = useState<{
    date: string
    location: string
    scenes: string[]
    estimatedHours: number
    weather: 'indoor' | 'outdoor' | 'both'
    notes: string
  }>({
    date: '',
    location: '',
    scenes: [],
    estimatedHours: 8,
    weather: 'indoor',
    notes: ''
  })

  useEffect(() => {
    if (currentProject?.scriptContent) {
      analyzeScript()
    } else {
      setIsLoading(false)
    }
    loadScheduleData()
  }, [currentProject])

  const analyzeScript = async () => {
    if (!currentProject?.scriptContent) return
    
    setIsLoading(true)
    try {
      await new Promise(resolve => setTimeout(resolve, 500))
      
      const scriptScenes = analyzeScriptForScenes(currentProject.scriptContent)
      setScenes(scriptScenes)
      
      // Convert script scenes to stripboard format
      const stripboard: StripboardScene[] = scriptScenes.map((scene, index) => ({
        id: `scene_${index}`,
        sceneNumber: scene.sceneNumber,
        location: scene.location.name,
        timeOfDay: scene.timeOfDay.toLowerCase().includes('night') ? 'night' : 'day',
        pageCount: Math.ceil(scene.estimatedDuration / 8), // Rough page estimate
        estimatedTime: scene.estimatedDuration,
        characters: scene.characters,
        props: [],
        notes: ''
      }))
      
      setStripboardScenes(stripboard)
      
      toast.success('🎬 Script analyzed for scheduling!', {
        style: { background: '#fed7aa', color: '#9a3412', fontWeight: '600' }
      })
    } catch (error) {
      console.error('Script analysis failed:', error)
      toast.error('Failed to analyze script')
    } finally {
      setIsLoading(false)
    }
  }

  const loadScheduleData = () => {
    try {
      const savedDays = localStorage.getItem(`schedule_${currentProject?.id}`)
      if (savedDays) {
        const parsed = JSON.parse(savedDays).map((day: any) => ({
          ...day,
          date: new Date(day.date)
        }))
        setShootDays(parsed)
      }
    } catch (error) {
      console.error('Failed to load schedule data:', error)
    }
  }

  const saveScheduleData = (days: ShootDay[]) => {
    if (!currentProject) return
    localStorage.setItem(`schedule_${currentProject.id}`, JSON.stringify(days))
  }

  const handleAddShootDay = () => {
    if (!newShootDay.date || !newShootDay.location) {
      toast.error('Date and location are required')
      return
    }

    const shootDay: ShootDay = {
      id: `day_${Date.now()}`,
      date: new Date(newShootDay.date),
      location: newShootDay.location,
      scenes: newShootDay.scenes,
      estimatedHours: newShootDay.estimatedHours,
      weather: newShootDay.weather,
      notes: newShootDay.notes,
      status: 'planned'
    }

    const updatedDays = [...shootDays, shootDay]
    setShootDays(updatedDays)
    saveScheduleData(updatedDays)
    
    setNewShootDay({
      date: '',
      location: '',
      scenes: [],
      estimatedHours: 8,
      weather: 'indoor',
      notes: ''
    })
    setIsAddDayModalOpen(false)
    
    toast.success('📅 Shoot day added successfully!', {
      style: { background: '#dcfce7', color: '#166534', fontWeight: '600' }
    })
  }

  const handleDeleteShootDay = (dayId: string) => {
    if (window.confirm('Are you sure you want to delete this shoot day?')) {
      const updatedDays = shootDays.filter(day => day.id !== dayId)
      setShootDays(updatedDays)
      saveScheduleData(updatedDays)
      toast.success('🗑️ Shoot day deleted')
    }
  }

  const handleStripboardEdit = () => {
    setIsStripboardEditOpen(true)
    toast.success('🎬 Stripboard editor opened!', {
      style: { background: '#fed7aa', color: '#9a3412', fontWeight: '600' }
    })
  }

  const assignSceneToDay = (sceneId: string, dayId: string) => {
    const updatedScenes = stripboardScenes.map(scene =>
      scene.id === sceneId ? { ...scene, shootDay: dayId } : scene
    )
    setStripboardScenes(updatedScenes)
    
    const updatedDays = shootDays.map(day =>
      day.id === dayId 
        ? { ...day, scenes: [...day.scenes, stripboardScenes.find(s => s.id === sceneId)?.sceneNumber || ''] }
        : day
    )
    setShootDays(updatedDays)
    saveScheduleData(updatedDays)
    
    toast.success('📋 Scene assigned to shoot day!')
  }

  // Calendar helper functions
  const getDaysInMonth = (date: Date) => {
    return new Date(date.getFullYear(), date.getMonth() + 1, 0).getDate()
  }

  const getFirstDayOfMonth = (date: Date) => {
    return new Date(date.getFullYear(), date.getMonth(), 1).getDay()
  }

  const getShootDaysForDate = (date: Date) => {
    return shootDays.filter(day => 
      day.date.toDateString() === date.toDateString()
    )
  }

  if (!currentProject) {
    return (
      <div className="flex items-center justify-center h-96">
        <LoadingSpinner size="lg" />
      </div>
    )
  }

  const daysInMonth = getDaysInMonth(currentMonth)
  const firstDay = getFirstDayOfMonth(currentMonth)
  const monthYear = currentMonth.toLocaleDateString('en-US', { month: 'long', year: 'numeric' })

  return (
    <div className="h-full flex flex-col" style={{ background: 'linear-gradient(135deg, #fff7ed 0%, #fed7aa 100%)' }}>
      {/* Orange Header */}
      <div className="bg-gradient-to-r from-orange-400 to-orange-600 shadow-lg">
        <div className="max-w-7xl mx-auto px-6">
          <div className="flex items-center justify-between h-16">
            <div className="flex items-center space-x-4">
              <div className="flex items-center space-x-3">
                <div className="w-10 h-10 bg-white rounded-xl flex items-center justify-center shadow-lg">
                  <CalendarDaysIcon className="h-6 w-6 text-orange-500" />
                </div>
                <div>
                  <h1 className="text-xl font-bold text-white drop-shadow-sm">Production Schedule</h1>
                  <p className="text-sm text-orange-100">{currentProject.title}</p>
                </div>
              </div>
            </div>

            <div className="flex items-center space-x-4">
              <div className="flex items-center bg-white/20 backdrop-blur-sm rounded-lg p-1">
                <button
                  onClick={() => setSelectedView('calendar')}
                  className={`px-4 py-2 rounded-md transition-all ${
                    selectedView === 'calendar' 
                      ? 'bg-white text-orange-600 shadow-sm' 
                      : 'text-white hover:bg-white/20'
                  }`}
                >
                  Calendar
                </button>
                <button
                  onClick={() => setSelectedView('stripboard')}
                  className={`px-4 py-2 rounded-md transition-all ${
                    selectedView === 'stripboard' 
                      ? 'bg-white text-orange-600 shadow-sm' 
                      : 'text-white hover:bg-white/20'
                  }`}
                >
                  Stripboard
                </button>
              </div>

              <button
                onClick={() => setIsAddDayModalOpen(true)}
                className="flex items-center px-4 py-2 bg-white text-orange-600 rounded-lg hover:bg-orange-50 transition-all font-medium"
              >
                <PlusIcon className="h-4 w-4 mr-2" />
                Add Shoot Day
              </button>
              
              <button
                onClick={handleStripboardEdit}
                className="flex items-center px-4 py-2 bg-white/20 backdrop-blur-sm text-white rounded-lg hover:bg-white/30 transition-all"
              >
                <PencilIcon className="h-4 w-4 mr-2" />
                Edit Stripboard
              </button>
            </div>
          </div>
        </div>
      </div>

      {/* Main Content */}
      <div className="flex-1 p-8 overflow-y-auto">
        {isLoading ? (
          <div className="flex items-center justify-center h-96">
            <div className="text-center">
              <LoadingSpinner size="lg" />
              <p className="mt-4 text-gray-600">Analyzing script for scheduling...</p>
            </div>
          </div>
        ) : selectedView === 'calendar' ? (
          // Calendar View
          <div className="bg-white rounded-2xl shadow-xl p-6">
            {/* Calendar Header */}
            <div className="flex items-center justify-between mb-6">
              <h2 className="text-2xl font-bold text-gray-900">{monthYear}</h2>
              <div className="flex items-center space-x-2">
                <button
                  onClick={() => setCurrentMonth(new Date(currentMonth.getFullYear(), currentMonth.getMonth() - 1))}
                  className="p-2 hover:bg-gray-100 rounded-lg"
                >
                  <ChevronLeftIcon className="h-5 w-5" />
                </button>
                <button
                  onClick={() => setCurrentMonth(new Date())}
                  className="px-4 py-2 bg-orange-100 text-orange-600 rounded-lg hover:bg-orange-200 transition-colors"
                >
                  Today
                </button>
                <button
                  onClick={() => setCurrentMonth(new Date(currentMonth.getFullYear(), currentMonth.getMonth() + 1))}
                  className="p-2 hover:bg-gray-100 rounded-lg"
                >
                  <ChevronRightIcon className="h-5 w-5" />
                </button>
              </div>
            </div>

            {/* Calendar Grid */}
            <div className="grid grid-cols-7 gap-1">
              {/* Day Headers */}
              {['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'].map(day => (
                <div key={day} className="p-3 text-center font-medium text-gray-600 bg-gray-50 rounded-lg">
                  {day}
                </div>
              ))}
              
              {/* Empty cells for days before month starts */}
              {Array.from({ length: firstDay }, (_, i) => (
                <div key={`empty-${i}`} className="p-3 h-24"></div>
              ))}
              
              {/* Calendar days */}
              {Array.from({ length: daysInMonth }, (_, i) => {
                const date = new Date(currentMonth.getFullYear(), currentMonth.getMonth(), i + 1)
                const shootDaysForDate = getShootDaysForDate(date)
                const isToday = date.toDateString() === new Date().toDateString()
                
                return (
                  <div key={i + 1} className={`p-2 h-24 border rounded-lg ${isToday ? 'bg-orange-100 border-orange-300' : 'bg-white border-gray-200'} hover:bg-gray-50`}>
                    <div className="text-sm font-medium text-gray-900 mb-1">{i + 1}</div>
                    <div className="space-y-1">
                      {shootDaysForDate.map(day => (
                        <div key={day.id} className="text-xs bg-orange-200 text-orange-800 px-2 py-1 rounded truncate">
                          {day.location}
                        </div>
                      ))}
                    </div>
                  </div>
                )
              })}
            </div>

            {/* Shoot Days List */}
            {shootDays.length > 0 && (
              <div className="mt-8">
                <h3 className="text-lg font-bold text-gray-900 mb-4">Upcoming Shoot Days</h3>
                <div className="space-y-3">
                  {shootDays.slice(0, 5).map(day => (
                    <div key={day.id} className="flex items-center justify-between p-4 bg-orange-50 rounded-xl border border-orange-200">
                      <div className="flex items-center space-x-4">
                        <div className="w-12 h-12 bg-orange-500 rounded-lg flex items-center justify-center">
                          <CalendarDaysIcon className="h-6 w-6 text-white" />
                        </div>
                        <div>
                          <div className="font-medium text-gray-900">{day.location}</div>
                          <div className="text-sm text-gray-600">
                            {day.date.toLocaleDateString()} • {day.estimatedHours} hours • {day.scenes.length} scenes
                          </div>
                        </div>
                      </div>
                      <div className="flex items-center space-x-2">
                        <button
                          onClick={() => handleDeleteShootDay(day.id)}
                          className="p-2 text-red-600 hover:bg-red-100 rounded-lg"
                        >
                          <TrashIcon className="h-4 w-4" />
                        </button>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>
        ) : (
          // Stripboard View
          <div className="bg-white rounded-2xl shadow-xl p-6">
            <div className="flex items-center justify-between mb-6">
              <h2 className="text-2xl font-bold text-gray-900">Scene Stripboard</h2>
              <div className="text-sm text-gray-600">
                {stripboardScenes.length} scenes • {stripboardScenes.reduce((total, scene) => total + scene.estimatedTime, 0)} minutes total
              </div>
            </div>

            {stripboardScenes.length > 0 ? (
              <div className="overflow-x-auto">
                <table className="w-full">
                  <thead>
                    <tr className="border-b border-gray-200">
                      <th className="text-left py-3 px-4 font-medium text-gray-900">Scene</th>
                      <th className="text-left py-3 px-4 font-medium text-gray-900">Location</th>
                      <th className="text-left py-3 px-4 font-medium text-gray-900">Time</th>
                      <th className="text-left py-3 px-4 font-medium text-gray-900">Duration</th>
                      <th className="text-left py-3 px-4 font-medium text-gray-900">Characters</th>
                      <th className="text-left py-3 px-4 font-medium text-gray-900">Shoot Day</th>
                      <th className="text-left py-3 px-4 font-medium text-gray-900">Actions</th>
                    </tr>
                  </thead>
                  <tbody>
                    {stripboardScenes.map(scene => (
                      <tr key={scene.id} className="border-b border-gray-100 hover:bg-gray-50">
                        <td className="py-3 px-4 font-medium">{scene.sceneNumber}</td>
                        <td className="py-3 px-4">
                          <div className="flex items-center">
                            <MapPinIcon className="h-4 w-4 mr-2 text-gray-400" />
                            {scene.location}
                          </div>
                        </td>
                        <td className="py-3 px-4">
                          <div className="flex items-center">
                            {scene.timeOfDay === 'day' ? (
                              <SunIcon className="h-4 w-4 mr-2 text-yellow-500" />
                            ) : (
                              <MoonIcon className="h-4 w-4 mr-2 text-blue-500" />
                            )}
                            {scene.timeOfDay}
                          </div>
                        </td>
                        <td className="py-3 px-4">
                          <div className="flex items-center">
                            <ClockIcon className="h-4 w-4 mr-2 text-gray-400" />
                            {scene.estimatedTime}m
                          </div>
                        </td>
                        <td className="py-3 px-4">
                          <div className="flex items-center">
                            <UserGroupIcon className="h-4 w-4 mr-2 text-gray-400" />
                            {scene.characters.length}
                          </div>
                        </td>
                        <td className="py-3 px-4">
                          {scene.shootDay ? (
                            <span className="px-2 py-1 bg-green-100 text-green-800 rounded-full text-xs">
                              Assigned
                            </span>
                          ) : (
                            <select
                              onChange={(e) => assignSceneToDay(scene.id, e.target.value)}
                              className="text-xs border rounded px-2 py-1"
                            >
                              <option value="">Assign to day...</option>
                              {shootDays.map(day => (
                                <option key={day.id} value={day.id}>
                                  {day.date.toLocaleDateString()} - {day.location}
                                </option>
                              ))}
                            </select>
                          )}
                        </td>
                        <td className="py-3 px-4">
                          <button className="p-1 text-gray-400 hover:text-gray-600">
                            <PencilIcon className="h-4 w-4" />
                          </button>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            ) : (
              <div className="text-center py-12">
                <FilmIcon className="h-16 w-16 text-gray-400 mx-auto mb-4" />
                <h3 className="text-lg font-medium text-gray-900 mb-2">No scenes found</h3>
                <p className="text-gray-600">Write your script to see scenes appear here for scheduling.</p>
              </div>
            )}
          </div>
        )}
      </div>

      {/* Add Shoot Day Modal */}
      {isAddDayModalOpen && (
        <div className="fixed inset-0 z-50 overflow-y-auto bg-black bg-opacity-50 flex items-center justify-center p-4">
          <div className="bg-white rounded-2xl max-w-md w-full p-6">
            <h3 className="text-lg font-bold text-gray-900 mb-4">Add New Shoot Day</h3>
            
            <div className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Date *</label>
                <input
                  type="date"
                  value={newShootDay.date}
                  onChange={(e) => setNewShootDay(prev => ({ ...prev, date: e.target.value }))}
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-orange-500 focus:border-transparent"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Location *</label>
                <input
                  type="text"
                  value={newShootDay.location}
                  onChange={(e) => setNewShootDay(prev => ({ ...prev, location: e.target.value }))}
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-orange-500 focus:border-transparent"
                  placeholder="e.g., Studio A, Central Park, etc."
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Estimated Hours</label>
                <input
                  type="number"
                  min="1"
                  max="24"
                  value={newShootDay.estimatedHours}
                  onChange={(e) => setNewShootDay(prev => ({ ...prev, estimatedHours: parseInt(e.target.value) }))}
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-orange-500 focus:border-transparent"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Weather Dependency</label>
                <select
                  value={newShootDay.weather}
                  onChange={(e) => setNewShootDay(prev => ({ ...prev, weather: e.target.value as 'indoor' | 'outdoor' | 'both' }))}
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-orange-500 focus:border-transparent"
                >
                  <option value="indoor">Indoor</option>
                  <option value="outdoor">Outdoor</option>
                  <option value="both">Both</option>
                </select>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Notes</label>
                <textarea
                  value={newShootDay.notes}
                  onChange={(e) => setNewShootDay(prev => ({ ...prev, notes: e.target.value }))}
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-orange-500 focus:border-transparent"
                  rows={3}
                  placeholder="Any special requirements or notes..."
                />
              </div>
            </div>

            <div className="flex justify-end space-x-3 mt-6">
              <button
                onClick={() => setIsAddDayModalOpen(false)}
                className="px-4 py-2 bg-gray-100 hover:bg-gray-200 text-gray-700 rounded-lg transition-colors"
              >
                Cancel
              </button>
              <button
                onClick={handleAddShootDay}
                className="px-4 py-2 bg-orange-500 hover:bg-orange-600 text-white rounded-lg transition-colors"
              >
                Add Shoot Day
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Stripboard Edit Modal */}
      {isStripboardEditOpen && (
        <div className="fixed inset-0 z-50 overflow-y-auto bg-black bg-opacity-50 flex items-center justify-center p-4">
          <div className="bg-white rounded-2xl max-w-4xl w-full p-6">
            <div className="flex items-center justify-between mb-6">
              <h3 className="text-lg font-bold text-gray-900">Stripboard Editor</h3>
              <button
                onClick={() => setIsStripboardEditOpen(false)}
                className="w-8 h-8 bg-gray-100 rounded-lg flex items-center justify-center hover:bg-gray-200 transition-colors"
              >
                ×
              </button>
            </div>
            
            <div className="text-center py-8">
              <FilmIcon className="h-16 w-16 text-gray-400 mx-auto mb-4" />
              <h4 className="text-lg font-medium text-gray-900 mb-2">Advanced Stripboard Editor</h4>
              <p className="text-gray-600 mb-4">
                Drag and drop scenes to reorder, group by location, and optimize shooting schedule.
              </p>
              <div className="space-y-2 text-sm text-gray-600">
                <p>• Reorder scenes for efficient shooting</p>
                <p>• Group scenes by location and cast</p>
                <p>• Optimize for budget and time constraints</p>
                <p>• Export shooting schedule and call sheets</p>
              </div>
            </div>
            
            <div className="flex justify-end">
              <button
                onClick={() => setIsStripboardEditOpen(false)}
                className="px-4 py-2 bg-orange-500 hover:bg-orange-600 text-white rounded-lg transition-colors"
              >
                Close Editor
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}