import { useState, useEffect } from 'react'
import { useProjectStore } from '@/stores/projectStore'
import { ScriptAnalyzer, DetectedElement } from '@/utils/scriptAnalyzer.ts'
import { 
  SparklesIcon, 
  CheckIcon, 
  XMarkIcon,
  ExclamationTriangleIcon,
  EyeIcon,
  PlusIcon
} from '@heroicons/react/24/outline'

interface AutoBreakdownEngineProps {
  isOpen: boolean
  onClose: () => void
  onElementsConfirmed: (elements: DetectedElement[]) => void
}

export default function AutoBreakdownEngine({ isOpen, onClose, onElementsConfirmed }: AutoBreakdownEngineProps) {
  const { currentProject } = useProjectStore()
  const [analyzer] = useState(() => new ScriptAnalyzer())
  const [detectedElements, setDetectedElements] = useState<DetectedElement[]>([])
  const [isAnalyzing, setIsAnalyzing] = useState(false)
  const [selectedElements, setSelectedElements] = useState<Set<string>>(new Set())
  const [selectedTab, setSelectedTab] = useState<'all' | 'high' | 'medium' | 'low'>('all')

  useEffect(() => {
    if (isOpen && currentProject?.scriptContent) {
      runAnalysis()
    }
  }, [isOpen, currentProject])

  const runAnalysis = async () => {
    if (!currentProject?.scriptContent) return
    
    setIsAnalyzing(true)
    
    try {
      // Simulate analysis time for UX
      await new Promise(resolve => setTimeout(resolve, 1500))
      
      const analysis = analyzer.analyzeScript(currentProject.scriptContent)
      setDetectedElements(analysis.elements)
      
      // Auto-select high confidence elements
      const autoSelected = new Set(
        analysis.elements
          .filter(e => e.confidence > 0.8)
          .map(e => e.id)
      )
      setSelectedElements(autoSelected)
      
    } catch (error) {
      console.error('Analysis failed:', error)
    } finally {
      setIsAnalyzing(false)
    }
  }

  const toggleElement = (elementId: string) => {
    const newSelected = new Set(selectedElements)
    if (newSelected.has(elementId)) {
      newSelected.delete(elementId)
    } else {
      newSelected.add(elementId)
    }
    setSelectedElements(newSelected)
  }

  const getConfidenceColor = (confidence: number) => {
    if (confidence >= 0.8) return 'text-green-600 bg-green-50 border-green-200'
    if (confidence >= 0.6) return 'text-yellow-600 bg-yellow-50 border-yellow-200'
    return 'text-red-600 bg-red-50 border-red-200'
  }

  const getConfidenceLabel = (confidence: number) => {
    if (confidence >= 0.8) return 'High'
    if (confidence >= 0.6) return 'Medium'
    return 'Low'
  }

  const filteredElements = detectedElements.filter(element => {
    switch (selectedTab) {
      case 'high': return element.confidence >= 0.8
      case 'medium': return element.confidence >= 0.6 && element.confidence < 0.8
      case 'low': return element.confidence < 0.6
      default: return true
    }
  })

  const confirmSelection = () => {
    const confirmedElements = detectedElements.filter(e => selectedElements.has(e.id))
    onElementsConfirmed(confirmedElements)
    onClose()
  }

  if (!isOpen) return null

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
      <div className="bg-white rounded-lg w-full max-w-4xl max-h-[90vh] overflow-hidden">
        {/* Header */}
        <div className="flex items-center justify-between p-6 border-b border-gray-200">
          <div className="flex items-center">
            <SparklesIcon className="h-6 w-6 text-primary-500 mr-2" />
            <div>
              <h3 className="text-lg font-semibold text-gray-900">AI Script Analysis</h3>
              <p className="text-sm text-gray-500">
                Automatically detected {detectedElements.length} elements from your script
              </p>
            </div>
          </div>
          <button
            onClick={onClose}
            className="text-gray-400 hover:text-gray-600"
          >
            <XMarkIcon className="h-6 w-6" />
          </button>
        </div>

        {isAnalyzing ? (
          // Loading State
          <div className="flex flex-col items-center justify-center py-12">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-500 mb-4"></div>
            <h3 className="text-lg font-medium text-gray-900 mb-2">Analyzing Script...</h3>
            <p className="text-sm text-gray-500 text-center max-w-md">
              Our AI is reading through your script and identifying props, locations, 
              characters, wardrobe, and other production elements.
            </p>
          </div>
        ) : (
          <>
            {/* Confidence Filter Tabs */}
            <div className="border-b border-gray-200">
              <nav className="flex space-x-8 px-6">
                {[
                  { key: 'all', label: 'All Elements', count: detectedElements.length },
                  { key: 'high', label: 'High Confidence', count: detectedElements.filter(e => e.confidence >= 0.8).length },
                  { key: 'medium', label: 'Medium Confidence', count: detectedElements.filter(e => e.confidence >= 0.6 && e.confidence < 0.8).length },
                  { key: 'low', label: 'Low Confidence', count: detectedElements.filter(e => e.confidence < 0.6).length },
                ].map((tab) => (
                  <button
                    key={tab.key}
                    onClick={() => setSelectedTab(tab.key as any)}
                    className={`py-3 px-1 border-b-2 font-medium text-sm ${
                      selectedTab === tab.key
                        ? 'border-primary-500 text-primary-600'
                        : 'border-transparent text-gray-500 hover:text-gray-700'
                    }`}
                  >
                    {tab.label} ({tab.count})
                  </button>
                ))}
              </nav>
            </div>

            {/* Elements List */}
            <div className="p-6 max-h-96 overflow-y-auto">
              <div className="space-y-3">
                {filteredElements.map((element) => (
                  <div
                    key={element.id}
                    className={`border rounded-lg p-4 transition-all ${
                      selectedElements.has(element.id)
                        ? 'border-primary-500 bg-primary-50'
                        : 'border-gray-200 hover:border-gray-300'
                    }`}
                  >
                    <div className="flex items-start space-x-3">
                      {/* Checkbox */}
                      <button
                        onClick={() => toggleElement(element.id)}
                        className={`flex-shrink-0 w-5 h-5 rounded border-2 flex items-center justify-center ${
                          selectedElements.has(element.id)
                            ? 'bg-primary-500 border-primary-500'
                            : 'border-gray-300 hover:border-primary-400'
                        }`}
                      >
                        {selectedElements.has(element.id) && (
                          <CheckIcon className="h-3 w-3 text-white" />
                        )}
                      </button>

                      {/* Element Details */}
                      <div className="flex-1 min-w-0">
                        <div className="flex items-center space-x-2 mb-1">
                          <h4 className="font-medium text-gray-900">{element.name}</h4>
                          <span className="inline-flex px-2 py-1 text-xs font-medium bg-gray-100 text-gray-700 rounded-full">
                            {element.type}
                          </span>
                          <span className={`inline-flex px-2 py-1 text-xs font-medium rounded-full border ${getConfidenceColor(element.confidence)}`}>
                            {getConfidenceLabel(element.confidence)} ({Math.round(element.confidence * 100)}%)
                          </span>
                          {element.autoTagged && (
                            <span className="inline-flex items-center px-2 py-1 text-xs font-medium bg-blue-100 text-blue-700 rounded-full">
                              <SparklesIcon className="h-3 w-3 mr-1" />
                              Auto
                            </span>
                          )}
                        </div>

                        <p className="text-sm text-gray-600 mb-2 truncate">
                          <strong>Context:</strong> "{element.context.length > 100 
                            ? element.context.substring(0, 100) + '...' 
                            : element.context}"
                        </p>

                        <div className="flex items-center justify-between">
                          <div className="flex items-center text-xs text-gray-500 space-x-3">
                            <span>Scenes: {element.scenes.length}</span>
                            {element.confidence < 0.7 && (
                              <span className="flex items-center text-yellow-600">
                                <ExclamationTriangleIcon className="h-3 w-3 mr-1" />
                                Review needed
                              </span>
                            )}
                          </div>
                          
                          <button className="text-xs text-primary-600 hover:text-primary-700 flex items-center">
                            <EyeIcon className="h-3 w-3 mr-1" />
                            View in script
                          </button>
                        </div>

                        {/* Suggestions */}
                        {element.suggestions.length > 0 && (
                          <div className="mt-2 pt-2 border-t border-gray-100">
                            <p className="text-xs text-gray-500 mb-1">Suggestions:</p>
                            <div className="flex flex-wrap gap-1">
                              {element.suggestions.map((suggestion, idx) => (
                                <span
                                  key={idx}
                                  className="inline-flex px-2 py-1 text-xs bg-gray-100 text-gray-600 rounded cursor-pointer hover:bg-gray-200"
                                  onClick={() => {
                                    // Replace element name with suggestion
                                    const updatedElements = detectedElements.map(e =>
                                      e.id === element.id ? { ...e, name: suggestion } : e
                                    )
                                    setDetectedElements(updatedElements)
                                  }}
                                >
                                  {suggestion}
                                </span>
                              ))}
                            </div>
                          </div>
                        )}
                      </div>
                    </div>
                  </div>
                ))}
              </div>

              {filteredElements.length === 0 && (
                <div className="text-center py-8">
                  <SparklesIcon className="h-12 w-12 text-gray-300 mx-auto mb-4" />
                  <h3 className="text-sm font-medium text-gray-900 mb-2">
                    No elements found in this category
                  </h3>
                  <p className="text-sm text-gray-500">
                    Try a different confidence level or check your script content.
                  </p>
                </div>
              )}
            </div>

            {/* Footer */}
            <div className="border-t border-gray-200 p-6">
              <div className="flex items-center justify-between">
                <div className="text-sm text-gray-500">
                  {selectedElements.size} of {detectedElements.length} elements selected
                  {selectedElements.size > 0 && (
                    <span className="text-primary-600 ml-2">
                      → Will be added to breakdown
                    </span>
                  )}
                </div>
                
                <div className="flex space-x-3">
                  <button
                    onClick={onClose}
                    className="px-4 py-2 text-sm font-medium text-gray-700 bg-white border border-gray-300 rounded-md hover:bg-gray-50"
                  >
                    Cancel
                  </button>
                  
                  <button
                    onClick={confirmSelection}
                    disabled={selectedElements.size === 0}
                    className="px-4 py-2 text-sm font-medium text-white bg-primary-600 border border-transparent rounded-md hover:bg-primary-700 disabled:opacity-50 disabled:cursor-not-allowed flex items-center"
                  >
                    <PlusIcon className="h-4 w-4 mr-2" />
                    Add {selectedElements.size} Elements
                  </button>
                </div>
              </div>
            </div>
          </>
        )}
      </div>
    </div>
  )
}