import { useState, useRef } from 'react'
import { 
  DocumentArrowUpIcon, 
  XMarkIcon 
} from '@heroicons/react/24/outline'

interface ScriptImporterProps {
  isOpen: boolean
  onClose: () => void
  onImport: (content: string) => void
}

export default function ScriptImporter({ isOpen, onClose, onImport }: ScriptImporterProps) {
  const [isLoading, setIsLoading] = useState(false)
  const [error, setError] = useState('')
  const fileInputRef = useRef<HTMLInputElement>(null)

  const handleFileSelect = async (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0]
    if (!file) return

    setIsLoading(true)
    setError('')

    try {
      const fileExtension = file.name.split('.').pop()?.toLowerCase()
      let content = ''

      if (fileExtension === 'txt' || fileExtension === 'fountain') {
        // Plain text or Fountain format
        content = await file.text()
      } else if (fileExtension === 'pdf') {
        // PDF import with better handling
        try {
          const arrayBuffer = await file.arrayBuffer()
          content = await extractTextFromPDF(arrayBuffer)
          if (!content.trim()) {
            setError('No text found in PDF. Please ensure the PDF contains selectable text, not scanned images.')
            setIsLoading(false)
            return
          }
        } catch (pdfError) {
          setError('PDF import requires text-based PDFs. For scanned PDFs, please export as text first.')
          setIsLoading(false)
          return
        }
      } else if (fileExtension === 'fdx') {
        // Final Draft format - basic XML parsing
        const text = await file.text()
        content = extractFromFDX(text)
      } else {
        // Try to read as text
        content = await file.text()
      }

      // Basic cleanup and formatting
      content = cleanupImportedScript(content)
      
      onImport(content)
      onClose()
    } catch (err) {
      setError('Failed to import file. Please check the format and try again.')
      console.error('Import error:', err)
    } finally {
      setIsLoading(false)
    }
  }

  const extractTextFromPDF = async (arrayBuffer: ArrayBuffer): Promise<string> => {
    // Simple PDF text extraction using basic pattern matching
    // This is a lightweight approach for basic PDFs
    try {
      const uint8Array = new Uint8Array(arrayBuffer)
      const decoder = new TextDecoder('latin1')
      const pdfText = decoder.decode(uint8Array)
      
      // Extract text between BT (begin text) and ET (end text) markers
      const textMatches = pdfText.match(/BT\s+(.*?)\s+ET/gs)
      if (!textMatches) {
        throw new Error('No text objects found')
      }
      
      let extractedText = ''
      textMatches.forEach(match => {
        // Extract text from PDF text objects (very basic)
        const textContent = match.match(/\((.*?)\)\s*Tj/g)
        if (textContent) {
          textContent.forEach(text => {
            const cleanText = text.replace(/\((.*?)\)\s*Tj/, '$1')
            extractedText += cleanText + '\n'
          })
        }
      })
      
      return extractedText.trim()
    } catch (error) {
      throw new Error('Failed to extract text from PDF')
    }
  }

  const extractFromFDX = (xmlContent: string): string => {
    // Basic FDX parsing - extract text content from XML tags
    try {
      // Remove XML tags and extract text content
      let content = xmlContent
        .replace(/<[^>]+>/g, '\n') // Replace all XML tags with newlines
        .replace(/&quot;/g, '"')
        .replace(/&apos;/g, "'")
        .replace(/&lt;/g, '<')
        .replace(/&gt;/g, '>')
        .replace(/&amp;/g, '&')
        .split('\n')
        .map(line => line.trim())
        .filter(line => line.length > 0)
        .join('\n')
      
      return content
    } catch (err) {
      throw new Error('Invalid FDX format')
    }
  }

  const cleanupImportedScript = (content: string): string => {
    // Basic cleanup of imported scripts
    return content
      .split('\n')
      .map(line => line.trim())
      .filter(line => line.length > 0 || content.includes('\n\n')) // Keep structure
      .join('\n')
      .replace(/\n{3,}/g, '\n\n') // Max 2 consecutive newlines
  }

  if (!isOpen) return null

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
      <div className="bg-white rounded-lg p-6 w-full max-w-md">
        <div className="flex justify-between items-center mb-4">
          <h3 className="text-lg font-semibold text-gray-900">Import Script</h3>
          <button
            onClick={onClose}
            className="text-gray-400 hover:text-gray-600"
          >
            <XMarkIcon className="h-6 w-6" />
          </button>
        </div>

        <div className="mb-6">
          <p className="text-sm text-gray-600 mb-4">
            Import scripts from various formats. Supported formats:
          </p>
          <ul className="text-sm text-gray-500 space-y-1 mb-4">
            <li>• <strong>TXT/Fountain:</strong> Plain text and Fountain markup</li>
            <li>• <strong>FDX:</strong> Final Draft XML format</li>
            <li>• <strong>PDF:</strong> Text extraction (text-based PDFs only)</li>
          </ul>
        </div>

        {error && (
          <div className="mb-4 p-3 bg-red-50 border border-red-200 rounded-md">
            <p className="text-sm text-red-600">{error}</p>
          </div>
        )}

        <div className="space-y-4">
          <input
            ref={fileInputRef}
            type="file"
            accept=".txt,.fountain,.fdx,.pdf"
            onChange={handleFileSelect}
            className="hidden"
          />
          
          <button
            onClick={() => fileInputRef.current?.click()}
            disabled={isLoading}
            className="w-full flex items-center justify-center px-4 py-3 border-2 border-dashed border-gray-300 rounded-md hover:border-primary-400 hover:bg-primary-50 transition-colors"
          >
            <DocumentArrowUpIcon className="h-8 w-8 text-gray-400 mr-3" />
            <div className="text-center">
              <p className="text-sm font-medium text-gray-900">
                {isLoading ? 'Processing...' : 'Choose file to import'}
              </p>
              <p className="text-xs text-gray-500">
                TXT, Fountain, FDX, PDF formats supported
              </p>
            </div>
          </button>
        </div>

        <div className="flex justify-end space-x-2 mt-6">
          <button
            onClick={onClose}
            className="px-4 py-2 text-sm font-medium text-gray-700 bg-white border border-gray-300 rounded-md hover:bg-gray-50"
          >
            Cancel
          </button>
        </div>
      </div>
    </div>
  )
}