export const getTemplateById = (id: string): ScriptTemplate | undefined => {
  return SCRIPT_TEMPLATES.find(template => template.id === id)
}

export const getTemplatesByCategory = (category: ScriptTemplate['category']): ScriptTemplate[] => {
  return SCRIPT_TEMPLATES.filter(template => template.category === category)
}

export const searchTemplates = (query: string): ScriptTemplate[] => {
  const lowerQuery = query.toLowerCase()
  return SCRIPT_TEMPLATES.filter(template => 
    template.name.toLowerCase().includes(lowerQuery) ||
    template.description.toLowerCase().includes(lowerQuery) ||
    template.tags.some(tag => tag.toLowerCase().includes(lowerQuery))
  )
}