// Advanced Script Analysis and Auto-Tagging System

export interface DetectedElement {
  id: string
  type: 'cast' | 'props' | 'wardrobe' | 'makeup' | 'sfx' | 'location' | 'vehicle' | 'animal' | 'music' | 'other'
  name: string
  scenes: string[]
  confidence: number // 0-1 confidence score
  context: string // The line/context where it was found
  suggestions: string[] // Alternative interpretations
  autoTagged: boolean
}

export interface ScriptAnalysisResult {
  elements: DetectedElement[]
  characters: string[]
  locations: string[]
  timeOfDayCount: Record<string, number>
  sceneCount: number
  estimatedPages: number
  estimatedRuntime: number
  genres: string[]
  themes: string[]
}

// Common patterns for element detection
const ELEMENT_PATTERNS = {
  // Props - common objects mentioned in action lines
  PROPS: [
    /\b(gun|pistol|rifle|weapon|knife|sword|blade)\b/gi,
    /\b(phone|cellphone|smartphone|telephone)\b/gi,
    /\b(car keys|keys|wallet|purse|bag|briefcase)\b/gi,
    /\b(badge|id|identification|license)\b/gi,
    /\b(laptop|computer|tablet|notebook|notepad)\b/gi,
    /\b(glass|cup|bottle|drink|beer|wine|coffee)\b/gi,
    /\b(document|file|folder|paper|letter|note)\b/gi,
    /\b(camera|photo|picture|photograph)\b/gi,
    /\b(book|magazine|newspaper|journal)\b/gi,
    /\b(jewelry|ring|necklace|watch|bracelet)\b/gi,
  ],
  
  // Wardrobe - clothing and accessories
  WARDROBE: [
    /\b(suit|dress|shirt|pants|jeans|jacket|coat)\b/gi,
    /\b(uniform|costume|outfit|attire)\b/gi,
    /\b(hat|cap|helmet|glasses|sunglasses)\b/gi,
    /\b(shoes|boots|sneakers|heels)\b/gi,
    /\b(gloves|scarf|tie|bow tie)\b/gi,
  ],
  
  // Vehicles
  VEHICLES: [
    /\b(car|automobile|sedan|suv|truck|van)\b/gi,
    /\b(motorcycle|bike|bicycle|scooter)\b/gi,
    /\b(bus|taxi|cab|limousine|limo)\b/gi,
    /\b(plane|airplane|helicopter|jet)\b/gi,
    /\b(boat|ship|yacht|ferry)\b/gi,
    /\b(train|subway|tram)\b/gi,
  ],
  
  // SFX - Special effects and sounds
  SFX: [
    /\b(explosion|blast|boom|bang)\b/gi,
    /\b(gunshot|shot|fire|firing)\b/gi,
    /\b(crash|smash|break|shatter)\b/gi,
    /\b(thunder|lightning|storm|rain)\b/gi,
    /\b(siren|alarm|bell|buzzer)\b/gi,
    /\b(music|song|singing|melody)\b/gi,
  ],
  
  // Animals
  ANIMALS: [
    /\b(dog|puppy|cat|kitten|pet)\b/gi,
    /\b(horse|stallion|mare)\b/gi,
    /\b(bird|eagle|crow|pigeon)\b/gi,
    /\b(snake|spider|rat|mouse)\b/gi,
    /\b(lion|tiger|bear|wolf)\b/gi,
  ],
  
  // Makeup/Special makeup
  MAKEUP: [
    /\b(blood|wound|scar|bruise|injury)\b/gi,
    /\b(makeup|cosmetics|lipstick|mascara)\b/gi,
    /\b(prosthetic|mask|disguise)\b/gi,
    /\b(tattoo|piercing)\b/gi,
  ],
}

// Location patterns
const LOCATION_PATTERNS = [
  /\b(kitchen|bedroom|bathroom|living room|dining room)\b/gi,
  /\b(office|building|warehouse|factory|store|shop)\b/gi,
  /\b(hospital|school|church|restaurant|bar|cafe)\b/gi,
  /\b(park|street|road|highway|bridge)\b/gi,
  /\b(airport|station|terminal)\b/gi,
  /\b(beach|forest|mountain|desert|lake|river)\b/gi,
]

// Character name patterns (all caps in dialogue context)
const CHARACTER_PATTERNS = /^[A-Z][A-Z\s]+(\([^)]*\))?$/

// Time of day extraction
const TIME_PATTERNS = /\b(DAY|NIGHT|DAWN|DUSK|MORNING|AFTERNOON|EVENING|LATER|CONTINUOUS)\b/gi

// Genre indicators
const GENRE_INDICATORS = {
  ACTION: ['fight', 'chase', 'explosion', 'gun', 'weapon', 'battle'],
  COMEDY: ['laugh', 'joke', 'funny', 'humor', 'silly', 'ridiculous'],
  HORROR: ['scream', 'blood', 'monster', 'terror', 'fear', 'scary'],
  ROMANCE: ['love', 'kiss', 'heart', 'romantic', 'wedding', 'date'],
  THRILLER: ['suspense', 'mystery', 'dangerous', 'chase', 'tension'],
  DRAMA: ['emotion', 'tear', 'family', 'relationship', 'conflict'],
}

export class ScriptAnalyzer {
  private generateId(): string {
    return Math.random().toString(36).substr(2, 9)
  }

  analyzeScript(scriptContent: string): ScriptAnalysisResult {
    const lines = scriptContent.split('\n')
    const elements: DetectedElement[] = []
    const characters: Set<string> = new Set()
    const locations: Set<string> = new Set()
    const timeOfDayCount: Record<string, number> = {}
    const genres: Set<string> = new Set()
    const themes: Set<string> = new Set()

    let currentScene = ''
    let sceneCount = 0

    // Analyze each line
    lines.forEach((line, lineIndex) => {
      const trimmedLine = line.trim()
      if (!trimmedLine) return

      // Detect scene headings
      if (this.isSceneHeading(trimmedLine)) {
        sceneCount++
        currentScene = `Scene_${sceneCount}`
        
        // Extract location from scene heading
        const location = this.extractLocationFromSceneHeading(trimmedLine)
        if (location) {
          locations.add(location)
        }

        // Extract time of day
        const timeMatch = trimmedLine.match(TIME_PATTERNS)
        if (timeMatch) {
          const time = timeMatch[0].toUpperCase()
          timeOfDayCount[time] = (timeOfDayCount[time] || 0) + 1
        }
      }

      // Detect character names
      else if (this.isCharacterName(trimmedLine)) {
        const characterName = trimmedLine.replace(/\([^)]*\)/g, '').trim()
        characters.add(characterName)
      }

      // Analyze action lines for elements
      else if (this.isActionLine(trimmedLine)) {
        const detectedElements = this.detectElementsInLine(trimmedLine, currentScene, lineIndex)
        elements.push(...detectedElements)

        // Genre detection
        this.detectGenreIndicators(trimmedLine, genres)
      }
    })

    // Estimate pages (industry standard: ~1 minute per page)
    const estimatedPages = Math.max(1, Math.ceil(scriptContent.length / 2500))
    
    // Estimate runtime based on format patterns
    const estimatedRuntime = this.estimateRuntime(scriptContent, estimatedPages)

    return {
      elements: this.deduplicateElements(elements),
      characters: Array.from(characters),
      locations: Array.from(locations),
      timeOfDayCount,
      sceneCount,
      estimatedPages,
      estimatedRuntime,
      genres: Array.from(genres),
      themes: Array.from(themes)
    }
  }

  private isSceneHeading(line: string): boolean {
    return /^(INT|EXT|INT\/EXT|INTERIOR|EXTERIOR)[\.\s]+/i.test(line)
  }

  private isCharacterName(line: string): boolean {
    return CHARACTER_PATTERNS.test(line) && line.length < 30
  }

  private isActionLine(line: string): boolean {
    return !this.isSceneHeading(line) && 
           !this.isCharacterName(line) && 
           !line.match(/^\([^)]+\)$/) && // Not parenthetical
           !line.match(/^(FADE|CUT|DISSOLVE)/i) // Not transition
  }

  private extractLocationFromSceneHeading(line: string): string | null {
    const match = line.match(/^(INT|EXT|INT\/EXT)[\.\s]+([^-]+)/i)
    return match ? match[2].trim() : null
  }

  private detectElementsInLine(line: string, scene: string, _lineIndex: number): DetectedElement[] {
    const elements: DetectedElement[] = []

    // Check each element type
    Object.entries(ELEMENT_PATTERNS).forEach(([type, patterns]) => {
      patterns.forEach(pattern => {
        const matches = line.match(pattern)
        if (matches) {
          matches.forEach(match => {
            elements.push({
              id: this.generateId(),
              type: type.toLowerCase() as any,
              name: this.capitalizeWords(match.toLowerCase()),
              scenes: [scene],
              confidence: this.calculateConfidence(match, line, type),
              context: line,
              suggestions: this.generateSuggestions(match, type),
              autoTagged: true
            })
          })
        }
      })
    })

    // Additional location detection
    LOCATION_PATTERNS.forEach(pattern => {
      const matches = line.match(pattern)
      if (matches) {
        matches.forEach(match => {
          elements.push({
            id: this.generateId(),
            type: 'location',
            name: this.capitalizeWords(match.toLowerCase()),
            scenes: [scene],
            confidence: 0.7,
            context: line,
            suggestions: [],
            autoTagged: true
          })
        })
      }
    })

    return elements
  }

  private calculateConfidence(match: string, context: string, type: string): number {
    let confidence = 0.6 // Base confidence

    // Increase confidence based on context
    if (context.toLowerCase().includes('pick up') || context.toLowerCase().includes('grab')) {
      confidence += 0.2
    }
    
    if (context.toLowerCase().includes('uses') || context.toLowerCase().includes('holds')) {
      confidence += 0.1
    }

    // Specific type adjustments
    if (type === 'PROPS' && (match.toLowerCase().includes('gun') || match.toLowerCase().includes('weapon'))) {
      confidence += 0.2
    }

    return Math.min(confidence, 0.95)
  }

  private generateSuggestions(match: string, type: string): string[] {
    const suggestions: string[] = []
    
    // Add common variations
    if (type === 'PROPS') {
      if (match.toLowerCase().includes('gun')) {
        suggestions.push('Pistol', 'Rifle', 'Revolver')
      }
      if (match.toLowerCase().includes('phone')) {
        suggestions.push('Smartphone', 'Cell Phone', 'iPhone')
      }
    }

    return suggestions
  }

  private detectGenreIndicators(line: string, genres: Set<string>) {
    const lowerLine = line.toLowerCase()
    
    Object.entries(GENRE_INDICATORS).forEach(([genre, indicators]) => {
      if (indicators.some(indicator => lowerLine.includes(indicator))) {
        genres.add(genre)
      }
    })
  }

  private estimateRuntime(content: string, pages: number): number {
    // Industry standard: ~1 minute per page for screenplays
    // Adjust based on dialogue vs action ratio
    const dialogueLines = content.split('\n').filter(line => 
      this.isCharacterName(line.trim()) || 
      (!this.isSceneHeading(line.trim()) && !this.isActionLine(line.trim()) && line.trim())
    ).length

    const totalLines = content.split('\n').filter(line => line.trim()).length
    const dialogueRatio = totalLines > 0 ? dialogueLines / totalLines : 0.5

    // More dialogue = potentially longer runtime
    let runtime = pages
    if (dialogueRatio > 0.6) runtime *= 1.2
    if (dialogueRatio < 0.3) runtime *= 0.8

    return Math.round(runtime)
  }

  private deduplicateElements(elements: DetectedElement[]): DetectedElement[] {
    const elementMap = new Map<string, DetectedElement>()

    elements.forEach(element => {
      const key = `${element.type}-${element.name.toLowerCase()}`
      
      if (elementMap.has(key)) {
        const existing = elementMap.get(key)!
        // Merge scenes and use higher confidence
        existing.scenes = [...new Set([...existing.scenes, ...element.scenes])]
        existing.confidence = Math.max(existing.confidence, element.confidence)
        // Combine contexts
        if (existing.context !== element.context) {
          existing.context += ` | ${element.context}`
        }
      } else {
        elementMap.set(key, element)
      }
    })

    return Array.from(elementMap.values())
  }

  private capitalizeWords(str: string): string {
    return str.replace(/\b\w/g, char => char.toUpperCase())
  }

  // Auto-fill metadata analysis
  analyzeMetadata(scriptContent: string): {
    suggestedGenre: string[]
    suggestedTimeOfDay: string
    suggestedTone: string
    suggestedBudgetLevel: 'low' | 'medium' | 'high'
  } {
    const analysis = this.analyzeScript(scriptContent)
    
    // Determine budget level based on elements detected
    let budgetLevel: 'low' | 'medium' | 'high' = 'low'
    const expensiveElements = analysis.elements.filter(e => 
      e.type === 'vehicle' || e.type === 'sfx' || 
      e.name.toLowerCase().includes('explosion') ||
      e.name.toLowerCase().includes('helicopter')
    )
    
    if (expensiveElements.length > 5) budgetLevel = 'high'
    else if (expensiveElements.length > 2) budgetLevel = 'medium'

    // Most common time of day
    const timeEntries = Object.entries(analysis.timeOfDayCount)
    const suggestedTimeOfDay = timeEntries.length > 0 
      ? timeEntries.reduce((a, b) => a[1] > b[1] ? a : b)[0]
      : 'DAY'

    // Suggested tone based on content
    let suggestedTone = 'Drama'
    if (analysis.genres.includes('ACTION')) suggestedTone = 'Action-packed'
    if (analysis.genres.includes('COMEDY')) suggestedTone = 'Light-hearted'
    if (analysis.genres.includes('HORROR')) suggestedTone = 'Dark'

    return {
      suggestedGenre: analysis.genres,
      suggestedTimeOfDay,
      suggestedTone,
      suggestedBudgetLevel: budgetLevel
    }
  }
}