import { useState, useEffect } from 'react'
import { useProjectStore } from '@/stores/projectStore'
import LoadingSpinner from '@/components/ui/LoadingSpinner'
import ResponsiveLayout from '@/components/ui/ResponsiveLayout'
import AutoBreakdownEngine from '@/components/breakdown/AutoBreakdownEngine'
import { analyzeScriptForElements, analyzeScriptForScenes, BreakdownElement, SceneBreakdown } from '@/utils/autoBreakdownEngine'
import { 
  PlusIcon, 
  TagIcon, 
  UserIcon,
  BuildingOfficeIcon,
  WrenchScrewdriverIcon,
  SwatchIcon,
  MusicalNoteIcon,
  SparklesIcon,
  DocumentTextIcon,
  FilmIcon,
  TrashIcon,
  PencilIcon
} from '@heroicons/react/24/outline'
import toast from 'react-hot-toast'

const elementTypes = [
  { type: 'cast', label: 'Cast', icon: UserIcon, color: 'bg-red-500' },
  { type: 'props', label: 'Props', icon: WrenchScrewdriverIcon, color: 'bg-orange-500' },
  { type: 'wardrobe', label: 'Wardrobe', icon: SwatchIcon, color: 'bg-yellow-500' },
  { type: 'location', label: 'Location', icon: BuildingOfficeIcon, color: 'bg-cyan-500' },
  { type: 'music', label: 'Music/SFX', icon: MusicalNoteIcon, color: 'bg-pink-500' },
  { type: 'other', label: 'Other', icon: TagIcon, color: 'bg-gray-500' }
] as const

export default function RealBreakdownPage() {
  const { currentProject } = useProjectStore()
  const [selectedType, setSelectedType] = useState<string | null>(null)
  const [isAutoBreakdownOpen, setIsAutoBreakdownOpen] = useState(false)
  const [elements, setElements] = useState<BreakdownElement[]>([])
  const [scenes, setScenes] = useState<SceneBreakdown[]>([])
  const [isLoading, setIsLoading] = useState(true)
  const [isAddElementModalOpen, setIsAddElementModalOpen] = useState(false)
  const [newElement, setNewElement] = useState({
    name: '',
    type: 'props' as BreakdownElement['type'],
    category: '',
    notes: ''
  })

  useEffect(() => {
    if (currentProject?.scriptContent) {
      analyzeScript()
    } else {
      setIsLoading(false)
    }
  }, [currentProject])

  const analyzeScript = async () => {
    if (!currentProject?.scriptContent) return
    
    setIsLoading(true)
    try {
      // Add a small delay to show loading state
      await new Promise(resolve => setTimeout(resolve, 500))
      
      const scriptElements = analyzeScriptForElements(currentProject.scriptContent)
      const scriptScenes = analyzeScriptForScenes(currentProject.scriptContent)
      
      setElements(scriptElements)
      setScenes(scriptScenes)
      
      toast.success('Script analyzed successfully!', {
        icon: '🔍',
        duration: 2000,
        style: {
          background: '#fff7ed',
          color: '#9a3412',
          border: '1px solid #fed7aa'
        }
      })
    } catch (error) {
      console.error('Script analysis failed:', error)
      toast.error('Failed to analyze script')
      setElements([])
      setScenes([])
    } finally {
      setIsLoading(false)
    }
  }

  const handleElementsConfirmed = (confirmedElements: any[]) => {
    // Convert from AutoBreakdownEngine format to our format
    const convertedElements: BreakdownElement[] = confirmedElements.map(elem => ({
      id: elem.id,
      type: elem.type,
      name: elem.name,
      category: elem.category || 'General',
      scenes: elem.scenes || [],
      firstAppearance: elem.firstAppearance || 1,
      totalAppearances: elem.totalAppearances || 1,
      confidence: elem.confidence || 0.8,
      isAutoDetected: true,
      tags: elem.tags || [],
      notes: elem.notes
    }))
    
    setElements(prev => [...prev, ...convertedElements])
    toast.success(`Added ${confirmedElements.length} elements to breakdown`)
  }

  const handleAddElement = () => {
    if (!newElement.name.trim()) {
      toast.error('Element name is required')
      return
    }

    const element: BreakdownElement = {
      id: `manual_${Date.now()}`,
      type: newElement.type,
      name: newElement.name.trim(),
      category: newElement.category.trim() || 'Manual Entry',
      scenes: [], // Will be populated when user assigns scenes
      firstAppearance: scenes.length > 0 ? 1 : 0,
      totalAppearances: 0,
      confidence: 1.0,
      isAutoDetected: false,
      tags: [],
      notes: newElement.notes.trim()
    }

    setElements(prev => [...prev, element])
    setNewElement({ name: '', type: 'props', category: '', notes: '' })
    setIsAddElementModalOpen(false)
    toast.success('Element added successfully!', { icon: '✅' })
  }

  const handleDeleteElement = (elementId: string) => {
    if (window.confirm('Are you sure you want to delete this element?')) {
      setElements(prev => prev.filter(el => el.id !== elementId))
      toast.success('Element deleted', { icon: '🗑️' })
    }
  }

  const filteredElements = selectedType 
    ? elements.filter(el => el.type === selectedType)
    : elements

  if (!currentProject) {
    return (
      <div className="flex items-center justify-center h-96">
        <LoadingSpinner size="lg" />
      </div>
    )
  }

  // Sidebar component for element types
  const sidebarContent = (
    <div className="p-4">
      <h3 className="text-sm font-medium text-gray-900 mb-3">Element Types</h3>
      <div className="space-y-2">
        <button
          onClick={() => setSelectedType(null)}
          className={`w-full flex items-center px-3 py-2 text-sm rounded-md text-left touch:min-h-[44px] transition-colors ${
            selectedType === null 
              ? 'bg-primary-100 text-primary-700 border border-primary-200' 
              : 'text-gray-700 hover:bg-gray-50'
          }`}
        >
          <TagIcon className="h-4 w-4 mr-3" />
          All Elements ({elements.length})
        </button>
        
        {elementTypes.map((elementType) => {
          const count = elements.filter(el => el.type === elementType.type).length
          const Icon = elementType.icon
          
          return (
            <button
              key={elementType.type}
              onClick={() => setSelectedType(elementType.type)}
              className={`w-full flex items-center px-3 py-2 text-sm rounded-md text-left touch:min-h-[44px] transition-colors ${
                selectedType === elementType.type 
                  ? 'bg-primary-100 text-primary-700 border border-primary-200' 
                  : 'text-gray-700 hover:bg-gray-50'
              }`}
            >
              <div className={`w-3 h-3 rounded-full mr-3 ${elementType.color}`} />
              <Icon className="h-4 w-4 mr-2" />
              {elementType.label} ({count})
            </button>
          )
        })}
      </div>

      <div className="mt-6 pt-4 border-t border-gray-200">
        <h4 className="text-sm font-medium text-gray-900 mb-2">Quick Stats</h4>
        <div className="space-y-1 text-sm text-gray-600">
          <div>Total Scenes: {scenes.length}</div>
          <div>Total Elements: {elements.length}</div>
          <div>Characters: {elements.filter(e => e.type === 'cast').length}</div>
          <div>Locations: {new Set(scenes.map(s => s.location.name)).size}</div>
        </div>
      </div>
    </div>
  )

  return (
    <div className="h-full flex flex-col bg-gray-50">
      {/* Enhanced Header */}
      <div className="bg-white border-b border-gray-200 shadow-sm">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex items-center justify-between h-16">
            <div className="flex items-center space-x-4">
              <div className="flex items-center space-x-3">
                <div className="w-8 h-8 bg-primary-500 rounded-lg flex items-center justify-center">
                  <FilmIcon className="h-5 w-5 text-white" />
                </div>
                <div>
                  <h1 className="text-lg font-semibold text-gray-900">Script Breakdown</h1>
                  <p className="text-sm text-gray-500">
                    {currentProject?.scriptContent ? 'Live analysis' : 'No script content'}
                  </p>
                </div>
              </div>
            </div>
            
            <div className="flex items-center space-x-2">
              <button 
                onClick={() => setIsAutoBreakdownOpen(true)}
                disabled={!currentProject?.scriptContent}
                className="btn-primary text-sm"
                title="AI-powered script analysis"
              >
                <SparklesIcon className="h-4 w-4 mr-2" />
                <span className="hidden sm:inline">AI Analysis</span>
                <span className="sm:hidden">AI</span>
              </button>
              <button 
                onClick={() => setIsAddElementModalOpen(true)}
                className="btn-outline text-sm"
              >
                <PlusIcon className="h-4 w-4 mr-2" />
                <span className="hidden sm:inline">Add Element</span>
                <span className="sm:hidden">Add</span>
              </button>
              <button 
                onClick={analyzeScript}
                disabled={!currentProject?.scriptContent || isLoading}
                className="btn-secondary text-sm"
                title="Re-analyze script"
              >
                <DocumentTextIcon className="h-4 w-4 mr-2" />
                <span className="hidden sm:inline">Re-analyze</span>
                <span className="sm:hidden">Scan</span>
              </button>
            </div>
          </div>
        </div>
      </div>

      {/* Main Content */}
      <div className="flex-1">
        {isLoading ? (
          <div className="flex items-center justify-center h-96">
            <div className="text-center">
              <LoadingSpinner size="lg" />
              <p className="mt-4 text-gray-600">Analyzing script content...</p>
            </div>
          </div>
        ) : !currentProject?.scriptContent ? (
          <div className="flex items-center justify-center h-96">
            <div className="text-center">
              <FilmIcon className="h-16 w-16 text-gray-400 mx-auto mb-4" />
              <h3 className="text-lg font-medium text-gray-900 mb-2">No Script Content</h3>
              <p className="text-gray-600 mb-4">
                Start writing your script to see automated breakdown analysis.
              </p>
              <button className="btn-primary">
                Go to Script Editor
              </button>
            </div>
          </div>
        ) : (
          <ResponsiveLayout sidebar={sidebarContent} className="flex-1">
            <div className="p-4 sm:p-6">
              {filteredElements.length > 0 ? (
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4">
                  {filteredElements.map((element) => {
                    const elementTypeConfig = elementTypes.find(t => t.type === element.type)
                    
                    return (
                      <div
                        key={element.id}
                        className="element-card group"
                      >
                        <div className="flex items-start justify-between mb-3">
                          <div className="flex items-center">
                            <div className={`w-3 h-3 rounded-full mr-2 ${elementTypeConfig?.color || 'bg-gray-500'}`} />
                            <h4 className="font-medium text-gray-900 text-sm">{element.name}</h4>
                            {element.isAutoDetected && (
                              <span className="ml-2 text-xs text-primary-600 bg-primary-100 px-1 rounded">
                                AI
                              </span>
                            )}
                          </div>
                          <div className="flex items-center space-x-1 opacity-0 group-hover:opacity-100 transition-opacity">
                            <button 
                              className="btn-icon text-gray-400 hover:text-gray-600"
                              title="Edit element"
                            >
                              <PencilIcon className="h-3 w-3" />
                            </button>
                            <button 
                              onClick={() => handleDeleteElement(element.id)}
                              className="btn-icon text-gray-400 hover:text-red-600"
                              title="Delete element"
                            >
                              <TrashIcon className="h-3 w-3" />
                            </button>
                          </div>
                        </div>
                        
                        <p className="text-sm text-gray-600 mb-3">{element.category}</p>
                        
                        <div className="flex items-center justify-between flex-wrap gap-2">
                          <div className="flex flex-wrap gap-1">
                            {element.scenes.slice(0, 3).map((scene) => (
                              <span
                                key={scene}
                                className="inline-flex px-2 py-1 text-xs font-medium bg-gray-100 text-gray-700 rounded"
                              >
                                {scene}
                              </span>
                            ))}
                            {element.scenes.length > 3 && (
                              <span className="inline-flex px-2 py-1 text-xs font-medium bg-gray-100 text-gray-700 rounded">
                                +{element.scenes.length - 3}
                              </span>
                            )}
                          </div>
                          <span className="text-xs text-gray-500 whitespace-nowrap">
                            {element.scenes.length} scene{element.scenes.length !== 1 ? 's' : ''}
                          </span>
                        </div>

                        {element.confidence < 1.0 && (
                          <div className="mt-2 text-xs text-amber-600">
                            Confidence: {Math.round(element.confidence * 100)}%
                          </div>
                        )}

                        {element.notes && (
                          <div className="mt-2 text-xs text-gray-500 italic">
                            {element.notes}
                          </div>
                        )}
                      </div>
                    )
                  })}
                </div>
              ) : (
                <div className="text-center py-12">
                  <TagIcon className="h-12 w-12 text-gray-400 mx-auto mb-4" />
                  <h3 className="text-lg font-medium text-gray-900 mb-2">
                    {selectedType 
                      ? `No ${elementTypes.find(t => t.type === selectedType)?.label.toLowerCase()} elements found`
                      : 'No elements found'
                    }
                  </h3>
                  <p className="text-gray-600 mb-4 px-4">
                    {currentProject?.scriptContent 
                      ? 'Try using AI Analysis to automatically detect elements, or add them manually.'
                      : 'Start writing your script to see elements appear here automatically.'
                    }
                  </p>
                  <div className="flex items-center justify-center space-x-3">
                    <button 
                      onClick={() => setIsAutoBreakdownOpen(true)}
                      disabled={!currentProject?.scriptContent}
                      className="btn-primary"
                    >
                      <SparklesIcon className="h-4 w-4 mr-2" />
                      AI Analysis
                    </button>
                    <button 
                      onClick={() => setIsAddElementModalOpen(true)}
                      className="btn-outline"
                    >
                      <PlusIcon className="h-4 w-4 mr-2" />
                      Add Element
                    </button>
                  </div>
                </div>
              )}
            </div>
          </ResponsiveLayout>
        )}
      </div>

      {/* AI Auto-Breakdown Modal */}
      <AutoBreakdownEngine
        isOpen={isAutoBreakdownOpen}
        onClose={() => setIsAutoBreakdownOpen(false)}
        onElementsConfirmed={handleElementsConfirmed}
      />

      {/* Add Element Modal */}
      {isAddElementModalOpen && (
        <div className="fixed inset-0 z-50 overflow-y-auto">
          <div className="flex items-center justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0">
            <div className="fixed inset-0 transition-opacity" aria-hidden="true">
              <div className="absolute inset-0 bg-gray-500 opacity-75"></div>
            </div>

            <div className="inline-block align-bottom bg-white rounded-lg text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-lg sm:w-full">
              <div className="bg-white px-4 pt-5 pb-4 sm:p-6 sm:pb-4">
                <h3 className="text-lg font-medium text-gray-900 mb-4">Add New Element</h3>
                
                <div className="space-y-4">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">
                      Element Name *
                    </label>
                    <input
                      type="text"
                      value={newElement.name}
                      onChange={(e) => setNewElement(prev => ({ ...prev, name: e.target.value }))}
                      className="input-primary"
                      placeholder="e.g., Coffee Cup, Detective Badge, etc."
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">
                      Element Type *
                    </label>
                    <select
                      value={newElement.type}
                      onChange={(e) => setNewElement(prev => ({ ...prev, type: e.target.value as BreakdownElement['type'] }))}
                      className="input-primary"
                    >
                      {elementTypes.map(type => (
                        <option key={type.type} value={type.type}>
                          {type.label}
                        </option>
                      ))}
                    </select>
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">
                      Category
                    </label>
                    <input
                      type="text"
                      value={newElement.category}
                      onChange={(e) => setNewElement(prev => ({ ...prev, category: e.target.value }))}
                      className="input-primary"
                      placeholder="e.g., Hero Prop, Background, etc."
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">
                      Notes
                    </label>
                    <textarea
                      value={newElement.notes}
                      onChange={(e) => setNewElement(prev => ({ ...prev, notes: e.target.value }))}
                      className="textarea-primary"
                      rows={3}
                      placeholder="Any special requirements or notes..."
                    />
                  </div>
                </div>
              </div>

              <div className="bg-gray-50 px-4 py-3 sm:px-6 sm:flex sm:flex-row-reverse">
                <button
                  type="button"
                  onClick={handleAddElement}
                  className="btn-primary sm:ml-3"
                >
                  Add Element
                </button>
                <button
                  type="button"
                  onClick={() => setIsAddElementModalOpen(false)}
                  className="btn-secondary mt-3 sm:mt-0"
                >
                  Cancel
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}