import { useState } from 'react'
import { useProjectStore } from '@/stores/projectStore'
import LoadingSpinner from '@/components/ui/LoadingSpinner'
import { 
  UserPlusIcon, 
  PaperAirplaneIcon,
  UserCircleIcon,
  AtSymbolIcon,
  ClockIcon
} from '@heroicons/react/24/outline'

const mockCollaborators = [
  {
    id: '1',
    name: 'John Director',
    email: 'john@film.com',
    role: 'director',
    avatar: null,
    status: 'online',
    lastSeen: new Date()
  },
  {
    id: '2', 
    name: 'Sarah Producer',
    email: 'sarah@production.com',
    role: 'producer',
    avatar: null,
    status: 'offline',
    lastSeen: new Date(Date.now() - 2 * 60 * 60 * 1000) // 2 hours ago
  },
  {
    id: '3',
    name: 'Mike Writer',
    email: 'mike@scripts.com', 
    role: 'writer',
    avatar: null,
    status: 'online',
    lastSeen: new Date()
  }
]

const mockComments = [
  {
    id: '1',
    author: 'John Director',
    content: 'This scene needs more tension. Consider adding a close-up on the character\'s reaction.',
    timestamp: new Date(Date.now() - 30 * 60 * 1000), // 30 minutes ago
    scene: 'Scene 2A',
    resolved: false
  },
  {
    id: '2',
    author: 'Sarah Producer',
    content: 'Budget concern: The car chase scene might be too expensive. Can we scale it down?',
    timestamp: new Date(Date.now() - 2 * 60 * 60 * 1000), // 2 hours ago
    scene: 'Scene 5B',
    resolved: false
  },
  {
    id: '3',
    author: 'Mike Writer', 
    content: 'Updated the dialogue based on yesterday\'s notes. Please review.',
    timestamp: new Date(Date.now() - 4 * 60 * 60 * 1000), // 4 hours ago
    scene: 'Scene 1A',
    resolved: true
  }
]

export default function CollaborationPage() {
  const { currentProject } = useProjectStore()
  const [newComment, setNewComment] = useState('')
  const [selectedTab, setSelectedTab] = useState<'comments' | 'team'>('comments')

  if (!currentProject) {
    return (
      <div className="flex items-center justify-center h-96">
        <LoadingSpinner size="lg" />
      </div>
    )
  }

  const getRoleColor = (role: string) => {
    switch (role) {
      case 'director': return 'bg-purple-100 text-purple-800'
      case 'producer': return 'bg-green-100 text-green-800'
      case 'writer': return 'bg-blue-100 text-blue-800'
      case 'script-supervisor': return 'bg-orange-100 text-orange-800'
      default: return 'bg-gray-100 text-gray-800'
    }
  }

  const getStatusColor = (status: string) => {
    return status === 'online' ? 'bg-green-500' : 'bg-gray-400'
  }

  const formatTime = (date: Date) => {
    const now = new Date()
    const diff = now.getTime() - date.getTime()
    const minutes = Math.floor(diff / (1000 * 60))
    const hours = Math.floor(diff / (1000 * 60 * 60))
    const days = Math.floor(diff / (1000 * 60 * 60 * 24))

    if (minutes < 60) return `${minutes}m ago`
    if (hours < 24) return `${hours}h ago`
    return `${days}d ago`
  }

  const handleSendComment = () => {
    if (newComment.trim()) {
      // Would add comment to store/API here
      setNewComment('')
    }
  }

  return (
    <div className="h-full flex flex-col">
      {/* Header */}
      <div className="bg-white border-b border-gray-200 px-6 py-4">
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-lg font-medium text-gray-900">Collaboration</h1>
            <p className="text-sm text-gray-500">Work together with your team in real-time</p>
          </div>
          <button className="btn-primary">
            <UserPlusIcon className="h-4 w-4 mr-2" />
            Invite Collaborator
          </button>
        </div>
      </div>

      {/* Tabs */}
      <div className="bg-white border-b border-gray-200">
        <nav className="flex space-x-8 px-6">
          <button
            onClick={() => setSelectedTab('comments')}
            className={`py-3 px-1 border-b-2 font-medium text-sm ${
              selectedTab === 'comments'
                ? 'border-primary-500 text-primary-600'
                : 'border-transparent text-gray-500 hover:text-gray-700'
            }`}
          >
            Comments ({mockComments.length})
          </button>
          <button
            onClick={() => setSelectedTab('team')}
            className={`py-3 px-1 border-b-2 font-medium text-sm ${
              selectedTab === 'team'
                ? 'border-primary-500 text-primary-600'
                : 'border-transparent text-gray-500 hover:text-gray-700'
            }`}
          >
            Team ({mockCollaborators.length})
          </button>
        </nav>
      </div>

      <div className="flex-1 p-6">
        {selectedTab === 'comments' ? (
          <div className="space-y-6">
            {/* New Comment Form */}
            <div className="bg-white rounded-lg border border-gray-200 p-4">
              <textarea
                value={newComment}
                onChange={(e) => setNewComment(e.target.value)}
                placeholder="Add a comment about the script, scene, or production notes..."
                className="w-full p-3 border border-gray-300 rounded-lg resize-none focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-primary-500"
                rows={3}
              />
              <div className="flex justify-between items-center mt-3">
                <div className="flex items-center space-x-2 text-sm text-gray-500">
                  <AtSymbolIcon className="h-4 w-4" />
                  <span>Use @mention to notify team members</span>
                </div>
                <button
                  onClick={handleSendComment}
                  disabled={!newComment.trim()}
                  className="btn-primary disabled:opacity-50 disabled:cursor-not-allowed"
                >
                  <PaperAirplaneIcon className="h-4 w-4 mr-2" />
                  Send Comment
                </button>
              </div>
            </div>

            {/* Comments List */}
            <div className="space-y-4">
              {mockComments.map((comment) => (
                <div
                  key={comment.id}
                  className={`bg-white rounded-lg border border-gray-200 p-4 ${
                    comment.resolved ? 'opacity-60' : ''
                  }`}
                >
                  <div className="flex items-start space-x-3">
                    <UserCircleIcon className="h-8 w-8 text-gray-400 flex-shrink-0" />
                    <div className="flex-1 min-w-0">
                      <div className="flex items-center space-x-2 mb-1">
                        <p className="text-sm font-medium text-gray-900">{comment.author}</p>
                        <span className="text-xs text-gray-500">•</span>
                        <span className="text-xs text-gray-500">{comment.scene}</span>
                        <span className="text-xs text-gray-500">•</span>
                        <span className="text-xs text-gray-500 flex items-center">
                          <ClockIcon className="h-3 w-3 mr-1" />
                          {formatTime(comment.timestamp)}
                        </span>
                        {comment.resolved && (
                          <span className="inline-flex px-2 py-1 text-xs font-medium bg-green-100 text-green-800 rounded-full">
                            Resolved
                          </span>
                        )}
                      </div>
                      <p className="text-sm text-gray-700">{comment.content}</p>
                      <div className="flex items-center space-x-2 mt-2">
                        <button className="text-xs text-gray-500 hover:text-gray-700">
                          Reply
                        </button>
                        {!comment.resolved && (
                          <button className="text-xs text-green-600 hover:text-green-700">
                            Mark as Resolved
                          </button>
                        )}
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        ) : (
          // Team Tab
          <div className="space-y-6">
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
              {mockCollaborators.map((collaborator) => (
                <div
                  key={collaborator.id}
                  className="bg-white rounded-lg border border-gray-200 p-4"
                >
                  <div className="flex items-center space-x-3 mb-3">
                    <div className="relative">
                      <UserCircleIcon className="h-10 w-10 text-gray-400" />
                      <div className={`absolute -bottom-1 -right-1 w-3 h-3 rounded-full border-2 border-white ${getStatusColor(collaborator.status)}`} />
                    </div>
                    <div className="flex-1 min-w-0">
                      <p className="text-sm font-medium text-gray-900 truncate">
                        {collaborator.name}
                      </p>
                      <p className="text-xs text-gray-500 truncate">
                        {collaborator.email}
                      </p>
                    </div>
                  </div>
                  
                  <div className="flex items-center justify-between">
                    <span className={`inline-flex px-2 py-1 text-xs font-medium rounded-full ${getRoleColor(collaborator.role)}`}>
                      {collaborator.role}
                    </span>
                    <div className="text-xs text-gray-500">
                      {collaborator.status === 'online' ? (
                        <span className="text-green-600">Online</span>
                      ) : (
                        <span>Last seen {formatTime(collaborator.lastSeen)}</span>
                      )}
                    </div>
                  </div>

                  <div className="mt-3 flex space-x-2">
                    <button className="text-xs text-primary-600 hover:text-primary-700">
                      Message
                    </button>
                    <button className="text-xs text-gray-500 hover:text-gray-700">
                      Edit Role
                    </button>
                  </div>
                </div>
              ))}
            </div>
          </div>
        )}
      </div>

      {/* Status Bar */}
      <div className="bg-gray-50 border-t border-gray-200 px-6 py-3">
        <div className="flex items-center justify-between text-sm text-gray-600">
          <div className="flex items-center space-x-4">
            <div className="flex items-center">
              <div className="w-2 h-2 bg-green-500 rounded-full mr-2" />
              <span>{mockCollaborators.filter(c => c.status === 'online').length} online</span>
            </div>
            <span>•</span>
            <span>{mockComments.filter(c => !c.resolved).length} open comments</span>
          </div>
          <div className="flex items-center space-x-2">
            <span>Real-time collaboration</span>
            <div className="w-2 h-2 bg-green-500 rounded-full" />
          </div>
        </div>
      </div>
    </div>
  )
}