import { useState } from 'react'
import { format, formatDistanceToNow } from 'date-fns'
import { 
  ClockIcon, 
  UserIcon, 
  EyeIcon, 
  ArrowPathIcon,
  TagIcon,
} from '@heroicons/react/24/outline'
import Modal from '../ui/Modal'

export interface Revision {
  id: string
  content: string
  timestamp: Date
  user: string
  userId: string
  version: string
  description?: string
  changes: {
    added: number
    removed: number
    modified: number
  }
  tags: string[]
}

interface RevisionManagerProps {
  isOpen: boolean
  onClose: () => void
  revisions: Revision[]
  currentContent: string
  onRestoreRevision: (revision: Revision) => void
  onCreateTag: (revisionId: string, tag: string) => void
  currentUser: string
}

export default function RevisionManager({
  isOpen,
  onClose,
  revisions,
  currentContent: _currentContent,
  onRestoreRevision,
  onCreateTag,
  currentUser
}: RevisionManagerProps) {
  const [selectedRevision, setSelectedRevision] = useState<Revision | null>(null)
  const [showPreview, setShowPreview] = useState(false)
  const [newTag, setNewTag] = useState('')
  const [filter, setFilter] = useState<'all' | 'mine' | 'others'>('all')

  const filteredRevisions = revisions.filter(revision => {
    if (filter === 'mine') return revision.userId === currentUser
    if (filter === 'others') return revision.userId !== currentUser
    return true
  })

  const handleRestoreRevision = (revision: Revision) => {
    if (window.confirm(`Restore to revision "${revision.version}" from ${format(revision.timestamp, 'PPpp')}?\n\nThis will replace your current script content.`)) {
      onRestoreRevision(revision)
      onClose()
    }
  }

  const handleAddTag = (revisionId: string) => {
    if (newTag.trim()) {
      onCreateTag(revisionId, newTag.trim())
      setNewTag('')
    }
  }

  // Simple diff calculation - in real app, use proper diff library
  // const getRevisionDiff = (revision: Revision) => {
  //   const currentLines = currentContent.split('\n')
  //   const revisionLines = revision.content.split('\n')
  //   return {
  //     added: Math.max(0, revisionLines.length - currentLines.length),
  //     removed: Math.max(0, currentLines.length - revisionLines.length),
  //     modified: Math.min(currentLines.length, revisionLines.length)
  //   }
  // }

  return (
    <Modal isOpen={isOpen} onClose={onClose} title="Revision History" size="xl">
      <div className="flex h-[70vh]">
        {/* Revision List */}
        <div className="w-1/2 border-r border-gray-200 pr-4">
          {/* Filter Buttons */}
          <div className="flex space-x-2 mb-4">
            <button
              onClick={() => setFilter('all')}
              className={`px-3 py-1 text-sm rounded-md ${
                filter === 'all' ? 'bg-primary-100 text-primary-700' : 'bg-gray-100 text-gray-700'
              }`}
            >
              All ({revisions.length})
            </button>
            <button
              onClick={() => setFilter('mine')}
              className={`px-3 py-1 text-sm rounded-md ${
                filter === 'mine' ? 'bg-primary-100 text-primary-700' : 'bg-gray-100 text-gray-700'
              }`}
            >
              Mine ({revisions.filter(r => r.userId === currentUser).length})
            </button>
            <button
              onClick={() => setFilter('others')}
              className={`px-3 py-1 text-sm rounded-md ${
                filter === 'others' ? 'bg-primary-100 text-primary-700' : 'bg-gray-100 text-gray-700'
              }`}
            >
              Others ({revisions.filter(r => r.userId !== currentUser).length})
            </button>
          </div>

          {/* Revision List */}
          <div className="space-y-2 max-h-full overflow-y-auto">
            {filteredRevisions.map((revision) => (
              <div
                key={revision.id}
                className={`p-3 border rounded-lg cursor-pointer transition-colors ${
                  selectedRevision?.id === revision.id
                    ? 'border-primary-300 bg-primary-50'
                    : 'border-gray-200 hover:border-gray-300 hover:bg-gray-50'
                }`}
                onClick={() => setSelectedRevision(revision)}
              >
                <div className="flex items-start justify-between mb-2">
                  <div>
                    <div className="font-medium text-sm text-gray-900">
                      {revision.version}
                    </div>
                    <div className="flex items-center text-xs text-gray-500 mt-1">
                      <UserIcon className="h-3 w-3 mr-1" />
                      {revision.user}
                      <ClockIcon className="h-3 w-3 ml-2 mr-1" />
                      {formatDistanceToNow(revision.timestamp, { addSuffix: true })}
                    </div>
                  </div>
                  <div className="text-right">
                    <div className="text-xs text-gray-500">
                      {format(revision.timestamp, 'MMM d, HH:mm')}
                    </div>
                  </div>
                </div>

                {/* Changes Summary */}
                <div className="flex items-center space-x-2 text-xs mb-2">
                  {revision.changes.added > 0 && (
                    <span className="px-2 py-1 bg-green-100 text-green-700 rounded">
                      +{revision.changes.added}
                    </span>
                  )}
                  {revision.changes.removed > 0 && (
                    <span className="px-2 py-1 bg-red-100 text-red-700 rounded">
                      -{revision.changes.removed}
                    </span>
                  )}
                  {revision.changes.modified > 0 && (
                    <span className="px-2 py-1 bg-blue-100 text-blue-700 rounded">
                      ~{revision.changes.modified}
                    </span>
                  )}
                </div>

                {/* Description */}
                {revision.description && (
                  <p className="text-sm text-gray-600 mb-2">{revision.description}</p>
                )}

                {/* Tags */}
                {revision.tags.length > 0 && (
                  <div className="flex flex-wrap gap-1">
                    {revision.tags.map((tag) => (
                      <span
                        key={tag}
                        className="inline-flex items-center px-2 py-1 text-xs bg-gray-100 text-gray-700 rounded"
                      >
                        <TagIcon className="h-3 w-3 mr-1" />
                        {tag}
                      </span>
                    ))}
                  </div>
                )}
              </div>
            ))}

            {filteredRevisions.length === 0 && (
              <div className="text-center py-8 text-gray-500">
                <ClockIcon className="h-8 w-8 mx-auto mb-2 opacity-50" />
                <p>No revisions found</p>
              </div>
            )}
          </div>
        </div>

        {/* Revision Details */}
        <div className="w-1/2 pl-4">
          {selectedRevision ? (
            <div>
              <div className="flex items-center justify-between mb-4">
                <div>
                  <h3 className="text-lg font-medium text-gray-900">
                    {selectedRevision.version}
                  </h3>
                  <p className="text-sm text-gray-600">
                    by {selectedRevision.user} • {format(selectedRevision.timestamp, 'PPpp')}
                  </p>
                </div>
                <div className="flex space-x-2">
                  <button
                    onClick={() => setShowPreview(!showPreview)}
                    className="btn-outline text-sm"
                  >
                    <EyeIcon className="h-4 w-4 mr-1" />
                    {showPreview ? 'Hide' : 'Preview'}
                  </button>
                  <button
                    onClick={() => handleRestoreRevision(selectedRevision)}
                    className="btn-primary text-sm"
                  >
                    <ArrowPathIcon className="h-4 w-4 mr-1" />
                    Restore
                  </button>
                </div>
              </div>

              {/* Add Tag */}
              <div className="mb-4">
                <div className="flex space-x-2">
                  <input
                    type="text"
                    value={newTag}
                    onChange={(e) => setNewTag(e.target.value)}
                    placeholder="Add tag..."
                    className="flex-1 px-3 py-1 text-sm border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent"
                    onKeyPress={(e) => {
                      if (e.key === 'Enter') {
                        handleAddTag(selectedRevision.id)
                      }
                    }}
                  />
                  <button
                    onClick={() => handleAddTag(selectedRevision.id)}
                    className="px-3 py-1 text-sm bg-gray-100 hover:bg-gray-200 rounded-md"
                  >
                    Add Tag
                  </button>
                </div>
              </div>

              {/* Change Summary */}
              <div className="mb-4 p-3 bg-gray-50 rounded-lg">
                <h4 className="font-medium text-sm text-gray-900 mb-2">Changes</h4>
                <div className="grid grid-cols-3 gap-4 text-sm">
                  <div className="text-center">
                    <div className="text-lg font-medium text-green-600">
                      +{selectedRevision.changes.added}
                    </div>
                    <div className="text-gray-500">Added</div>
                  </div>
                  <div className="text-center">
                    <div className="text-lg font-medium text-red-600">
                      -{selectedRevision.changes.removed}
                    </div>
                    <div className="text-gray-500">Removed</div>
                  </div>
                  <div className="text-center">
                    <div className="text-lg font-medium text-blue-600">
                      ~{selectedRevision.changes.modified}
                    </div>
                    <div className="text-gray-500">Modified</div>
                  </div>
                </div>
              </div>

              {/* Preview */}
              {showPreview && (
                <div className="bg-white border border-gray-200 rounded-lg p-4">
                  <h4 className="font-medium text-sm text-gray-900 mb-2">Script Preview</h4>
                  <div className="max-h-64 overflow-y-auto text-sm font-mono leading-6 text-gray-800">
                    {selectedRevision.content.split('\n').slice(0, 20).map((line, index) => (
                      <div key={index}>{line || '\u00A0'}</div>
                    ))}
                    {selectedRevision.content.split('\n').length > 20 && (
                      <div className="text-gray-500 italic">... and {selectedRevision.content.split('\n').length - 20} more lines</div>
                    )}
                  </div>
                </div>
              )}
            </div>
          ) : (
            <div className="flex items-center justify-center h-full text-gray-500">
              <div className="text-center">
                <ClockIcon className="h-8 w-8 mx-auto mb-2 opacity-50" />
                <p>Select a revision to view details</p>
              </div>
            </div>
          )}
        </div>
      </div>
    </Modal>
  )
}