import { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import { useProjectStore } from '@/stores/projectStore'
import Modal from '@/components/ui/Modal'
import LoadingSpinner from '@/components/ui/LoadingSpinner'
import ScriptTemplateSelector from '@/components/project/ScriptTemplateSelector'
import { ScriptTemplate } from '@/templates/scriptTemplates'
import { ChevronLeftIcon, ChevronRightIcon } from '@heroicons/react/24/outline'

interface NewProjectModalProps {
  isOpen: boolean
  onClose: () => void
}

const projectFormats = [
  { value: 'feature', label: 'Feature Film', description: '90-120+ minutes' },
  { value: 'short', label: 'Short Film', description: '5-30 minutes' },
  { value: 'series', label: 'TV Series', description: 'Episode-based content' },
  { value: 'documentary', label: 'Documentary', description: 'Non-fiction film' },
  { value: 'commercial', label: 'Commercial', description: '30-60 seconds' },
]

const genres = [
  'Action', 'Adventure', 'Animation', 'Comedy', 'Crime', 'Documentary', 
  'Drama', 'Fantasy', 'Horror', 'Musical', 'Mystery', 'Romance', 
  'Sci-Fi', 'Thriller', 'War', 'Western'
]

export default function NewProjectModal({ isOpen, onClose }: NewProjectModalProps) {
  const navigate = useNavigate()
  const { createProject } = useProjectStore()
  
  const [currentStep, setCurrentStep] = useState<'details' | 'template'>('details')
  const [selectedTemplate, setSelectedTemplate] = useState<ScriptTemplate | null>(null)
  
  const [formData, setFormData] = useState({
    title: '',
    description: '',
    format: 'feature',
    genre: [] as string[],
    status: 'development'
  })
  
  const [isLoading, setIsLoading] = useState(false)
  const [error, setError] = useState('')

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!formData.title.trim()) {
      setError('Project title is required')
      return
    }

    setIsLoading(true)
    setError('')

    try {
      // Determine script content based on template selection
      let scriptContent = selectedTemplate 
        ? selectedTemplate.content 
        : `FADE IN:

EXT. ${formData.title.toUpperCase()} - DAY

[Your story begins here...]

FADE OUT.`
      
      const projectId = await createProject({
        title: formData.title.trim(),
        description: formData.description.trim(),
        format: formData.format as any,
        genre: formData.genre,
        status: formData.status as any,
        scriptContent,
        scenes: [],
        characters: [],
        locations: [],
        pageCount: 1,
        wordCount: 0,
        estimatedRuntime: formData.format === 'feature' ? 90 : formData.format === 'short' ? 15 : 60,
        collaborators: [],
        versions: [],
        settings: {
          allowPublicView: false,
          requireApprovalForChanges: false,
          enableRealTimeCollaboration: true,
          backupFrequency: 'daily'
        }
      })

      // Close modal and navigate to the new project
      onClose()
      navigate(`/project/${projectId}`)
    } catch (err) {
      setError('Failed to create project. Please try again.')
    } finally {
      setIsLoading(false)
    }
  }

  const handleGenreToggle = (genre: string) => {
    setFormData(prev => ({
      ...prev,
      genre: prev.genre.includes(genre)
        ? prev.genre.filter(g => g !== genre)
        : [...prev.genre, genre]
    }))
  }

  const handleClose = () => {
    setFormData({
      title: '',
      description: '',
      format: 'feature',
      genre: [],
      status: 'development'
    })
    setCurrentStep('details')
    setSelectedTemplate(null)
    setError('')
    onClose()
  }

  const handleNextStep = () => {
    if (!formData.title.trim()) {
      setError('Project title is required')
      return
    }
    setError('')
    setCurrentStep('template')
  }

  const handlePreviousStep = () => {
    setCurrentStep('details')
    setError('')
  }

  const handleTemplateSelect = (template: ScriptTemplate | null) => {
    setSelectedTemplate(template)
  }

  const modalTitle = currentStep === 'details' ? 'Create New Project' : 'Choose Script Template'
  
  return (
    <Modal isOpen={isOpen} onClose={handleClose} title={modalTitle} size={currentStep === 'template' ? 'xl' : 'lg'}>
      {currentStep === 'details' ? (
        <form onSubmit={(e) => { e.preventDefault(); handleNextStep(); }} className="space-y-6">
        {/* Title */}
        <div>
          <label htmlFor="title" className="block text-sm font-medium text-gray-700 mb-2">
            Project Title *
          </label>
          <input
            type="text"
            id="title"
            value={formData.title}
            onChange={(e) => setFormData(prev => ({ ...prev, title: e.target.value }))}
            className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-primary-500"
            placeholder="Enter your project title"
            required
          />
        </div>

        {/* Description */}
        <div>
          <label htmlFor="description" className="block text-sm font-medium text-gray-700 mb-2">
            Description
          </label>
          <textarea
            id="description"
            value={formData.description}
            onChange={(e) => setFormData(prev => ({ ...prev, description: e.target.value }))}
            rows={3}
            className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-primary-500"
            placeholder="Brief description of your project"
          />
        </div>

        {/* Format */}
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-2">
            Project Format
          </label>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
            {projectFormats.map((format) => (
              <div
                key={format.value}
                className={`relative rounded-md border p-3 cursor-pointer transition-colors ${
                  formData.format === format.value
                    ? 'border-primary-500 bg-primary-50'
                    : 'border-gray-300 hover:bg-gray-50'
                }`}
                onClick={() => setFormData(prev => ({ ...prev, format: format.value }))}
              >
                <div className="flex items-center">
                  <input
                    type="radio"
                    name="format"
                    value={format.value}
                    checked={formData.format === format.value}
                    onChange={(e) => setFormData(prev => ({ ...prev, format: e.target.value }))}
                    className="h-4 w-4 text-primary-600 focus:ring-primary-500 border-gray-300"
                  />
                  <div className="ml-3">
                    <label className="block text-sm font-medium text-gray-900 cursor-pointer">
                      {format.label}
                    </label>
                    <p className="text-sm text-gray-500">{format.description}</p>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>

        {/* Genre */}
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-2">
            Genre (select multiple)
          </label>
          <div className="flex flex-wrap gap-2">
            {genres.map((genre) => (
              <button
                key={genre}
                type="button"
                onClick={() => handleGenreToggle(genre)}
                className={`px-3 py-1 rounded-full text-sm font-medium transition-colors ${
                  formData.genre.includes(genre)
                    ? 'bg-primary-100 text-primary-800 border border-primary-200'
                    : 'bg-gray-100 text-gray-700 border border-gray-300 hover:bg-gray-200'
                }`}
              >
                {genre}
              </button>
            ))}
          </div>
        </div>

        {/* Error Message */}
        {error && (
          <div className="text-red-600 text-sm bg-red-50 border border-red-200 rounded-md p-3">
            {error}
          </div>
        )}

        {/* Actions */}
        <div className="flex justify-end space-x-3 pt-6 border-t border-gray-200">
          <button
            type="button"
            onClick={handleClose}
            className="px-4 py-2 text-sm font-medium text-gray-700 bg-white border border-gray-300 rounded-md hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-primary-500"
          >
            Cancel
          </button>
          <button
            type="submit"
            disabled={!formData.title.trim()}
            className="px-4 py-2 text-sm font-medium text-white bg-orange-600 border border-transparent rounded-md hover:bg-orange-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-orange-500 disabled:opacity-50 disabled:cursor-not-allowed flex items-center"
          >
            <span>Next: Choose Template</span>
            <ChevronRightIcon className="h-4 w-4 ml-2" />
          </button>
        </div>
      </form>
      ) : (
        <div className="space-y-6">
          {/* Template Selection Step */}
          <ScriptTemplateSelector
            onSelectTemplate={handleTemplateSelect}
            selectedTemplateId={selectedTemplate?.id}
          />
          
          {/* Template Actions */}
          <div className="flex justify-between pt-6 border-t border-gray-200">
            <button
              type="button"
              onClick={handlePreviousStep}
              className="flex items-center px-4 py-2 text-sm font-medium text-gray-700 bg-white border border-gray-300 rounded-md hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-gray-500"
            >
              <ChevronLeftIcon className="h-4 w-4 mr-2" />
              Back to Details
            </button>
            
            <div className="flex space-x-3">
              <button
                type="button"
                onClick={handleClose}
                className="px-4 py-2 text-sm font-medium text-gray-700 bg-white border border-gray-300 rounded-md hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-gray-500"
              >
                Cancel
              </button>
              <button
                type="button"
                onClick={handleSubmit}
                disabled={isLoading}
                className="px-6 py-2 text-sm font-medium text-white bg-orange-600 border border-transparent rounded-md hover:bg-orange-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-orange-500 disabled:opacity-50 disabled:cursor-not-allowed flex items-center"
              >
                {isLoading ? (
                  <>
                    <LoadingSpinner size="sm" className="mr-2" />
                    Creating Project...
                  </>
                ) : (
                  <>
                    🎬 Create Project
                    {selectedTemplate && (
                      <span className="ml-2 text-xs bg-orange-500 px-2 py-1 rounded">
                        with {selectedTemplate.name}
                      </span>
                    )}
                  </>
                )}
              </button>
            </div>
          </div>
        </div>
      )}
    </Modal>
  )
}