import { detectElementTypeWithContext } from './screenplayFormatter'

export interface FDXExportOptions {
  title: string
  author: string
  draft?: string
  date?: Date
  contact?: string
  includeRevisions?: boolean
  coloredRevisions?: boolean
}

/**
 * Final Draft XML (.fdx) Exporter
 * Creates industry-standard Final Draft files that can be opened in Final Draft software
 */
export class FDXExporter {
  private lines: string[]
  private options: FDXExportOptions

  constructor(content: string, options: FDXExportOptions) {
    this.lines = content.split('\n')
    this.options = options
  }

  /**
   * Export script as Final Draft XML
   */
  public export(): string {
    const elements = this.parseElements()
    const titlePage = this.generateTitlePage()
    const scriptElements = this.generateScriptElements(elements)
    
    return this.generateFDXDocument(titlePage, scriptElements)
  }

  /**
   * Parse script content into Final Draft elements
   */
  private parseElements(): FDXElement[] {
    const elements: FDXElement[] = []
    let elementId = 1

    for (let i = 0; i < this.lines.length; i++) {
      const line = this.lines[i]
      if (!line.trim()) {
        // Empty line - add paragraph break
        elements.push({
          id: elementId++,
          type: 'Action',
          text: '',
          isEmpty: true
        })
        continue
      }

      const elementType = detectElementTypeWithContext(this.lines, i)
      const fdxType = this.mapToFDXType(elementType)
      
      elements.push({
        id: elementId++,
        type: fdxType,
        text: line.trim(),
        isEmpty: false,
        originalType: elementType
      })
    }

    return elements
  }

  /**
   * Map internal element types to Final Draft types
   */
  private mapToFDXType(elementType: string): string {
    const mapping: { [key: string]: string } = {
      'scene_heading': 'Scene Heading',
      'action': 'Action',
      'character': 'Character',
      'dialogue': 'Dialogue',
      'parenthetical': 'Parenthetical',
      'transition': 'Transition',
      'shot': 'Shot'
    }

    return mapping[elementType] || 'Action'
  }

  /**
   * Generate title page XML
   */
  private generateTitlePage(): string {
    const date = this.options.date || new Date()
    const formattedDate = date.toLocaleDateString('en-US', {
      month: 'long',
      day: 'numeric',
      year: 'numeric'
    })

    return `
    <TitlePage>
      <Content>
        <Paragraph Type="Title">
          <Text>${this.escapeXML(this.options.title.toUpperCase())}</Text>
        </Paragraph>
        <Paragraph Type="Title">
          <Text></Text>
        </Paragraph>
        <Paragraph Type="Title">
          <Text></Text>
        </Paragraph>
        <Paragraph Type="Written By">
          <Text>Written by</Text>
        </Paragraph>
        <Paragraph Type="Written By">
          <Text></Text>
        </Paragraph>
        <Paragraph Type="Author">
          <Text>${this.escapeXML(this.options.author)}</Text>
        </Paragraph>
        <Paragraph Type="Author">
          <Text></Text>
        </Paragraph>
        <Paragraph Type="Author">
          <Text></Text>
        </Paragraph>
        <Paragraph Type="Author">
          <Text></Text>
        </Paragraph>
        <Paragraph Type="Contact">
          <Text>${this.escapeXML(this.options.contact || '')}</Text>
        </Paragraph>
        <Paragraph Type="Contact">
          <Text>${formattedDate}</Text>
        </Paragraph>
        ${this.options.draft ? `
        <Paragraph Type="Draft">
          <Text>${this.escapeXML(this.options.draft)}</Text>
        </Paragraph>
        ` : ''}
      </Content>
    </TitlePage>`
  }

  /**
   * Generate script elements XML
   */
  private generateScriptElements(elements: FDXElement[]): string {
    let xml = ''
    
    elements.forEach(element => {
      if (element.isEmpty) {
        xml += `        <Paragraph Type="Action"><Text></Text></Paragraph>\n`
      } else {
        xml += `        <Paragraph Type="${element.type}">`
        
        // Add text content
        if (element.text) {
          xml += `<Text>${this.escapeXML(element.text)}</Text>`
        }
        
        xml += `</Paragraph>\n`
      }
    })

    return xml
  }

  /**
   * Generate complete FDX document
   */
  private generateFDXDocument(titlePage: string, scriptElements: string): string {
    const now = new Date().toISOString()
    
    return `<?xml version="1.0" encoding="UTF-8" standalone="no" ?>
<FinalDraft DocumentType="Script" Template="No" Version="12">

  <Content>
${titlePage}

    <Body>
      <Content>
${scriptElements}
      </Content>
    </Body>
  </Content>

  <ScriptProperties>
    <Title>${this.escapeXML(this.options.title)}</Title>
    <Author>${this.escapeXML(this.options.author)}</Author>
    <Draft>${this.escapeXML(this.options.draft || 'First Draft')}</Draft>
    <Date>${now}</Date>
    <Copyright></Copyright>
    <Contact>${this.escapeXML(this.options.contact || '')}</Contact>
    <Summary></Summary>
  </ScriptProperties>

  <ElementSettings Type="Action">
    <FontSpec AdornmentStyle="0" Background="#FFFFFFFFFFFF" Color="#000000000000" Effect="" Face="Courier New" Size="12" Style="" />
    <Behavior PaginateAs="" ReturnKey="" />
    <Margins Bottom="0" Left="1.5" Right="7.0" Top="12" />
    <LineSpacing Lines="1" />
    <Capitalization>Mixed</Capitalization>
  </ElementSettings>

  <ElementSettings Type="Character">
    <FontSpec AdornmentStyle="0" Background="#FFFFFFFFFFFF" Color="#000000000000" Effect="" Face="Courier New" Size="12" Style="" />
    <Behavior PaginateAs="" ReturnKey="Dialogue" />
    <Margins Bottom="0" Left="3.7" Right="7.0" Top="12" />
    <LineSpacing Lines="1" />
    <Capitalization>Upper</Capitalization>
  </ElementSettings>

  <ElementSettings Type="Dialogue">
    <FontSpec AdornmentStyle="0" Background="#FFFFFFFFFFFF" Color="#000000000000" Effect="" Face="Courier New" Size="12" Style="" />
    <Behavior PaginateAs="" ReturnKey="" />
    <Margins Bottom="0" Left="2.5" Right="2.5" Top="0" />
    <LineSpacing Lines="1" />
    <Capitalization>Mixed</Capitalization>
  </ElementSettings>

  <ElementSettings Type="Parenthetical">
    <FontSpec AdornmentStyle="0" Background="#FFFFFFFFFFFF" Color="#000000000000" Effect="" Face="Courier New" Size="12" Style="" />
    <Behavior PaginateAs="" ReturnKey="Dialogue" />
    <Margins Bottom="0" Left="3.1" Right="3.5" Top="0" />
    <LineSpacing Lines="1" />
    <Capitalization>Mixed</Capitalization>
  </ElementSettings>

  <ElementSettings Type="Scene Heading">
    <FontSpec AdornmentStyle="0" Background="#FFFFFFFFFFFF" Color="#000000000000" Effect="" Face="Courier New" Size="12" Style="Bold" />
    <Behavior PaginateAs="" ReturnKey="Action" />
    <Margins Bottom="0" Left="1.5" Right="7.0" Top="24" />
    <LineSpacing Lines="1" />
    <Capitalization>Upper</Capitalization>
  </ElementSettings>

  <ElementSettings Type="Transition">
    <FontSpec AdornmentStyle="0" Background="#FFFFFFFFFFFF" Color="#000000000000" Effect="" Face="Courier New" Size="12" Style="" />
    <Behavior PaginateAs="" ReturnKey="Scene Heading" />
    <Margins Bottom="0" Left="6.0" Right="7.0" Top="12" />
    <LineSpacing Lines="1" />
    <Capitalization>Upper</Capitalization>
  </ElementSettings>

  <ElementSettings Type="Shot">
    <FontSpec AdornmentStyle="0" Background="#FFFFFFFFFFFF" Color="#000000000000" Effect="" Face="Courier New" Size="12" Style="Bold" />
    <Behavior PaginateAs="" ReturnKey="Action" />
    <Margins Bottom="0" Left="1.5" Right="7.0" Top="12" />
    <LineSpacing Lines="1" />
    <Capitalization>Upper</Capitalization>
  </ElementSettings>

  <Watermark ShowOnPrint="No" ShowOnScreen="No">
    <FontSpec AdornmentStyle="0" Background="#FFFFFFFFFFFF" Color="#999999999999" Effect="" Face="Arial" Size="36" Style="Bold" />
    <Text>WATERMARK</Text>
  </Watermark>

  <WindowState Height="700" Left="78" Top="0" Width="700" />

</FinalDraft>`
  }

  /**
   * Escape XML special characters
   */
  private escapeXML(text: string): string {
    return text
      .replace(/&/g, '&amp;')
      .replace(/</g, '&lt;')
      .replace(/>/g, '&gt;')
      .replace(/"/g, '&quot;')
      .replace(/'/g, '&#x27;')
  }
}

interface FDXElement {
  id: number
  type: string
  text: string
  isEmpty: boolean
  originalType?: string
}

/**
 * Export script content to Final Draft format
 */
export function exportToFDX(
  content: string,
  options: FDXExportOptions
): void {
  const exporter = new FDXExporter(content, options)
  const fdxContent = exporter.export()
  
  // Create and download file
  const blob = new Blob([fdxContent], { type: 'application/xml' })
  const url = URL.createObjectURL(blob)
  const a = document.createElement('a')
  a.href = url
  a.download = `${options.title.replace(/[^a-z0-9]/gi, '_').toLowerCase()}.fdx`
  document.body.appendChild(a)
  a.click()
  document.body.removeChild(a)
  URL.revokeObjectURL(url)
}

/**
 * Validate FDX export options
 */
export function validateFDXOptions(options: Partial<FDXExportOptions>): string[] {
  const errors: string[] = []
  
  if (!options.title?.trim()) {
    errors.push('Title is required')
  }
  
  if (!options.author?.trim()) {
    errors.push('Author name is required')
  }
  
  if (options.title && options.title.length > 200) {
    errors.push('Title must be less than 200 characters')
  }
  
  if (options.author && options.author.length > 100) {
    errors.push('Author name must be less than 100 characters')
  }
  
  return errors
}