import { create } from 'zustand'
import { devtools } from 'zustand/middleware'

export interface ScriptElement {
  id: string
  type: 'cast' | 'props' | 'wardrobe' | 'makeup' | 'sfx' | 'location' | 'vehicle' | 'animal' | 'music' | 'other'
  name: string
  description?: string
  category?: string
  color: string
  scenes: string[] // scene IDs where this element appears
  notes?: string
  images?: string[]
  cost?: number
  responsible?: string // crew member ID
  status: 'pending' | 'confirmed' | 'acquired' | 'completed'
  createdAt: string
  updatedAt: string
}

export interface ScriptScene {
  id: string
  number: string
  heading: string
  description: string
  location: string
  timeOfDay: 'DAY' | 'NIGHT' | 'DAWN' | 'DUSK' | 'CONTINUOUS'
  intExt: 'INT' | 'EXT'
  pageCount: number
  estimatedMinutes: number
  characters: string[]
  elements: ScriptElement[]
  notes?: string
  tags: string[]
  status: 'draft' | 'approved' | 'scheduled' | 'shot' | 'completed'
  schedulingPriority: 'low' | 'medium' | 'high'
  createdAt: string
  updatedAt: string
}

export interface Project {
  id: string
  title: string
  description?: string
  format: 'feature' | 'short' | 'series' | 'documentary' | 'commercial'
  status: 'development' | 'pre-production' | 'production' | 'post-production' | 'completed'
  genre: string[]
  
  // Script data
  scriptContent: string
  scenes: ScriptScene[]
  characters: string[]
  locations: string[]
  
  // Metadata
  pageCount: number
  wordCount: number
  estimatedRuntime: number
  
  // Collaboration
  owner: string // user ID
  collaborators: Array<{
    userId: string
    role: 'producer' | 'director' | 'writer' | 'script-supervisor' | 'viewer'
    permissions: string[]
  }>
  
  // Versions
  version: string
  versions: Array<{
    id: string
    version: string
    changes: string
    createdBy: string
    createdAt: string
    scriptContent: string
  }>
  
  // Settings
  settings: {
    allowPublicView: boolean
    requireApprovalForChanges: boolean
    enableRealTimeCollaboration: boolean
    backupFrequency: 'off' | 'hourly' | 'daily' | 'weekly'
    exportWatermark?: string
  }
  
  createdAt: string
  updatedAt: string
}

interface ProjectState {
  currentProject: Project | null
  projects: Project[]
  isLoading: boolean
  selectedScenes: string[]
  
  // Actions
  loadProjects: () => Promise<void>
  loadProject: (projectId: string) => Promise<void>
  createProject: (projectData: Partial<Project>) => Promise<string>
  updateProject: (projectId: string, updates: Partial<Project>) => Promise<void>
  deleteProject: (projectId: string) => Promise<void>
  duplicateProject: (projectId: string) => Promise<string>
  
  // Script actions
  updateScriptContent: (content: string) => void
  parseScript: () => Promise<void>
  addScene: (scene: Omit<ScriptScene, 'id' | 'createdAt' | 'updatedAt'>) => Promise<void>
  updateScene: (sceneId: string, updates: Partial<ScriptScene>) => Promise<void>
  deleteScene: (sceneId: string) => Promise<void>
  reorderScenes: (sceneIds: string[]) => Promise<void>
  
  // Element actions
  addElement: (element: Omit<ScriptElement, 'id' | 'createdAt' | 'updatedAt'>) => Promise<void>
  updateElement: (elementId: string, updates: Partial<ScriptElement>) => Promise<void>
  deleteElement: (elementId: string) => Promise<void>
  tagElementInScene: (elementId: string, sceneId: string) => Promise<void>
  untagElementFromScene: (elementId: string, sceneId: string) => Promise<void>
  
  // Selection
  setSelectedScenes: (sceneIds: string[]) => void
  clearSelectedScenes: () => void
  
  // Utilities
  getElementsByType: (type: ScriptElement['type']) => ScriptElement[]
  getScenesByLocation: (location: string) => ScriptScene[]
  getScenesByCharacter: (character: string) => ScriptScene[]
  getTotalPageCount: () => number
  getEstimatedRuntime: () => number
}

export const useProjectStore = create<ProjectState>()(
  devtools(
    (set, get) => ({
      currentProject: null,
      projects: [],
      isLoading: false,
      selectedScenes: [],

      loadProjects: async () => {
        set({ isLoading: true })
        try {
          const storedProjects = localStorage.getItem('hemantdoc_projects')
          let projects: Project[] = []
          
          if (storedProjects) {
            projects = JSON.parse(storedProjects)
          } else {
            // Create initial sample project
            projects = [{
              id: 'sample-project-' + Date.now(),
              title: 'Sample Screenplay',
              description: 'A sample project to get started with HemantDoc ScriptWriter Pro',
              format: 'short' as const,
              status: 'development' as const,
              genre: ['Drama'],
              scriptContent: 'FADE IN:\n\nEXT. COFFEE SHOP - DAY\n\nA small coffee shop buzzes with morning activity. SARAH (30s) enters, looking determined.\n\nSARAH\n(to barista)\nOne large coffee, please. And make it strong.\n\nFADE OUT.',
              scenes: [],
              characters: ['SARAH', 'BARISTA'],
              locations: ['COFFEE SHOP - EXT'],
              pageCount: 1,
              wordCount: 32,
              estimatedRuntime: 5,
              owner: 'standalone-user',
              collaborators: [],
              version: '1.0',
              versions: [],
              settings: {
                allowPublicView: false,
                requireApprovalForChanges: false,
                enableRealTimeCollaboration: true,
                backupFrequency: 'daily' as const,
                exportWatermark: 'HemantDoc ScriptWriter Pro'
              },
              createdAt: new Date().toISOString(),
              updatedAt: new Date().toISOString()
            }]
            localStorage.setItem('hemantdoc_projects', JSON.stringify(projects))
          }
          
          set({ projects, isLoading: false })
        } catch (error) {
          console.error('Load projects error:', error)
          set({ projects: [], isLoading: false })
        }
      },

      loadProject: async (projectId: string) => {
        set({ isLoading: true })
        try {
          const storedProjects = localStorage.getItem('hemantdoc_projects')
          if (storedProjects) {
            const projects: Project[] = JSON.parse(storedProjects)
            const project = projects.find(p => p.id === projectId)
            set({ currentProject: project || null, isLoading: false })
          } else {
            set({ currentProject: null, isLoading: false })
          }
        } catch (error) {
          console.error('Load project error:', error)
          set({ currentProject: null, isLoading: false })
        }
      },

      createProject: async (projectData: Partial<Project>) => {
        try {
          const newProject: Project = {
            id: 'project-' + Date.now() + '-' + Math.random().toString(36).substr(2, 9),
            title: projectData.title || 'Untitled Project',
            description: projectData.description || '',
            format: projectData.format || 'feature',
            status: projectData.status || 'development',
            genre: projectData.genre || [],
            scriptContent: projectData.scriptContent || 'FADE IN:\n\n\n\nFADE OUT.',
            scenes: [],
            characters: [],
            locations: [],
            pageCount: 1,
            wordCount: 6,
            estimatedRuntime: 0,
            owner: 'standalone-user',
            collaborators: [],
            version: '1.0',
            versions: [],
            settings: {
              allowPublicView: false,
              requireApprovalForChanges: false,
              enableRealTimeCollaboration: true,
              backupFrequency: 'daily',
              exportWatermark: 'HemantDoc ScriptWriter Pro'
            },
            createdAt: new Date().toISOString(),
            updatedAt: new Date().toISOString()
          }
          
          // Save to localStorage
          const storedProjects = localStorage.getItem('hemantdoc_projects')
          const projects: Project[] = storedProjects ? JSON.parse(storedProjects) : []
          projects.push(newProject)
          localStorage.setItem('hemantdoc_projects', JSON.stringify(projects))
          
          set(state => ({
            projects: [...state.projects, newProject],
            currentProject: newProject,
          }))
          
          return newProject.id
        } catch (error) {
          console.error('Create project error:', error)
          throw error
        }
      },

      updateProject: async (projectId: string, updates: Partial<Project>) => {
        try {
          const storedProjects = localStorage.getItem('hemantdoc_projects')
          if (!storedProjects) return
          
          const projects: Project[] = JSON.parse(storedProjects)
          const projectIndex = projects.findIndex(p => p.id === projectId)
          
          if (projectIndex === -1) return
          
          const updatedProject = {
            ...projects[projectIndex],
            ...updates,
            updatedAt: new Date().toISOString()
          }
          
          projects[projectIndex] = updatedProject
          localStorage.setItem('hemantdoc_projects', JSON.stringify(projects))
          
          set(state => ({
            projects: state.projects.map(p => 
              p.id === projectId ? updatedProject : p
            ),
            currentProject: state.currentProject?.id === projectId 
              ? updatedProject 
              : state.currentProject,
          }))
        } catch (error) {
          console.error('Update project error:', error)
          throw error
        }
      },

      deleteProject: async (projectId: string) => {
        try {
          const storedProjects = localStorage.getItem('hemantdoc_projects')
          if (storedProjects) {
            const projects: Project[] = JSON.parse(storedProjects)
            const filteredProjects = projects.filter(p => p.id !== projectId)
            localStorage.setItem('hemantdoc_projects', JSON.stringify(filteredProjects))
          }
          
          set(state => ({
            projects: state.projects.filter(p => p.id !== projectId),
            currentProject: state.currentProject?.id === projectId 
              ? null 
              : state.currentProject,
          }))
        } catch (error) {
          console.error('Delete project error:', error)
          throw error
        }
      },

      duplicateProject: async (projectId: string) => {
        try {
          const storedProjects = localStorage.getItem('hemantdoc_projects')
          if (!storedProjects) throw new Error('No projects found')
          
          const projects: Project[] = JSON.parse(storedProjects)
          const originalProject = projects.find(p => p.id === projectId)
          
          if (!originalProject) throw new Error('Project not found')
          
          const duplicatedProject: Project = {
            ...originalProject,
            id: 'project-' + Date.now() + '-' + Math.random().toString(36).substr(2, 9),
            title: originalProject.title + ' (Copy)',
            createdAt: new Date().toISOString(),
            updatedAt: new Date().toISOString(),
            version: '1.0',
            versions: []
          }
          
          projects.push(duplicatedProject)
          localStorage.setItem('hemantdoc_projects', JSON.stringify(projects))
          
          set(state => ({
            projects: [...state.projects, duplicatedProject],
          }))
          
          return duplicatedProject.id
        } catch (error) {
          console.error('Duplicate project error:', error)
          throw error
        }
      },

      updateScriptContent: (content: string) => {
        set(state => ({
          currentProject: state.currentProject ? {
            ...state.currentProject,
            scriptContent: content,
            wordCount: content.split(/\s+/).length,
            updatedAt: new Date().toISOString(),
          } : null,
        }))
      },

      parseScript: async () => {
        const { currentProject } = get()
        if (!currentProject) return

        try {
          // Simple client-side script parsing
          const content = currentProject.scriptContent
          const lines = content.split('\n')
          
          const scenes: ScriptScene[] = []
          const characters = new Set<string>()
          const locations = new Set<string>()
          
          interface ParsedScene {
            heading?: string
            description?: string
            location?: string
            timeOfDay?: 'DAY' | 'NIGHT' | 'DAWN' | 'DUSK' | 'CONTINUOUS'
            intExt?: 'INT' | 'EXT'
          }
          
          let currentScene: ParsedScene | null = null
          let sceneCount = 0
          
          lines.forEach((line) => {
            const trimmed = line.trim().toUpperCase()
            
            // Scene heading detection
            if (trimmed.match(/^(INT\.|EXT\.|FADE IN|FADE OUT)/)) {
              if (currentScene) {
                const scene = currentScene as ParsedScene
                scenes.push({
                  id: `scene-${sceneCount}`,
                  number: (sceneCount + 1).toString(),
                  heading: scene.heading || '',
                  description: scene.description || '',
                  location: scene.location || '',
                  timeOfDay: scene.timeOfDay || 'DAY',
                  intExt: scene.intExt || 'INT',
                  pageCount: 1,
                  estimatedMinutes: 2,
                  characters: Array.from(characters),
                  elements: [],
                  notes: '',
                  tags: [],
                  status: 'draft',
                  schedulingPriority: 'medium',
                  createdAt: new Date().toISOString(),
                  updatedAt: new Date().toISOString()
                } as ScriptScene)
                sceneCount++
              }
              
              currentScene = {
                heading: line.trim(),
                description: '',
                location: trimmed.replace(/^(INT\.|EXT\.)\s*/, '').split(' - ')[0],
                timeOfDay: trimmed.includes(' - NIGHT') ? 'NIGHT' : 'DAY',
                intExt: trimmed.startsWith('INT.') ? 'INT' : 'EXT'
              }
              
              if (currentScene.location) {
                locations.add(currentScene.location)
              }
            }
            
            // Character name detection (all caps, centered)
            if (trimmed && trimmed === trimmed.toUpperCase() && !trimmed.match(/^(INT\.|EXT\.|FADE)/)) {
              const words = trimmed.split(/\s+/)
              if (words.length <= 3 && !words.some(w => w.includes('.'))) {
                characters.add(trimmed)
              }
            }
            
            if (currentScene && !currentScene.heading?.toUpperCase().includes(trimmed)) {
              currentScene.description += line + '\n'
            }
          })
          
          // Add last scene
          if (currentScene) {
            const scene = currentScene as ParsedScene
            scenes.push({
              id: `scene-${sceneCount}`,
              number: (sceneCount + 1).toString(),
              heading: scene.heading || '',
              description: scene.description || '',
              location: scene.location || '',
              timeOfDay: scene.timeOfDay || 'DAY',
              intExt: scene.intExt || 'INT',
              pageCount: 1,
              estimatedMinutes: 2,
              characters: Array.from(characters),
              elements: [],
              notes: '',
              tags: [],
              status: 'draft',
              schedulingPriority: 'medium',
              createdAt: new Date().toISOString(),
              updatedAt: new Date().toISOString()
            } as ScriptScene)
          }
          
          const pageCount = Math.ceil(content.length / 250) // Rough estimate
          const estimatedRuntime = pageCount * 1 // 1 minute per page
          
          const updatedProject = {
            ...currentProject,
            scenes,
            characters: Array.from(characters),
            locations: Array.from(locations),
            pageCount,
            estimatedRuntime,
            updatedAt: new Date().toISOString()
          }
          
          // Update localStorage
          const storedProjects = localStorage.getItem('hemantdoc_projects')
          if (storedProjects) {
            const projects: Project[] = JSON.parse(storedProjects)
            const projectIndex = projects.findIndex(p => p.id === currentProject.id)
            if (projectIndex !== -1) {
              projects[projectIndex] = updatedProject
              localStorage.setItem('hemantdoc_projects', JSON.stringify(projects))
            }
          }
          
          set(state => ({
            currentProject: updatedProject,
            projects: state.projects.map(p => p.id === currentProject.id ? updatedProject : p)
          }))
        } catch (error) {
          console.error('Parse script error:', error)
        }
      },

      addScene: async (sceneData: Omit<ScriptScene, 'id' | 'createdAt' | 'updatedAt'>) => {
        const { currentProject } = get()
        if (!currentProject) return

        try {
          const newScene: ScriptScene = {
            ...sceneData,
            id: 'scene-' + Date.now() + '-' + Math.random().toString(36).substr(2, 9),
            createdAt: new Date().toISOString(),
            updatedAt: new Date().toISOString()
          }
          
          const updatedProject = {
            ...currentProject,
            scenes: [...currentProject.scenes, newScene],
            updatedAt: new Date().toISOString()
          }
          
          // Update localStorage
          const storedProjects = localStorage.getItem('hemantdoc_projects')
          if (storedProjects) {
            const projects: Project[] = JSON.parse(storedProjects)
            const projectIndex = projects.findIndex(p => p.id === currentProject.id)
            if (projectIndex !== -1) {
              projects[projectIndex] = updatedProject
              localStorage.setItem('hemantdoc_projects', JSON.stringify(projects))
            }
          }
          
          set(state => ({
            currentProject: updatedProject,
            projects: state.projects.map(p => p.id === currentProject.id ? updatedProject : p)
          }))
        } catch (error) {
          console.error('Add scene error:', error)
        }
      },

      updateScene: async (sceneId: string, updates: Partial<ScriptScene>) => {
        const { currentProject } = get()
        if (!currentProject) return

        try {
          const sceneIndex = currentProject.scenes.findIndex(s => s.id === sceneId)
          if (sceneIndex === -1) return
          
          const updatedScene = {
            ...currentProject.scenes[sceneIndex],
            ...updates,
            updatedAt: new Date().toISOString()
          }
          
          const updatedProject = {
            ...currentProject,
            scenes: currentProject.scenes.map(scene =>
              scene.id === sceneId ? updatedScene : scene
            ),
            updatedAt: new Date().toISOString()
          }
          
          // Update localStorage
          const storedProjects = localStorage.getItem('hemantdoc_projects')
          if (storedProjects) {
            const projects: Project[] = JSON.parse(storedProjects)
            const projectIndex = projects.findIndex(p => p.id === currentProject.id)
            if (projectIndex !== -1) {
              projects[projectIndex] = updatedProject
              localStorage.setItem('hemantdoc_projects', JSON.stringify(projects))
            }
          }
          
          set(state => ({
            currentProject: updatedProject,
            projects: state.projects.map(p => p.id === currentProject.id ? updatedProject : p)
          }))
        } catch (error) {
          console.error('Update scene error:', error)
        }
      },

      deleteScene: async (sceneId: string) => {
        const { currentProject } = get()
        if (!currentProject) return

        try {
          const updatedProject = {
            ...currentProject,
            scenes: currentProject.scenes.filter(scene => scene.id !== sceneId),
            updatedAt: new Date().toISOString()
          }
          
          // Update localStorage
          const storedProjects = localStorage.getItem('hemantdoc_projects')
          if (storedProjects) {
            const projects: Project[] = JSON.parse(storedProjects)
            const projectIndex = projects.findIndex(p => p.id === currentProject.id)
            if (projectIndex !== -1) {
              projects[projectIndex] = updatedProject
              localStorage.setItem('hemantdoc_projects', JSON.stringify(projects))
            }
          }
          
          set(state => ({
            currentProject: updatedProject,
            projects: state.projects.map(p => p.id === currentProject.id ? updatedProject : p)
          }))
        } catch (error) {
          console.error('Delete scene error:', error)
        }
      },

      reorderScenes: async (sceneIds: string[]) => {
        const { currentProject } = get()
        if (!currentProject) return

        try {
          // Reorder scenes locally
          const orderedScenes = sceneIds.map(id =>
            currentProject.scenes.find(scene => scene.id === id)!
          ).filter(Boolean)
          
          const updatedProject = {
            ...currentProject,
            scenes: orderedScenes,
            updatedAt: new Date().toISOString()
          }
          
          // Update localStorage
          const storedProjects = localStorage.getItem('hemantdoc_projects')
          if (storedProjects) {
            const projects: Project[] = JSON.parse(storedProjects)
            const projectIndex = projects.findIndex(p => p.id === currentProject.id)
            if (projectIndex !== -1) {
              projects[projectIndex] = updatedProject
              localStorage.setItem('hemantdoc_projects', JSON.stringify(projects))
            }
          }
          
          set(state => ({
            currentProject: updatedProject,
            projects: state.projects.map(p => p.id === currentProject.id ? updatedProject : p)
          }))
        } catch (error) {
          console.error('Reorder scenes error:', error)
        }
      },

      addElement: async (elementData: Omit<ScriptElement, 'id' | 'createdAt' | 'updatedAt'>) => {
        const { currentProject } = get()
        if (!currentProject) return

        try {
          const newElement: ScriptElement = {
            ...elementData,
            id: 'element-' + Date.now() + '-' + Math.random().toString(36).substr(2, 9),
            createdAt: new Date().toISOString(),
            updatedAt: new Date().toISOString()
          }
          
          // Add element to relevant scenes
          const updatedProject = {
            ...currentProject,
            scenes: currentProject.scenes.map(scene =>
              elementData.scenes.includes(scene.id) ? {
                ...scene,
                elements: [...scene.elements, newElement],
              } : scene
            ),
            updatedAt: new Date().toISOString()
          }
          
          // Update localStorage
          const storedProjects = localStorage.getItem('hemantdoc_projects')
          if (storedProjects) {
            const projects: Project[] = JSON.parse(storedProjects)
            const projectIndex = projects.findIndex(p => p.id === currentProject.id)
            if (projectIndex !== -1) {
              projects[projectIndex] = updatedProject
              localStorage.setItem('hemantdoc_projects', JSON.stringify(projects))
            }
          }
          
          set(state => ({
            currentProject: updatedProject,
            projects: state.projects.map(p => p.id === currentProject.id ? updatedProject : p)
          }))
        } catch (error) {
          console.error('Add element error:', error)
        }
      },

      updateElement: async (elementId: string, updates: Partial<ScriptElement>) => {
        const { currentProject } = get()
        if (!currentProject) return

        try {
          const updatedProject = {
            ...currentProject,
            scenes: currentProject.scenes.map(scene => ({
              ...scene,
              elements: scene.elements.map(element =>
                element.id === elementId ? {
                  ...element,
                  ...updates,
                  updatedAt: new Date().toISOString()
                } : element
              ),
            })),
            updatedAt: new Date().toISOString()
          }
          
          // Update localStorage
          const storedProjects = localStorage.getItem('hemantdoc_projects')
          if (storedProjects) {
            const projects: Project[] = JSON.parse(storedProjects)
            const projectIndex = projects.findIndex(p => p.id === currentProject.id)
            if (projectIndex !== -1) {
              projects[projectIndex] = updatedProject
              localStorage.setItem('hemantdoc_projects', JSON.stringify(projects))
            }
          }
          
          set(state => ({
            currentProject: updatedProject,
            projects: state.projects.map(p => p.id === currentProject.id ? updatedProject : p)
          }))
        } catch (error) {
          console.error('Update element error:', error)
        }
      },

      deleteElement: async (elementId: string) => {
        const { currentProject } = get()
        if (!currentProject) return

        try {
          const updatedProject = {
            ...currentProject,
            scenes: currentProject.scenes.map(scene => ({
              ...scene,
              elements: scene.elements.filter(element => element.id !== elementId),
            })),
            updatedAt: new Date().toISOString()
          }
          
          // Update localStorage
          const storedProjects = localStorage.getItem('hemantdoc_projects')
          if (storedProjects) {
            const projects: Project[] = JSON.parse(storedProjects)
            const projectIndex = projects.findIndex(p => p.id === currentProject.id)
            if (projectIndex !== -1) {
              projects[projectIndex] = updatedProject
              localStorage.setItem('hemantdoc_projects', JSON.stringify(projects))
            }
          }
          
          set(state => ({
            currentProject: updatedProject,
            projects: state.projects.map(p => p.id === currentProject.id ? updatedProject : p)
          }))
        } catch (error) {
          console.error('Delete element error:', error)
        }
      },

      tagElementInScene: async (elementId: string, sceneId: string) => {
        const { currentProject } = get()
        if (!currentProject) return

        try {
          // Find element to tag
          const element = currentProject.scenes
            .flatMap(s => s.elements)
            .find(e => e.id === elementId)
          
          if (element) {
            const updatedProject = {
              ...currentProject,
              scenes: currentProject.scenes.map(scene =>
                scene.id === sceneId ? {
                  ...scene,
                  elements: [...scene.elements, element],
                } : scene
              ),
              updatedAt: new Date().toISOString()
            }
            
            // Update localStorage
            const storedProjects = localStorage.getItem('hemantdoc_projects')
            if (storedProjects) {
              const projects: Project[] = JSON.parse(storedProjects)
              const projectIndex = projects.findIndex(p => p.id === currentProject.id)
              if (projectIndex !== -1) {
                projects[projectIndex] = updatedProject
                localStorage.setItem('hemantdoc_projects', JSON.stringify(projects))
              }
            }
            
            set(state => ({
              currentProject: updatedProject,
              projects: state.projects.map(p => p.id === currentProject.id ? updatedProject : p)
            }))
          }
        } catch (error) {
          console.error('Tag element error:', error)
        }
      },

      untagElementFromScene: async (elementId: string, sceneId: string) => {
        const { currentProject } = get()
        if (!currentProject) return

        try {
          const updatedProject = {
            ...currentProject,
            scenes: currentProject.scenes.map(scene =>
              scene.id === sceneId ? {
                ...scene,
                elements: scene.elements.filter(element => element.id !== elementId),
              } : scene
            ),
            updatedAt: new Date().toISOString()
          }
          
          // Update localStorage
          const storedProjects = localStorage.getItem('hemantdoc_projects')
          if (storedProjects) {
            const projects: Project[] = JSON.parse(storedProjects)
            const projectIndex = projects.findIndex(p => p.id === currentProject.id)
            if (projectIndex !== -1) {
              projects[projectIndex] = updatedProject
              localStorage.setItem('hemantdoc_projects', JSON.stringify(projects))
            }
          }
          
          set(state => ({
            currentProject: updatedProject,
            projects: state.projects.map(p => p.id === currentProject.id ? updatedProject : p)
          }))
        } catch (error) {
          console.error('Untag element error:', error)
        }
      },

      setSelectedScenes: (sceneIds: string[]) => {
        set({ selectedScenes: sceneIds })
      },

      clearSelectedScenes: () => {
        set({ selectedScenes: [] })
      },

      getElementsByType: (type: ScriptElement['type']) => {
        const { currentProject } = get()
        if (!currentProject) return []

        const elements = new Map<string, ScriptElement>()
        
        currentProject.scenes.forEach(scene => {
          scene.elements.forEach(element => {
            if (element.type === type) {
              elements.set(element.id, element)
            }
          })
        })
        
        return Array.from(elements.values())
      },

      getScenesByLocation: (location: string) => {
        const { currentProject } = get()
        if (!currentProject) return []

        return currentProject.scenes.filter(scene => scene.location === location)
      },

      getScenesByCharacter: (character: string) => {
        const { currentProject } = get()
        if (!currentProject) return []

        return currentProject.scenes.filter(scene => 
          scene.characters.includes(character)
        )
      },

      getTotalPageCount: () => {
        const { currentProject } = get()
        if (!currentProject) return 0

        return currentProject.scenes.reduce((total, scene) => total + scene.pageCount, 0)
      },

      getEstimatedRuntime: () => {
        const { currentProject } = get()
        if (!currentProject) return 0

        return currentProject.scenes.reduce((total, scene) => total + scene.estimatedMinutes, 0)
      },
    }),
    {
      name: 'project-store',
    }
  )
)