import { useState, useEffect, useCallback } from 'react'
import { useProjectStore } from '@/stores/projectStore'
import { useAuthStore } from '@/stores/authStore'
import LoadingSpinner from '@/components/ui/LoadingSpinner'
import EnhancedScriptEditor from '@/components/script/EnhancedScriptEditor'
import ScriptImporter from '@/components/script/ScriptImporter'
import RevisionManager, { Revision } from '@/components/script/RevisionManager'
import KeyboardShortcutsModal from '@/components/script/KeyboardShortcutsModal'
import { 
  formatEntireScript, 
  detectElementTypeWithContext
} from '@/utils/screenplayFormatter'
import { exportToFDX, FDXExportOptions, validateFDXOptions } from '@/utils/fdxExporter'
import toast from 'react-hot-toast'
import { 
  DocumentTextIcon,
  DocumentArrowUpIcon,
  SparklesIcon,
  ClockIcon,
  EyeIcon,
  CommandLineIcon,
  PlayIcon,
  UserIcon,
  ChatBubbleLeftRightIcon,
  ArrowRightIcon,
  CameraIcon,
  FilmIcon
} from '@heroicons/react/24/outline'

export default function UltimateScriptEditorPage() {
  const { currentProject, updateScriptContent } = useProjectStore()
  const { user } = useAuthStore()
  
  // State management
  const [content, setContent] = useState('')
  const [isSaving, setIsSaving] = useState(false)
  const [isPreviewMode, setIsPreviewMode] = useState(false)
  const [isImportModalOpen, setIsImportModalOpen] = useState(false)
  const [isRevisionModalOpen, setIsRevisionModalOpen] = useState(false)
  const [isKeyboardShortcutsOpen, setIsKeyboardShortcutsOpen] = useState(false)
  const [revisions, setRevisions] = useState<Revision[]>([])
  
  // Statistics
  const wordCount = content.split(/\s+/).filter(word => word.length > 0).length
  const lineCount = content.split('\n').length
  const characterCount = content.length
  const pageCount = Math.ceil(lineCount / 55) // Rough estimate
  const currentUser = user?.name || 'User'

  useEffect(() => {
    if (currentProject) {
      setContent(currentProject.scriptContent || '')
      loadRevisions()
    }
  }, [currentProject])

  // Load revisions from localStorage
  const loadRevisions = () => {
    try {
      const savedRevisions = localStorage.getItem(`revisions_${currentProject?.id}`)
      if (savedRevisions) {
        const parsed = JSON.parse(savedRevisions).map((rev: any) => ({
          ...rev,
          timestamp: new Date(rev.timestamp)
        }))
        setRevisions(parsed)
      }
    } catch (error) {
      console.error('Failed to load revisions:', error)
    }
  }

  // Save revision
  const saveRevision = useCallback((content: string, description?: string) => {
    if (!currentProject || !content.trim()) return

    const revision: Revision = {
      id: `revision_${Date.now()}`,
      content,
      timestamp: new Date(),
      user: currentUser,
      userId: user?.id || 'user',
      version: `v${revisions.length + 1}.0`,
      description,
      changes: {
        added: 0,
        removed: 0,
        modified: content.split('\n').length
      },
      tags: []
    }

    const updatedRevisions = [...revisions, revision]
    setRevisions(updatedRevisions)
    localStorage.setItem(`revisions_${currentProject.id}`, JSON.stringify(updatedRevisions))
    toast.success('🎬 Revision saved!', { 
      duration: 2000,
      style: { background: '#fed7aa', color: '#9a3412', fontWeight: '600' }
    })
  }, [currentProject, revisions, currentUser, user?.id])

  const handleContentChange = (newContent: string) => {
    setContent(newContent)
    
    clearTimeout((window as any).autoSaveTimer)
    ;(window as any).autoSaveTimer = setTimeout(() => {
      updateScriptContent(newContent)
      
      const lastRevision = revisions[revisions.length - 1]
      if (!lastRevision || Date.now() - lastRevision.timestamp.getTime() > 10 * 60 * 1000) {
        saveRevision(newContent, 'Auto-save revision')
      }
    }, 2000)
  }

  const handleAutoFormat = () => {
    const formattedContent = formatEntireScript(content)
    setContent(formattedContent)
    updateScriptContent(formattedContent)
    saveRevision(formattedContent, 'Auto-format applied')
    toast.success('✨ Script formatted perfectly!', { 
      duration: 3000,
      style: { background: '#fed7aa', color: '#9a3412', fontWeight: '600' }
    })
  }

  const handleElementFormat = (elementType: string) => {
    toast.success(`🎯 Formatting as ${elementType}`, {
      duration: 1500,
      style: { background: '#fed7aa', color: '#9a3412', fontWeight: '600' }
    })
  }

  const handleImport = (importedContent: string) => {
    const formattedContent = formatEntireScript(importedContent)
    setContent(formattedContent)
    updateScriptContent(formattedContent)
    saveRevision(formattedContent, 'Script imported')
    toast.success('📥 Script imported successfully!', { 
      style: { background: '#fed7aa', color: '#9a3412', fontWeight: '600' }
    })
  }

  const handleSave = async () => {
    if (!currentProject || !content.trim()) return
    
    setIsSaving(true)
    updateScriptContent(content)
    saveRevision(content, 'Manual save')
    
    setTimeout(() => {
      setIsSaving(false)
      toast.success('💾 Script saved successfully!', { 
        style: { background: '#dcfce7', color: '#166534', fontWeight: '600' }
      })
    }, 1000)
  }

  // Enhanced PDF Export
  const handleExportPDF = async () => {
    try {
      const { jsPDF } = await import('jspdf')
      const pdf = new jsPDF('portrait', 'pt', 'letter')
      
      // [Previous PDF export logic remains the same]
      const pageWidth = 612
      const pageHeight = 792
      const leftMargin = 90
      const rightMargin = 54
      const topMargin = 54
      const bottomMargin = 54
      const lineHeight = 12
      let yPosition = topMargin
      let pageNumber = 1
      
      // Title page
      pdf.setFont('courier', 'bold')
      pdf.setFontSize(24)
      const titleY = pageHeight / 2 - 100
      pdf.text(currentProject?.title?.toUpperCase() || 'UNTITLED SCRIPT', pageWidth / 2, titleY, { align: 'center' })
      
      pdf.setFont('courier', 'normal')
      pdf.setFontSize(12)
      pdf.text('Written by', pageWidth / 2, titleY + 60, { align: 'center' })
      pdf.text(currentUser, pageWidth / 2, titleY + 80, { align: 'center' })
      
      const today = new Date().toLocaleDateString('en-US', {
        month: 'long', day: 'numeric', year: 'numeric'
      })
      pdf.text(today, pageWidth - rightMargin, pageHeight - bottomMargin - 40, { align: 'right' })
      pdf.text('FIRST DRAFT', pageWidth / 2, titleY + 120, { align: 'center' })
      
      // Script content
      pdf.addPage()
      pageNumber++
      yPosition = topMargin + 36
      
      const lines = content.split('\n')
      
      for (let i = 0; i < lines.length; i++) {
        const line = lines[i]
        
        if (yPosition > pageHeight - bottomMargin - 24) {
          pdf.addPage()
          pageNumber++
          yPosition = topMargin
          if (pageNumber > 1) {
            pdf.setFont('courier', 'normal')
            pdf.setFontSize(12)
            pdf.text(`${pageNumber}.`, pageWidth - rightMargin - 20, topMargin - 18)
          }
        }
        
        if (line.trim()) {
          const elementType = detectElementTypeWithContext(lines, i)
          let text = line.trim()
          let xPosition = leftMargin
          let fontSize = 12
          let fontStyle = 'normal'
          
          switch (elementType) {
            case 'scene_heading':
              text = text.toUpperCase()
              fontStyle = 'bold'
              if (i > 0) yPosition += lineHeight
              break
            case 'character':
              text = text.toUpperCase()
              xPosition = leftMargin + 216
              fontStyle = 'bold'
              yPosition += lineHeight / 2
              break
            case 'dialogue':
              xPosition = leftMargin + 108
              break
            case 'parenthetical':
              if (!text.startsWith('(')) text = '(' + text
              if (!text.endsWith(')')) text = text + ')'
              xPosition = leftMargin + 144
              break
            case 'transition':
              text = text.toUpperCase()
              if (!text.endsWith(':') && !text.endsWith('.')) text = text + ':'
              xPosition = pageWidth - rightMargin - pdf.getTextWidth(text) - 18
              fontStyle = 'bold'
              yPosition += lineHeight
              break
          }
          
          pdf.setFont('courier', fontStyle as any)
          pdf.setFontSize(fontSize)
          
          const textWidth = pageWidth - leftMargin - rightMargin - (xPosition - leftMargin)
          const wrappedLines = pdf.splitTextToSize(text, textWidth)
          
          for (let j = 0; j < wrappedLines.length; j++) {
            if (yPosition > pageHeight - bottomMargin - 24) {
              pdf.addPage()
              pageNumber++
              yPosition = topMargin
              if (pageNumber > 1) {
                pdf.setFont('courier', 'normal')
                pdf.setFontSize(12)
                pdf.text(`${pageNumber}.`, pageWidth - rightMargin - 20, topMargin - 18)
              }
            }
            
            pdf.text(wrappedLines[j], xPosition, yPosition)
            yPosition += lineHeight
          }
        } else {
          yPosition += lineHeight
        }
      }
      
      pdf.save(`${currentProject?.title || 'script'}.pdf`)
      toast.success('📄 Professional PDF exported!', { 
        duration: 3000,
        style: { background: '#dcfce7', color: '#166534', fontWeight: '600' }
      })
    } catch (error) {
      console.error('PDF export failed:', error)
      toast.error('❌ PDF export failed. Please try again.')
    }
  }

  const handleExportFDX = () => {
    if (!currentProject || !content.trim()) {
      toast.error('No content to export')
      return
    }

    const options: FDXExportOptions = {
      title: currentProject.title,
      author: currentUser,
      draft: 'First Draft',
      date: new Date(),
      contact: user?.email || ''
    }

    const errors = validateFDXOptions(options)
    if (errors.length > 0) {
      toast.error(`Export failed: ${errors.join(', ')}`)
      return
    }

    try {
      exportToFDX(content, options)
      toast.success('🎬 Final Draft file exported!', { 
        duration: 3000,
        style: { background: '#dbeafe', color: '#1e40af', fontWeight: '600' }
      })
    } catch (error) {
      console.error('FDX export failed:', error)
      toast.error('❌ Final Draft export failed. Please try again.')
    }
  }

  const handleExportTXT = () => {
    if (!content.trim()) {
      toast.error('No content to export')
      return
    }

    const blob = new Blob([content], { type: 'text/plain' })
    const url = URL.createObjectURL(blob)
    const a = document.createElement('a')
    a.href = url
    a.download = `${currentProject?.title || 'script'}.txt`
    a.click()
    URL.revokeObjectURL(url)
    toast.success('📝 Text file exported!', { 
      style: { background: '#f3f4f6', color: '#374151', fontWeight: '600' }
    })
  }

  const handleRestoreRevision = (revision: Revision) => {
    setContent(revision.content)
    updateScriptContent(revision.content)
    toast.success(`⏪ Restored to ${revision.version}`, { 
      style: { background: '#fef3c7', color: '#92400e', fontWeight: '600' }
    })
  }

  const handleCreateTag = (revisionId: string, tag: string) => {
    const updatedRevisions = revisions.map(rev => 
      rev.id === revisionId 
        ? { ...rev, tags: [...rev.tags, tag] }
        : rev
    )
    setRevisions(updatedRevisions)
    localStorage.setItem(`revisions_${currentProject?.id}`, JSON.stringify(updatedRevisions))
    toast.success('🏷️ Tag added to revision')
  }

  // Keyboard shortcuts
  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      if (e.metaKey || e.ctrlKey) {
        switch (e.key) {
          case 'k':
            e.preventDefault()
            setIsKeyboardShortcutsOpen(true)
            break
          case 'r':
            e.preventDefault()
            setIsRevisionModalOpen(true)
            break
          case 'p':
            e.preventDefault()
            setIsPreviewMode(!isPreviewMode)
            break
          case 's':
            e.preventDefault()
            handleSave()
            break
          case 'o':
            e.preventDefault()
            setIsImportModalOpen(true)
            break
          case 'e':
            e.preventDefault()
            if (e.shiftKey) {
              handleExportFDX()
            } else if (e.altKey) {
              handleExportTXT()
            } else {
              handleExportPDF()
            }
            break
          case '1':
          case '2':
          case '3':
          case '4':
          case '5':
          case '6':
          case '7':
            e.preventDefault()
            const elementTypes = ['scene_heading', 'action', 'character', 'dialogue', 'parenthetical', 'transition', 'shot']
            handleElementFormat(elementTypes[parseInt(e.key) - 1])
            break
        }
      }
    }

    document.addEventListener('keydown', handleKeyDown)
    return () => document.removeEventListener('keydown', handleKeyDown)
  }, [isPreviewMode])

  if (!currentProject) {
    return (
      <div className="flex items-center justify-center h-screen" 
           style={{ background: 'linear-gradient(135deg, #fed7aa 0%, #f97316 100%)' }}>
        <LoadingSpinner size="lg" />
      </div>
    )
  }

  // Formatting buttons for left sidebar
  const formatButtons = [
    { type: 'scene_heading', label: 'Scene Heading', icon: FilmIcon, shortcut: '⌘1', color: 'bg-blue-500' },
    { type: 'action', label: 'Action', icon: PlayIcon, shortcut: '⌘2', color: 'bg-green-500' },
    { type: 'character', label: 'Character', icon: UserIcon, shortcut: '⌘3', color: 'bg-red-500' },
    { type: 'dialogue', label: 'Dialogue', icon: ChatBubbleLeftRightIcon, shortcut: '⌘4', color: 'bg-purple-500' },
    { type: 'parenthetical', label: 'Parenthetical', icon: DocumentTextIcon, shortcut: '⌘5', color: 'bg-yellow-500' },
    { type: 'transition', label: 'Transition', icon: ArrowRightIcon, shortcut: '⌘6', color: 'bg-pink-500' },
    { type: 'shot', label: 'Shot', icon: CameraIcon, shortcut: '⌘7', color: 'bg-indigo-500' }
  ]

  return (
    <div className="h-screen flex flex-col" style={{ background: 'linear-gradient(135deg, #fff7ed 0%, #fed7aa 100%)' }}>
      {/* Orange Header */}
      <div className="bg-gradient-to-r from-orange-400 to-orange-600 shadow-lg">
        <div className="max-w-7xl mx-auto px-6">
          <div className="flex items-center justify-between h-16">
            {/* Left Section */}
            <div className="flex items-center space-x-4">
              <div className="flex items-center space-x-3">
                <div className="w-10 h-10 bg-white rounded-xl flex items-center justify-center shadow-lg">
                  <DocumentTextIcon className="h-6 w-6 text-orange-500" />
                </div>
                <div>
                  <h1 className="text-xl font-bold text-white drop-shadow-sm">ScriptWriter Pro</h1>
                  <p className="text-sm text-orange-100">{currentProject.title}</p>
                </div>
              </div>
            </div>

            {/* Center Section - Live Stats */}
            <div className="hidden md:flex items-center space-x-8 text-white">
              <div className="text-center">
                <div className="text-2xl font-bold">{wordCount}</div>
                <div className="text-xs text-orange-100">WORDS</div>
              </div>
              <div className="text-center">
                <div className="text-2xl font-bold">{pageCount}</div>
                <div className="text-xs text-orange-100">PAGES</div>
              </div>
              <div className="text-center">
                <div className="text-2xl font-bold">{revisions.length}</div>
                <div className="text-xs text-orange-100">REVISIONS</div>
              </div>
              <div className="flex items-center space-x-2">
                <div className="w-3 h-3 bg-green-400 rounded-full animate-pulse"></div>
                <span className="text-sm">{currentUser}</span>
              </div>
            </div>

            {/* Right Section */}
            <div className="flex items-center space-x-3">
              <button
                onClick={() => setIsRevisionModalOpen(true)}
                className="flex items-center px-4 py-2 bg-white/20 backdrop-blur-sm text-white rounded-lg hover:bg-white/30 transition-all"
                title="View revision history (Cmd+R)"
              >
                <ClockIcon className="h-4 w-4 mr-2" />
                <span className="hidden sm:inline">Revisions</span>
              </button>
              
              <button
                onClick={() => setIsPreviewMode(!isPreviewMode)}
                className={`flex items-center px-4 py-2 rounded-lg transition-all ${
                  isPreviewMode 
                    ? 'bg-white text-orange-600 shadow-lg' 
                    : 'bg-white/20 backdrop-blur-sm text-white hover:bg-white/30'
                }`}
              >
                <EyeIcon className="h-4 w-4 mr-2" />
                {isPreviewMode ? 'Edit' : 'Preview'}
              </button>
            </div>
          </div>
        </div>
      </div>

      {/* Main Content Area */}
      <div className="flex-1 flex">
        {/* Left Sidebar - Formatting Toolbar */}
        <div className="w-64 bg-gradient-to-b from-orange-50 to-orange-100 border-r-2 border-orange-200 p-4 shadow-lg">
          <div className="space-y-4">
            {/* Quick Actions */}
            <div className="bg-white rounded-xl p-4 shadow-sm">
              <h3 className="text-sm font-semibold text-orange-800 mb-3">Quick Actions</h3>
              <div className="space-y-2">
                <button
                  onClick={handleAutoFormat}
                  className="w-full flex items-center px-3 py-2 bg-gradient-to-r from-orange-500 to-orange-600 text-white rounded-lg hover:from-orange-600 hover:to-orange-700 transition-all shadow-sm"
                >
                  <SparklesIcon className="h-4 w-4 mr-2" />
                  Auto-Format
                </button>
                
                <button
                  onClick={() => setIsImportModalOpen(true)}
                  className="w-full flex items-center px-3 py-2 bg-gray-100 text-gray-700 rounded-lg hover:bg-gray-200 transition-all"
                >
                  <DocumentArrowUpIcon className="h-4 w-4 mr-2" />
                  Import
                </button>
                
                <button
                  onClick={handleSave}
                  disabled={isSaving}
                  className="w-full flex items-center px-3 py-2 bg-green-100 text-green-700 rounded-lg hover:bg-green-200 transition-all disabled:opacity-50"
                >
                  {isSaving ? (
                    <div className="animate-spin h-4 w-4 mr-2 border-2 border-green-600 border-t-transparent rounded-full" />
                  ) : (
                    <DocumentTextIcon className="h-4 w-4 mr-2" />
                  )}
                  {isSaving ? 'Saving...' : 'Save'}
                </button>
              </div>
            </div>

            {/* Formatting Tools */}
            <div className="bg-white rounded-xl p-4 shadow-sm">
              <h3 className="text-sm font-semibold text-orange-800 mb-3">Format Elements</h3>
              <div className="space-y-1">
                {formatButtons.map((button) => {
                  const Icon = button.icon
                  return (
                    <button
                      key={button.type}
                      onClick={() => handleElementFormat(button.type)}
                      className="w-full flex items-center px-3 py-2 text-sm text-gray-700 hover:bg-orange-50 hover:text-orange-800 rounded-lg transition-all group"
                      title={`${button.label} (${button.shortcut})`}
                    >
                      <div className={`w-3 h-3 rounded-full mr-3 ${button.color} group-hover:scale-110 transition-transform`} />
                      <Icon className="h-4 w-4 mr-2 text-gray-500 group-hover:text-orange-600" />
                      <span className="flex-1 text-left">{button.label}</span>
                      <span className="text-xs text-gray-400 group-hover:text-orange-500">{button.shortcut}</span>
                    </button>
                  )
                })}
              </div>
            </div>

            {/* Export Options */}
            <div className="bg-white rounded-xl p-4 shadow-sm">
              <h3 className="text-sm font-semibold text-orange-800 mb-3">Export Options</h3>
              <div className="space-y-1">
                <button
                  onClick={handleExportPDF}
                  className="w-full flex items-center px-3 py-2 text-sm text-red-700 hover:bg-red-50 rounded-lg transition-all"
                >
                  <div className="w-3 h-3 bg-red-500 rounded-full mr-3" />
                  <span>PDF</span>
                </button>
                <button
                  onClick={handleExportFDX}
                  className="w-full flex items-center px-3 py-2 text-sm text-blue-700 hover:bg-blue-50 rounded-lg transition-all"
                >
                  <div className="w-3 h-3 bg-blue-500 rounded-full mr-3" />
                  <span>Final Draft</span>
                </button>
                <button
                  onClick={handleExportTXT}
                  className="w-full flex items-center px-3 py-2 text-sm text-gray-700 hover:bg-gray-50 rounded-lg transition-all"
                >
                  <div className="w-3 h-3 bg-gray-500 rounded-full mr-3" />
                  <span>Text</span>
                </button>
              </div>
            </div>

            {/* Help & Shortcuts */}
            <div className="bg-white rounded-xl p-4 shadow-sm">
              <button
                onClick={() => setIsKeyboardShortcutsOpen(true)}
                className="w-full flex items-center px-3 py-2 text-sm text-purple-700 hover:bg-purple-50 rounded-lg transition-all"
              >
                <CommandLineIcon className="h-4 w-4 mr-2" />
                <span>Keyboard Shortcuts</span>
                <span className="ml-auto text-xs text-gray-400">⌘K</span>
              </button>
            </div>
          </div>
        </div>

        {/* Main Script Editor Area */}
        <div className="flex-1 flex flex-col">
          <div className="flex-1 p-8">
            <div className="bg-white rounded-2xl shadow-xl border-2 border-orange-200 h-full">
              {isPreviewMode ? (
                <div className="w-full h-full p-8 overflow-y-auto" style={{ fontFamily: 'Courier New, monospace' }}>
                  {content.split('\n').map((line, index) => {
                    const lines = content.split('\n')
                    const elementType = detectElementTypeWithContext(lines, index)
                    let className = 'mb-2 leading-6'
                    let style: React.CSSProperties = {}
                    
                    switch (elementType) {
                      case 'scene_heading':
                        className += ' font-bold uppercase text-blue-800 text-lg'
                        break
                      case 'character':
                        className += ' font-bold uppercase text-center text-red-700'
                        style.marginLeft = '3in'
                        break
                      case 'dialogue':
                        style.marginLeft = '1.5in'
                        style.marginRight = '1in'
                        className += ' text-gray-800'
                        break
                      case 'parenthetical':
                        className += ' text-center italic text-gray-600'
                        style.marginLeft = '2in'
                        break
                      case 'transition':
                        className += ' font-bold uppercase text-right text-pink-700'
                        break
                      default:
                        style.marginRight = '1in'
                        className += ' text-gray-900'
                        break
                    }
                    
                    return (
                      <div 
                        key={index} 
                        className={className} 
                        style={style}
                        title={`Element Type: ${elementType}`}
                      >
                        {line || '\u00A0'}
                      </div>
                    )
                  })}
                </div>
              ) : (
                <EnhancedScriptEditor
                  value={content}
                  onChange={handleContentChange}
                  placeholder={`FADE IN:

EXT. ${currentProject.title.toUpperCase()} - DAY

Start writing your professional screenplay here...

🎬 PROFESSIONAL FEATURES:
• Industry-standard formatting
• Real-time auto-save  
• Complete revision history
• Export to PDF & Final Draft
• Full keyboard shortcuts (⌘K)

Begin typing to experience professional scriptwriting!

FADE OUT.`}
                  className="w-full h-full rounded-2xl"
                />
              )}
            </div>
          </div>
        </div>
      </div>

      {/* Enhanced Status Bar */}
      <div className="bg-gradient-to-r from-orange-100 to-orange-200 border-t-2 border-orange-300 px-6 py-3">
        <div className="flex items-center justify-between text-sm">
          <div className="flex items-center space-x-6 text-orange-800">
            <span className="font-semibold">Format: Professional Screenplay</span>
            <span>Font: Courier New 12pt</span>
            <span className="flex items-center">
              <div className="w-2 h-2 bg-green-500 rounded-full mr-2 animate-pulse" />
              Auto-save: {isSaving ? 'Saving...' : 'Active'}
            </span>
          </div>
          <div className="flex items-center space-x-6 text-orange-700">
            <span>{characterCount} characters</span>
            <span className="font-bold text-orange-600 flex items-center">
              <div className="w-2 h-2 bg-orange-500 rounded-full mr-2" />
              v3.0.0-dev
            </span>
          </div>
        </div>
      </div>

      {/* Modals */}
      <ScriptImporter
        isOpen={isImportModalOpen}
        onClose={() => setIsImportModalOpen(false)}
        onImport={handleImport}
      />

      <RevisionManager
        isOpen={isRevisionModalOpen}
        onClose={() => setIsRevisionModalOpen(false)}
        revisions={revisions}
        currentContent={content}
        onRestoreRevision={handleRestoreRevision}
        onCreateTag={handleCreateTag}
        currentUser={currentUser}
      />

      <KeyboardShortcutsModal
        isOpen={isKeyboardShortcutsOpen}
        onClose={() => setIsKeyboardShortcutsOpen(false)}
      />
    </div>
  )
}