import { useEffect } from 'react'
import { useParams, useNavigate, Outlet, useLocation } from 'react-router-dom'
import { useProjectStore } from '@/stores/projectStore'
import LoadingSpinner from '@/components/ui/LoadingSpinner'
import { 
  ArrowLeftIcon, 
  PencilIcon, 
  DocumentDuplicateIcon, 
  CalendarIcon,
  UserGroupIcon
} from '@heroicons/react/24/outline'

export default function ProjectPage() {
  const { projectId } = useParams<{ projectId: string }>()
  const navigate = useNavigate()
  const location = useLocation()
  const { currentProject, loadProject, isLoading } = useProjectStore()

  useEffect(() => {
    if (projectId) {
      loadProject(projectId)
    }
  }, [projectId, loadProject])

  if (isLoading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <LoadingSpinner size="lg" />
      </div>
    )
  }

  if (!currentProject) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <h2 className="text-2xl font-bold text-gray-900 mb-2">Project Not Found</h2>
          <p className="text-gray-600 mb-4">The project you're looking for doesn't exist.</p>
          <button
            onClick={() => navigate('/dashboard')}
            className="btn-primary"
          >
            Back to Dashboard
          </button>
        </div>
      </div>
    )
  }

  const navigation = [
    {
      name: 'Script Editor',
      path: 'script',
      icon: PencilIcon,
      description: 'Write and format your screenplay'
    },
    {
      name: 'Breakdown',
      path: 'breakdown', 
      icon: DocumentDuplicateIcon,
      description: 'Tag elements and create production sheets'
    },
    {
      name: 'Schedule',
      path: 'schedule',
      icon: CalendarIcon,
      description: 'Plan and organize your shoot'
    },
    {
      name: 'Collaborate',
      path: 'collaborate',
      icon: UserGroupIcon,
      description: 'Share and work with your team'
    }
  ]

  const currentPath = location.pathname.split('/').pop()

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <header className="bg-white shadow">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex items-center justify-between py-6">
            <div className="flex items-center">
              <button
                onClick={() => navigate('/dashboard')}
                className="mr-4 p-2 text-gray-400 hover:text-gray-600 rounded-md hover:bg-gray-100"
              >
                <ArrowLeftIcon className="h-5 w-5" />
              </button>
              <div>
                <h1 className="text-2xl font-bold text-gray-900">
                  {currentProject.title}
                </h1>
                <p className="text-sm text-gray-500">
                  {currentProject.format} • {currentProject.status} • v{currentProject.version}
                </p>
              </div>
            </div>
          </div>
        </div>
      </header>

      {/* Navigation Tabs */}
      <nav className="bg-white border-b border-gray-200">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex space-x-8">
            {navigation.map((item) => {
              const Icon = item.icon
              const isActive = currentPath === item.path
              
              return (
                <button
                  key={item.name}
                  onClick={() => navigate(`/project/${projectId}/${item.path}`)}
                  className={`flex items-center py-4 px-1 border-b-2 font-medium text-sm ${
                    isActive
                      ? 'border-primary-500 text-primary-600'
                      : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                  }`}
                >
                  <Icon className="h-5 w-5 mr-2" />
                  {item.name}
                </button>
              )
            })}
          </div>
        </div>
      </nav>

      {/* Main Content */}
      <main className="flex-1">
        <Outlet />
      </main>
    </div>
  )
}